import React, {useState, useEffect} from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { IServant, ITakeaway } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { toDateTimeString } from '../../../../util/convert';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [data, setData] = useState([] as Array<IServant>);
  const [viewId, setViewId] = useState('');
  const [takeaways, setTakeaways] = useState([] as Array<ITakeaway>);

  useEffect(() => {
    if(props.homeId.length > 0){
      props.onToggleLoading(true);
      props.onLoad(props.homeId, (result: Array<IServant>) => {
        props.onToggleLoading(false);
        setData(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Servants.');
      });
    }
  }, []);

 const onView = (id: string, takeaways: Array<ITakeaway>) => {
    if(viewId === id){
      setViewId('');
      setTakeaways([]);
    }else{
      setViewId(id);
      setTakeaways(takeaways);
    }
  };

  return(
    <View>
      <View className='ta-c mT-1 mB-3 p-2'>
        <Text className='c-blue-700 fw-600 mB-10'>Servants</Text>
      </View>
      {isNN(data) ?
        data.map((d: IServant, i: number) => {
        return(
          <View key={i} className='bdB mT-5'>
            <View className='mT-1 mB-3 p-2'>
              <Text className='fw-600'>{d.createdBy + ' on ' + toDateTimeString(d.createdAt)}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.name}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
            </View>
            {isNN(d.mobileNo) &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.mobileNo}</Text>
            </View>
            }
            {isNN(d.emailId) &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.emailId}</Text>
            </View>
            }
            <View className='mT-1 mB-3 p-2'>
              <span onClick={() => {
                onView(d._id, d.takeaways);
              }}>
                <Text className='cur-p c-blue-500'>{(viewId === d._id ? 'Hide' : 'View') + ' Takeaways'}</Text>
              </span>
            </View>
            {viewId === d._id &&
            <View className='mT-5 mB-5'>
              <View>
                <Text className='fw-600'>Takeaways</Text>
              </View>
              <View className='p-15'>
                {isNN(takeaways) ?
                  takeaways.map((e: ITakeaway, j: number) => {
                    return(
                    <View key={'tkycmt' + j} className={'pL-3 pR-3 pT-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                      <View>
                        <Text className='fw-600'>{e.createdBy + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                      </View>
                      <View>
                        <Text>{e.title}</Text>
                      </View>
                      <View>
                        <Text>{e.description}</Text>
                      </View>
                      {
                        e.isExit ?
                      <View>
                        <Text className='c-green-600'>{'Exit by ' + e.exitBy + ' in Gate ' + e.exitGateNo + ' on ' + toDateTimeString(e.exitAt)}</Text>
                      </View>
                      :
                      <View>
                        <Text className='c-red-600'>Still not exit.</Text>
                      </View>
                      }
                    </View>
                    )
                  })
                  :
                  <Text className='p-20'>No takeaway found.</Text>
                }
              </View>
            </View>
            }
          </View>
          );
      })
      :
      <Text>No data found.</Text>
    }
    </View>
  );
};