import React, {useState} from 'react';
import { View, Text } from '../common/elements';
import Button from '../common/button';
import Checkbox from '../common/checkbox';
import Spinner from '../common/spinner';
import { IPolls } from '../../models/store/polls';
import { IPoll, IVote } from '../../models/poll/index';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { isNN, isNNObject } from '../../util/index';
import { percentage } from '../../util/calc';
import Toast from  '../../util/toast';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  polls: IPolls,
  onVote: (pollId: string, votes: Array<IVote>, polls: IPolls, user: IUser, appUser: IAppUser, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  let curDate = new Date();
  const [isLoading, setIsLoading] = useState(false);
  const [votePollId, setVotePollId] = useState('');
  const [votes, setVotes] = useState([] as Array<IVote>);
  const [currentDate, setCurrentDate] = useState(new Date((curDate.getMonth() + 1) + '/' + curDate.getDate() + '/' + curDate.getFullYear()));

  const loadVotes = (poll: IPoll) => {
    let tVotes = [] as any;
    poll.options.map((po: string, poi: number) => 
      tVotes.push({
        option: po,
        votes: poll.votes.filter(v => v.option === poi).length
      })
    );
    return(
    tVotes.sort(function(a: any, b: any){return b.votes - a.votes}).map((vo: any, voi: number) => {
      return(
        <View key={'pvo' + voi} className='mT-1 mB-3 p-2'>
          <Text>
            {vo.votes + ' (' + percentage(vo.votes, poll.votes.length) +  '%) - ' + vo.option}
          </Text>
        </View>
      )
    })
    )
  };

  const onSelectOption = (poll: IPoll, optionId: number) => {
    if(votePollId === poll._id){
      if(isNN(votes.filter(v => v.option === optionId))){
        setVotes(votes.filter(v => v.option !== optionId));
      }else{
        let tVotes = [] as Array<IVote>;
        if(poll.isAllowMultiple){
          tVotes = votes;
        }
        tVotes.push({
            _homeId: props.user.homes[props.appUser.homeIndex]._id,
            _memberId: props.user._id,
            option: optionId,
            isActive: true,
            createdBy: props.user._id
          } as IVote);
          setVotes(tVotes);
        // if(poll.isAllowMultiple || !isNN(votes.filter(v => v.pollId === poll.id))){

        // }
      }
    }else{
      let tVotes = [] as Array<IVote>;
      tVotes.push({
        _homeId: props.user.homes[props.appUser.homeIndex]._id,
        _memberId: props.user._id,
        option: optionId,
        isActive: true,
        createdBy: props.user._id
      } as IVote);
      setVotePollId(poll._id);
      setVotes(tVotes);
    }
  };

  const onVote = (pollId: string) => {
    if(votePollId === pollId &&  isNN(votes)){
      setIsLoading(true);
      props.onVote(pollId, votes, props.polls, props.user, props.appUser, (result: boolean) => {
        setIsLoading(false);
        if(result === true){
          setVotePollId('');
          setVotes([]);
          Toast.success('Your vote saved successfully.');
        }else{
          Toast.fail('Failed to save your vote.');
        }
      });
    }else{
      Toast.warn('Please select any option to vote.');
    }
  };

    return(
      <View>
      {
        (isNNObject(props.polls) && props.polls.isUpToDate && !props.polls.isError && isNN(props.polls.data.filter(p => isNN(p.options) && new Date(p.fromDate) <= new Date() && new Date(p.toDate) >= currentDate))) ?
        props.polls.data.filter(p => isNN(p.options) && new Date(p.fromDate) <= new Date() && new Date(p.toDate) >= currentDate).map((d: IPoll, i: number) => {
          return(
            <View key={"p" + d._id} className='bdB mT-5'>
              <View className='mT-1 mB-3 p-2'>
                <Text className='fw-600'>{d.topic}</Text>
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.description}</Text>
              </View>
              {
                (d.isOneVotePerFlat !== true || !isNN(d.votes) || !isNN(d.votes.filter(vo => vo._homeId === props.user.homes[props.appUser.homeIndex]._id))) &&
                d.options.map((o: string, oi: number) => {
                  return(
                    <View key={"popt" + oi} className='mT-1 mB-3 p-2'>
                      <Checkbox isActive={(votePollId === d._id && isNN(votes.filter(v => v.option === oi)))} text={o} onPress={() => onSelectOption(d, oi)} />
                    </View>
                  )
                })
              }
              {
                (d.isOneVotePerFlat !== true || !isNN(d.votes) || !isNN(d.votes.filter(vo => vo._homeId === props.user.homes[props.appUser.homeIndex]._id))) &&
                <View className='mT-5'>
                  <Button title="Vote" onPress={() => onVote(d._id)} />
                </View>
              }
              <View className='mT-1 mB-3 p-2'>
                <Text className='fw-600'>Your Votes:</Text>
              </View>
              {isNN(d.options) &&
                loadVotes(d)
              }
            </View>
          )
      })
      :
      <Text className='p-20'>{(props.polls.isUpToDate ? (props.polls.isError ? props.polls.errorMessage : "No active poll found.") : "Loading...")}</Text>
    }
    <Spinner visible={isLoading} />
    </View>
    );
};