import * as React from 'react';
import { isNN, generateUUID } from '../../util';

interface IProps {
  isActive : boolean,
  text: string,
  onPress?: () => void,
  key?: string,
  className?: string
};

export default (props: IProps) => {
  const {isActive, text, onPress, key, className} = props;
  let id = generateUUID();
  return (
    <span className={'mR-10 ' + (className ? className : '')}>
      <input type='radio' id={id} key={key} checked={isActive} onClick={onPress ? onPress : () => {}} />
      {isNN(text) &&
        <label htmlFor={id} className='mL-5 va-m'>{text}</label>
      }
    </span>
  );
};