import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text } from '../common/elements';
import Spinner from '../common/spinner';
import Button from '../common/button';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IOutings } from '../../models/store/outings';
import { IOuting } from '../../models/outing';
import { isNN, isNNObject } from '../../util/index';
import { toDateTimeString } from '../../util/convert';
import { ROLE } from '../../constants';
import Toast from  '../../util/toast';
import Breadcrum from '../common/breadcrum';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  data: IOutings,
  onLoad: (appClientId: string, homeId: string, callback: (result: boolean) => void) => void,
  onReturnBack: (id: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  
  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, props.appUser.role === ROLE.USER ? props.user.homes[props.appUser.homeIndex]._id : '', (result: boolean) => {
      setIsLoading(false);
      if(!result){
        Toast.fail('Failed to load Door Locks.');
      }
    });
  }, []);

  const onReturnBack = (outingId: string) => {
    confirmAlert({
      title: 'Confirm to open the door',
      message: 'Are you sure? You want to open the door?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onReturnBack(outingId, props.user, props.data, (result: boolean) => {
              setIsLoading(false);
              if(result){
                Toast.success('Successfully opened the door.');
              }else{
                Toast.fail('Failed to open the door.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Door Locks', link: 'outings'}]} />
          <View className='bgc-white p-20'>
            {(props.appUser.role === ROLE.USER && (!isNNObject(props.data) || !isNN(props.data.data.filter(o => o.isReturned !== true)))) &&
              <View className='ta-r'>
                <Button
                title='Lock The Door'
                onPress={() => {
                  props.history.push('new-outing')
                }}
                />
              </View>
            }
            {
              (isNNObject(props.data) && props.data.isUpToDate && !props.data.isError && isNN(props.data.data)) ?
              props.data.data.map((d: IOuting, i: number) => {
                return(
                  <View key={"o" + d._id} className='bdB mT-5'>
                    <View className='mT-1 mB-3 p-2'>
                      <Text className='fw-600'>{d._homeId + ' by ' + d.createdBy + ' on ' + toDateTimeString(d.createdAt)}</Text>
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                        <Text className='fw-600'>{toDateTimeString(d.fromDate) + " to " + toDateTimeString(d.toDate)}</Text>
                    </View>
                    {isNN(d.place) &&
                    <View className='mT-1 mB-3 p-2'>
                      <Text className='fw-600'>{d.place}</Text>
                    </View>
                    }
                    {isNN(d.address) &&
                    <View className='mT-1 mB-3 p-2'>
                      <Text>{d.address}</Text>
                    </View>
                    }
                    {d.isReturned &&
                    <View className='mT-1 mB-3 p-2'>
                      <Text>{'Returned on ' + toDateTimeString(d.returnedAt)}</Text>
                    </View>
                    }
                    {props.appUser.role !== ROLE.USER && 
                      <View className='mT-3 mB-3 pT-5 pB-5'>
                        <Text>No one in the home</Text>
                      </View>
                    }
                    {(props.appUser.role === ROLE.USER && d.isReturned !== true) &&
                      <View className='mT-3 mB-3 pT-5 pB-5'>
                        <Button title='Open The Door' onPress={() => {
                          onReturnBack(d._id);
                        }} />
                      </View>
                    }
                  </View>
                )
              })
              :
              <Text className='p-20'>{(props.data.isUpToDate ? (props.data.isError ? props.data.errorMessage : "No door lock found.") : "Loading...")}</Text>
            }
            <Spinner visible={isLoading} />
          </View>
        </View>
      </View>
    </View>
  );
};