import React from 'react';
import { Link } from 'react-router-dom';
import { Image } from '../common/elements';
import { isNN } from '../../util/index';
import { NAV } from '../../constants/nav';
import Images from '../images';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user';

interface IProps {
  appUser: IAppUser,
  user: IUser
};

export default (props: IProps) => {
  return (
    <div className="sidebar">
      <div className="sidebar-inner">
        <div className="sidebar-logo">
          <div className="peers ai-c fxw-nw">
            <div className="peer peer-greed">
              <Link className="sidebar-link td-n" to='/'>
                <div className="peers ai-c fxw-nw">
                  <div className="peer">
                    <div className="logo">
                      <Image src={Images.logo} className='' style={{ width: 70, height: 65, padding: '13px 15px'}} alt='' />
                    </div>
                  </div>
                  <div className="peer peer-greed">
                    <h5 className="lh-1 mB-0 logo-text">Nest</h5>
                  </div>
                </div>
              </Link>
            </div>
            <div className="peer">
              <div className="mobile-toggle sidebar-toggle">
                <a href="" className="td-n">
                  <i className="ti-arrow-circle-left"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
        {(NAV && NAV.length > 0) &&
        <ul className="sidebar-menu scrollable pos-r">
          {
            NAV.map((d: any, i: number) => {
              if((isNN(d.path) || isNN(d.child)) && (!isNN(d.roles) || d.roles.indexOf(props.appUser.role) >= 0) && (!d.hasMRole || props.user.role.indexOf(',') > 0) && (!d.hasPayment || props.user.client.config.hasPayment)){
                return(
                  <li key={'nav-item-' + i} className={'nav-item' + (isNN(d.child) ? ' dropdown' : '') + (i === 0 ? ' mT-30' : '')}>
                    <Link className={(isNN(d.child) ? 'dropdown-toggle' : 'sidebar-link')} to={isNN(d.path) ? d.path : '#' }>
                    <span className="icon-holder">
                      <i className={d.icon}></i>
                    </span>
                    <span className="title">{d.title}</span>
                    {isNN(d.child) &&
                    <span className="arrow">
                      <i className="ti-angle-right"></i>
                    </span>
                    }
                  </Link>
                    {isNN(d.child) &&
                    <ul className="dropdown-menu">
                      {
                        d.child.map((d: any, i: number) => {
                          return(
                            <li key={'sub-nav-item-' + i}>
                              <Link className='sidebar-link' to={isNN(d.path) ? d.path : '#'}>{d.title}</Link>
                            </li>
                          )
                        })
                      }
                    </ul>
                    }
                  </li>
                )
              }
            })
          }
        </ul>
        }
      </div>
    </div>
  )
};