import * as React from 'react';
import {Text, View} from '../common/elements';
import {IAppUser} from '../../models/app-user';
import {IUser} from '../../models/user/user';
import {IAds} from '../../models/store/ads';
import {IUsers} from '../../models/store/users';
import {IHomes} from '../../models/store/homes';
import {IBlocks} from '../../models/store/blocks';
import {IOutings} from '../../models/store/outings';
import {ITransaction} from '../../models/home/transaction';
import {ITickets} from '../../models/store/tickets';
import {IVisitor} from '../../models/home';
import Home from './home';
import AMHome from './manager/home';
import GKHome from './gatekeeper/home';
import {ROLE} from '../../constants/index';
import {isNN} from '../../util';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  ads: IAds,
  outings: IOutings,
  users: IUsers,
  homes: IHomes,
  tickets: ITickets,
  blocks: IBlocks,
  gateNo: string,
  onGateNoChanged: (gateNo: string) => void,
  onPaymentUpload: (file: any, homeId: string, user: IUser, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, homeIndex: number, user: IUser, callback: (result: boolean) => void) => void,
  onSaveVisitor: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearchVisitor: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  if (props.appUser.role === ROLE.USER) {
    if (props.appUser.homeIndex >= 0 && isNN(props.user.homes)) {
      return (<Home history={props.history} user={props.user} home={props.user.homes[props.appUser.homeIndex]} onPaymentUpload={(file: any, homeId: string, callback: (result: boolean) => void) => props.onPaymentUpload(file, homeId, props.user, callback)} onPaymentUpdate={(paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => props.onPaymentUpdate(paymentId, transaction, props.appUser.homeIndex, props.user, callback)} />);
    } else {
      return (<View id='mainContent'><Text className='m-20'>No house assigned to you. Please contact administrator/manager.</Text></View>);
    }
  } else if (props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) {
    return (<AMHome history={props.history} appUser={props.appUser} user={props.user} users={props.users} homes={props.homes} blocks={props.blocks} />);
  } else if (props.appUser.role === ROLE.GATEKEEPER) {
    return (<GKHome history={props.history} appUser={props.appUser} user={props.user} homes={props.homes} gateNo={props.gateNo} onGateNoChanged={props.onGateNoChanged} onSaveVisitor={props.onSaveVisitor} onUpload={props.onUpload} onSearchVisitor={props.onSearchVisitor} />);
  } else {
    return (<View id='mainContent'><Text className='m-20'>No role assigned to you. Please contact administrator/manager.</Text></View>);
  }
};
