import * as React from 'react';
import { Link } from 'react-router-dom';
import { confirmAlert } from 'react-confirm-alert';
import { Image } from '../common/elements';
import { isNN } from '../../util/index';
import Images from '../images';
import { ROLE } from '../../constants/index';
import { IHome } from '../../models/home';
import Toast from  '../../util/toast';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  onHomeChange: (index: number, callback: (result: boolean) => void) => void,
  onLogout: (callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const onHomeChange = (index: number) => {
    props.onHomeChange(index, (result: boolean) => {
      if(result === true){
        
      }else{
      }
    });
  }
  
  const logout = () => {
    confirmAlert({
      title: 'Confirm to logout',
      message: 'Are you sure you wish to logout?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            props.onLogout((result: boolean) => {
              if(result === true){
                Toast.success('Logout Success!');
              }else{
                Toast.fail('Failed to logout!');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return (
  <div className="header navbar">
    <div className="header-container">
      <ul className="nav-left">
        <li>
          <a id='sidebar-toggle' className="sidebar-toggle" href="javascript:void(0);">
            <i className="ti-menu"></i>
          </a>
        </li>
        <li className="search-box">
          <a className="search-toggle no-pdd-right" href="javascript:void(0);">
            <i className="search-icon ti-search pdd-right-10"></i>
            <i className="search-icon-close ti-close pdd-right-10"></i>
          </a>
        </li>
        <li className="search-input">
          <input className="form-control" type="text" placeholder="Search..." />
        </li>
      </ul>
      <ul className="nav-right">
        {(props.appUser.role === ROLE.USER) &&
        <li className="notifications dropdown">
          <span className="counter bgc-red">{props.user.homes.length}</span>
          <a href="" className="dropdown-toggle no-after" data-toggle="dropdown">
            <i className="fa fa-home"></i>
          </a>
          <ul className="dropdown-menu">
            <li className="pX-20 pY-15 bdB">
              <i className="fa fa-home pR-10"></i>
              <span className="fsz-sm fw-600 c-grey-900">House(s)</span>
            </li>
            <li>
              <ul className="ovY-a pos-r scrollable lis-n p-0 m-0 fsz-sm">
              {
                props.user.homes.map((d: IHome, i: number) => {
                  return(
                    <li key={'h' + i}>
                      <a href="javascript:void(0);" className='peers fxw-nw td-n p-20 bdB c-grey-800 cH-blue bgcH-grey-100'>
                        <div className="peer peer-greed" onClick={() => onHomeChange(i)}>
                          <span>
                            <span className={(props.appUser.homeIndex === i ? 'fw-500' : 'c-grey-600')}>{'' + d.number}</span>
                          </span>
                        </div>
                      </a>
                    </li>
                  )
                })
                }
                </ul>
            </li>
          </ul>
        </li>
        }
        {(props.user.role.indexOf(',') > 0) &&
        <li className="notifications dropdown">
        <span className="counter bgc-red">{props.user.role.split(',').length}</span>
        <a href="" className="dropdown-toggle no-after" data-toggle="dropdown">
          <i className="ti-user"></i>
        </a>
        <ul className="dropdown-menu">
          <li className="pX-20 pY-15 bdB">
            <i className="ti-user pR-10"></i>
            <span className="fsz-sm fw-600 c-grey-900">Role(s)</span>
          </li>
          <li>
            <ul className="ovY-a pos-r scrollable lis-n p-0 m-0 fsz-sm">
            {
              props.user.role.split(',').map((d: string, i: number) => {
                return(
                  <li key={'r' + i}>
                    <a href="javascript:void(0);" className='peers fxw-nw td-n p-20 bdB c-grey-800 cH-blue bgcH-grey-100'>
                      <div className="peer peer-greed">
                        <span>
                          <span className={'tt-c ' + (props.appUser.role === d ? 'fw-500' : 'c-grey-600')}>{'' + d.toLowerCase()}</span>
                        </span>
                      </div>
                    </a>
                  </li>
                )
              })
              }
              </ul>
          </li>
          <li className="pX-20 pY-15 ta-c bdT">
            <span>
              <Link className="c-grey-600 cH-blue fsz-sm td-n" to='my-role'>Change Role <i className="ti-angle-right fsz-xs mL-10"></i></Link>
            </span>
          </li>
        </ul>
      </li>
        }

        <li className="notifications dropdown">
          <span className="counter bgc-red">0</span>
          <a href="" className="dropdown-toggle no-after" data-toggle="dropdown">
            <i className="ti-bell"></i>
          </a>
          <ul className="dropdown-menu">
            <li className="pX-20 pY-15 bdB">
              <i className="ti-bell pR-10"></i>
              <span className="fsz-sm fw-600 c-grey-900">Notification(s)</span>
            </li>
            <li>
              <ul className="ovY-a pos-r scrollable lis-n p-0 m-0 fsz-sm">
                <li>
                  <a href="" className='peers fxw-nw td-n p-20 bdB c-grey-800 cH-blue bgcH-grey-100'>
                    <div className="peer peer-greed">
                      <span>
                        <span className="c-grey-600">No notification found.</span>
                        </span>
                    </div>
                  </a>
                </li>
              </ul>
            </li>
          </ul>
        </li>
        
        <li className="dropdown">
          <a href="" className="dropdown-toggle no-after peers fxw-nw ai-c lh-1" data-toggle="dropdown">
            <div className="peer mR-10">
              <Image className="w-2r bdrs-50p" src={isNN(props.user.image) ? props.user.image : (props.user.gender.toLocaleUpperCase() === 'F' ? Images.avatarFemale : Images.avatarMale)} alt='' />
            </div>
            <div className="peer">
              <span className="fsz-sm c-grey-900">{props.user.name}</span>
            </div>
          </a>
          <ul className="dropdown-menu fsz-sm">
            <li>
              <Link className="d-b td-n pY-5 bgcH-grey-100 c-grey-700" to='my-profile'>
                <i className="fa fa-user mR-10"></i>
                <span>Profile</span>
              </Link>
            </li>
            <li>
              <Link className="d-b td-n pY-5 bgcH-grey-100 c-grey-700" to='my-documents'>
                <i className="fa fa-file-text mR-10"></i>
                <span>Documents</span>
              </Link>
            </li>
            {props.user.role.indexOf(',') > 0 &&
            <li>
              <Link className="d-b td-n pY-5 bgcH-grey-100 c-grey-700" to='my-role'>
                <i className="ti-user mR-10"></i>
                <span>Role</span>
              </Link>
            </li>
            }
            <li>
              <Link className="d-b td-n pY-5 bgcH-grey-100 c-grey-700" to='change-password'>
                <i className="fa fa-key mR-10"></i>
                <span>Password</span>
              </Link>
            </li>
            <li role="separator" className="divider"></li>
            <li>
              <a href="#" className="d-b td-n pY-5 bgcH-grey-100 c-grey-700" onClick={logout}>
                <i className="ti-power-off mR-10"></i>
                <span>Logout</span>
              </a>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
  );
};