import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text } from '../../common/elements';
import Button from '../../common/button';
import Breadcrum from '../../common/breadcrum';
import Spinner from '../../common/spinner';
import { IMember } from '../../../models/home/member';
import Toast from  '../../../util/toast';
import { isNN } from '../../../util';
import { toDateTimeString } from '../../../util/convert';

interface IProps {
  history: any,
  onLoad: (sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void,
  onReject: (id: string, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([] as Array<IMember>);

  useEffect(() => {
    setIsLoading(true);
    props.onLoad((result: Array<IMember>) => {
      setIsLoading(false);
      setData(result);
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  }, []);

  const onApprove = (id: string) => {
    confirmAlert({
      title: 'Confirm to Approve',
      message: 'You want to Approve the user?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            props.history.push('edit-user/' + id + '/true');
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onReject = (id: string) => {
    confirmAlert({
      title: 'Confirm to Reject',
      message: 'You want to Reject the user?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onReject(id, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                setData(data.filter(v => v._id !== id));
                Toast.success('User rejected successfully.');
              }else{
                Toast.fail('Failed to reject the user.');
              }
            }); 
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Users', link: 'users'}, {title: 'Users Waiting for Approval', link: 'approve-users'}]} />
          <View className='bgc-white p-20'>
            {isNN(data) ?
            data.map((d: IMember, i: number) => {
              return(
                <View key={i} className='bdB mT-5'>
                  <View className='mT-1 mB-3 p-2'>
                    <Text className='fw-600'>{d.name}</Text>
                  </View>
                  {(d.age > 0 || isNN(d.gender)) && 
                  <View className='mT-1 mB-3 p-2'>
                    <Text>{(d.age > 0 ? d.age : '') + ((d.age > 0 && isNN(d.gender)) ? ', ' : '') + (isNN(d.gender) ? (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other')) : '')}</Text>
                  </View>
                  }
                  <View className='mT-1 mB-3 p-2'>
                    On <Text className='fw-600'>{toDateTimeString(d.createdAt)}</Text>
                  </View>
                  {isNN(d.mobileNo) && 
                  <View className='mT-1 mB-3 p-2'>
                    <Text>{d.mobileNo}</Text>
                  </View>
                  }
                  {isNN(d.emailId) && 
                    <View className='mT-1 mB-3 p-2'>
                      <Text>{d.emailId}</Text>
                    </View>
                  }
                  <View className='mT-1 mB-3 p-2'>
                    <Button title='Approve' className='mR-10' onPress={() => { onApprove(d._id); }} />
                    <Button title='Reject' onPress={() => { onReject(d._id); }} />
                  </View>
                </View>
              )
            })
            :
            <Text className='p-20'>No user found.</Text>
            }
            <Spinner visible={isLoading} />
          </View>
        </View>
      </View>
    </View>
  );
};