import React, {useState, useEffect} from 'react';
import { View } from '../../../common/elements';
import Spinner from '../../../common/spinner';
import Breadcrum from '../../../common/breadcrum';
import { IUser } from '../../../../models/user/user';
import { IAppUser } from '../../../../models/app-user';
import { IUsers } from '../../../../models/store/users';
import { IHomes } from '../../../../models/store/homes';
import { IBlocks } from '../../../../models/store/blocks';
import { IHome, IMember, IVehicle, IServant, ITicket, IVisitor, IPayment } from '../../../../models/home';
import { IDocument } from '../../../../models/document';
import Toast from  '../../../../util/toast';
import { isNN } from '../../../../util/index';
import Info from './info';
import Members from './members';
import Vehicles from './vehicles';
import Servants from './servants';
import Tickets from './tickets';
import Visitors from './visitors';
import Payments from './payments';
import Documents from './documents';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks,
  onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => void,
  onLoadMembers: (homeId: string, sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void,
  onLoadVehicles: (homeId: string, sCallback: (result: Array<IVehicle>) => void, fCallback: (message: string) => void) => void,
  onLoadServants: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onLoadTickets: (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => void,
  onLoadVisitors: (homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void,
  onLoadPayments: (homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onLoadDocuments: (homeId: string, sCallback: (result: Array<IDocument>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [homeId, setHomeId] = useState('');
  const [activeTab, setActiveTab] = useState(1);
  const [data, setData] = useState({} as IHome);

  useEffect(() => {
    let homeId = props.match.params.id; // props.navigation.state.params.id;
    setHomeId(homeId);
    if(homeId.length > 0){
      setIsLoading(true);
      props.onLoad(homeId, (result: IHome) => {
        setIsLoading(false);
        if(result && isNN(result._id)){
          setData(result);
        }else{
          Toast.fail('Failed to load house.');
        }
      }, (message: string) => {
        setIsLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load house.');
      });
    }
  }, []);

  const toggleLoading = (isLoading: boolean) => {
    setIsLoading(isLoading);
  }
  
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Houses', link: 'homes'}, {title: 'View House', link: 'view-home'}]} />
          <View className='bgc-white p-20'>
            <View className='tab-bar'>
              <span className={'tab' + (activeTab === 1 ? ' active': '')} onClick={() => setActiveTab(1)}>
                <span className="icon-holder">
                  <i className="c-deep-purple-a700 ti-info-alt"></i>
                </span>
              </span>
              <span className={'tab' + (activeTab === 2 ? ' active': '')} onClick={() => setActiveTab(2)}>
                <span className="icon-holder">
                  <i className="c-brown-500 fa fa-users"></i>
                </span>
              </span>
              <span className={'tab' + (activeTab === 3 ? ' active': '')} onClick={() => setActiveTab(3)}>
                <span className="icon-holder">
                  <i className="c-indigo-500 fa fa-car"></i>
                </span>
              </span>
              <span className={'tab' + (activeTab === 4 ? ' active': '')} onClick={() => setActiveTab(4)}>
                <span className="icon-holder">
                  <i className="c-deep-purple-500 fa fa-female"></i>
                </span>
              </span>
              <span className={'tab' + (activeTab === 5 ? ' active': '')} onClick={() => setActiveTab(5)}>
                <span className="icon-holder">
                  <i className="c-orange-500 ti-headphone-alt"></i>
                </span>
              </span>
              <span className={'tab' + (activeTab === 6 ? ' active': '')} onClick={() => setActiveTab(6)}>
                <span className="icon-holder">
                  <i className="c-pink-500 fa fa-id-card-o"></i>
                </span>
              </span>
              {props.user.client.config.hasPayment &&
              <span className={'tab' + (activeTab === 7 ? ' active': '')} onClick={() => setActiveTab(7)}>
                <span className="icon-holder">
                  <i className="c-indigo-500 fa fa-credit-card"></i>
                </span>
              </span>
              }
              <span className={'tab' + (activeTab === 8 ? ' active': '')} onClick={() => setActiveTab(8)}>
                <span className="icon-holder">
                  <i className="c-blue-500 fa fa-file-text"></i>
                </span>
              </span>
            </View>
            <View className='p-15'>
              {
                activeTab === 1 ?
                    <Info blocks={props.blocks} data={data} />
                :
                  activeTab === 2 ?
                    <Members user={props.user} homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadMembers} />
                  :
                    activeTab === 3 ?
                      <Vehicles homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadVehicles} />
                    :
                      activeTab === 4 ?
                        <Servants homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadServants} />
                      :
                        activeTab === 5 ?
                          <Tickets homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadTickets} />
                        :
                          activeTab === 6 ?
                            <Visitors appUser={props.appUser} user={props.user} homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadVisitors} />
                          :
                            activeTab === 7 ?
                              <Payments homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadPayments} />
                            :
                              <Documents homeId={homeId} onToggleLoading={toggleLoading} onLoad={props.onLoadDocuments} />
              }            
            </View>
          </View>
          <Spinner visible={isLoading} />
        </View>
      </View>
    </View>
  );
};