import React, {useState, useEffect} from 'react';
import { View, TextInput, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import { IHomes } from '../../../models/store/homes';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home';
import NewVisitor from './new-visitor';
import { isNN } from '../../../util';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  gateNo: string,
  onGateNoChanged: (gateNo: string) => void,
  onSaveVisitor: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearchVisitor: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [gateNo, setGateNo] = useState('');

  useEffect(() => {
    setGateNo(props.gateNo);
  }, [props.gateNo]);

  const viewAllVisitors = () => {
    if(isNN(gateNo)) {
      props.history.push('all-visitors')
    } else {
      Toast.warn('Please enter Gate Number to view this page.');
    }
  };
  
  const viewAllTakeaways = () => {
    if(isNN(gateNo)) {
      props.history.push('all-takeways')
    } else {
      Toast.warn('Please enter Gate Number to view this page.');
    }
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <View className='bgc-white p-20'>
            <View className="">
              <TextInput type='text' placeholder='Gate Number' className='form-control w-a' value={gateNo} onChange={(event: any) => setGateNo(event.target.value)} onBlur={() => props.onGateNoChanged(gateNo)} />
              <Text className='fl-r c-blue-700 fw-600 cur-p' onClick={viewAllTakeaways}>View all Active Takeaways</Text>
              <Text className='fl-r c-blue-700 fw-600 cur-p mR-20' onClick={viewAllVisitors}>View all Active Visitors</Text>
            </View>
            <NewVisitor appUser={props.appUser} user={props.user} homes={props.homes} gateNo={gateNo} onToggleLoading={(isLoading) => setIsLoading(isLoading)} onSave={props.onSaveVisitor} onUpload={props.onUpload} onSearch={props.onSearchVisitor} />
            <Spinner visible={isLoading} />
          </View>
        </View>
      </View>
    </View>
  );
};