import * as React from 'react';
import { generateUUID } from '../../util';

interface IProps {
  isActive : boolean,
  text: string,
  onPress?: () => void,
  key?: string,
  className?: string
};

export default (props: IProps) => {
  const {isActive, text, onPress, key, className} = props;
  let id = generateUUID();
  return (
    <span className={'mR-10 ' + (className ? className : '')}>
      <input type='checkbox' id={id} key={key ? key : id} checked={isActive} className='va-m' onClick={onPress ? onPress : () =>{}} />
      <label htmlFor={id} className='mL-5 va-m mB-0'>{text}</label>
    </span>
  );
};