import React, {useState, useEffect} from 'react';
import { isNN } from '../../../../util/index';
import { IVisitor } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { IAppUser } from '../../../../models/app-user';
import { IUser } from '../../../../models/user';
import VisitorsView from '../../gatekeeper/visitors-view';
import { View, Text } from '../../../common/elements';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [data, setData] = useState([] as Array<IVisitor>);
  const [isActive, setIsActive] = useState(true);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = () => {
    if(props.homeId.length > 0){
      props.onToggleLoading(true);
      props.onLoad(props.homeId, isActive, (result: Array<IVisitor>) => {
        props.onToggleLoading(false);
        setData(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Visitors.');
      });
    }
  };
  
  const onActiveChange = () => {
    setIsActive(!isActive);
    loadData();
  };

  return(
    <View>
      <View className='ta-c mT-1 mB-3 p-2'>
        <Text className='c-blue-700 fw-600 mB-10'>Visitors</Text>
      </View>
      <VisitorsView data={data} isActive={isActive} user={props.user} appUser={props.appUser} onActiveChange={onActiveChange} />
    </View>
  );
};