import * as React from 'react';
import { View } from '../common/elements';

interface IProps {
  title: string,
  icon: string,
  color: string,
  onClick: () => void,
  bgColor?: string,
  hasCount?: boolean,
  count?: string
};

export default (props: IProps) => {
  return (
    <View className='col-md-3'>
      <View className="layers bd bgc-white p-20 cur-p" onClick={props.onClick}>
        <View className="layer w-100 mB-10">
          <h6 className="lh-1">{props.title}</h6>
        </View>
        <View className="layer w-100">
          <View className="peers ai-sb fxw-nw">
            <View className="peer peer-greed">
              <span className="icon-holder">
                <i className={props.icon + ' ' + props.color + ' fa-2x'}></i>
              </span>
            </View>
            <View className="peer">
              {props.hasCount ?
              <span className={'d-ib lh-0 va-m fw-600 bdrs-10em pX-15 pY-15 ' + props.color + ' ' + props.bgColor}>{props.count}</span>
              :
              <span className={'d-ib lh-0 va-m ' + props.color}>View all</span>
              }
            </View>
          </View>
        </View>
      </View>
    </View>
  );
};