import React, { useState } from 'react';
import { View, TextInput, Text } from '../common/elements';
import Button from '../common/button';
import Spinner from '../common/spinner';
import Breadcrum from '../common/breadcrum';
import { IUser } from '../../models/user/user';
import { isNN, onEnter } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  user: IUser,
  onUpdate: (userId: string, password: string, newPassword: string, sCallback: (result: boolean, message: string) => void) => void,
  onLogout: (callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');

  const passwordPolicyCheck = (p: string) => {
    let anUpperCase = /[A-Z]/;
    let aLowerCase = /[a-z]/; 
    let aNumber = /[0-9]/;
    let aSpecial = /[!|@|#|$|%|^|&|*|(|)|-|_]/;
    let result = "";

    if(p.length < 6){
      result="New Password not long enough!"
        return result;
    }

    var numUpper = 0;
    var numLower = 0;
    var numNums = 0;
    var numSpecials = 0;
    for(var i=0; i<p.length; i++){
        if(anUpperCase.test(p[i]))
            numUpper++;
        else if(aLowerCase.test(p[i]))
            numLower++;
        else if(aNumber.test(p[i]))
            numNums++;
        else if(aSpecial.test(p[i]))
            numSpecials++;
    }

    if(numUpper < 1 || numLower < 1 || numNums < 1 || numSpecials < 1){
      result="New Password dose not match with password policy!";
        return result;
    }
    return result;
  };

  const onUpdate = () => {
    if(isNN(password) && isNN(newPassword) && isNN(confirmNewPassword)){
      if(newPassword === confirmNewPassword){
        let policyResult = passwordPolicyCheck(newPassword);
        if(policyResult.length < 1){
          setIsLoading(true);
          props.onUpdate(props.user._id, password, newPassword, (result: boolean, message: string) => {
            setIsLoading(false);
            if(result === true){
              Toast.success('Password updated successfully. Please login with your new password.');
                props.onLogout((result: boolean) => {});
            }else{
              Toast.fail(message);
            }
          });
        }else{
          Toast.warn(policyResult);
        }
      }else{
        Toast.warn('New Password and Confirm New Password not matchs.');
      }
    }else{
      Toast.warn('Please fill all the fields before Update.');
    }
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Change Password', link: 'change-password'}]} />
          <View className='bgc-white p-20'>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <TextInput type='password' placeholder='Password' className='form-control' value={password} onChange={(event: any) => setPassword(event.target.value)} />
            </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <TextInput type='password' placeholder='New Password' className='form-control' value={newPassword} onChange={(event: any) => setNewPassword(event.target.value)} />
            </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <TextInput type='password' placeholder='Confirm New Password' className='form-control' value={confirmNewPassword} onChange={(event: any) => setConfirmNewPassword(event.target.value)}
              onKeyPress={(e) => {
                onEnter(e, onUpdate);
              }} />
            </View>
            <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
              <Button
              title="UPDATE"
              onPress={onUpdate}
              />
            </View>
            <View className='mT-7'>
              <Text>Note: Password should contain adleast one uppercase, one lowercase, one number and one special character.</Text>
            </View>
          </View>
          <Spinner visible={isLoading} />
        </View>
      </View>
    </View>
  );
};