import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text, TextInput, TextArea } from '../../common/elements';
import Spinner from '../../common/spinner';
import Button from '../../common/button';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { INotification } from '../../../models/notification';
import { isNN, isNNObject, onEnter } from '../../../util/index';
import { toDateString } from '../../../util/convert';
import Toast from  '../../../util/toast';
import RadioButton from '../../common/radio-button';

interface IProps {
  user: IUser,
  onLoad: (appClientId: string, sCallback: (result: Array<INotification>) => void, fCallback: (message: string) => void) => void,
  onPush: (appClientId: string, data: INotification, user: IUser, callback: (result: INotification | null) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([] as Array<INotification>);
  const [title, setTitle] = useState('');
  const [body, setBody] = useState('');

  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, (result: Array<INotification>) => {
      setIsLoading(false);
      setData(result);
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  }, []);
  
  const onPush = () => {
    if(isNN(title) && isNN(body)){
      confirmAlert({
        title: 'Confirm to send push notification',
        message: 'Are you sure? This notification will send to all users using this app in your Appartment. Are you sure you want to Push?',
        buttons: [
          {
            label: 'Yes',
            onClick: () => {
              setIsLoading(true);
              let notification = {
                title: title,
                body: body
              } as INotification;
              props.onPush(props.user.client._id, notification, props.user, (result: INotification | null) => {
                setIsLoading(false);
                if(isNNObject(result)){
                  // data.unshift(result as INotification);
                  setData([result as INotification, ...data]);
                  setTitle('');
                  setBody('');
                  Toast.success('Notification pushed successfully.');
                }else{
                  Toast.fail('Failed to push Notification.');
                }
              });
            }
          },
          {
            label: 'No',
            onClick: () => {}
          }
        ]
      });
    }else{
      Toast.warn('Please fill all the required fields before Push.');
    }
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Notification', link: 'notification'}]} />
        <View className='bgc-white p-20'>
        {isNN(props.user.client.config.notificationTypes) &&
        <View className='mT-3 mB-3 pT-5 pB-5'>
          {props.user.client.config.notificationTypes.map((d, i) => {
            return(<RadioButton isActive={d === title} text={d} onPress={() => setTitle(d)} />);
          })
          }
          <RadioButton isActive={!isNN(props.user.client.config.notificationTypes.filter((d, i) => d === title))} text='Other' onPress={() => setTitle('')} />
        </View>
        }
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextInput type='text' placeholder="Title" className='form-control' value={title} disabled={isNN(props.user.client.config.notificationTypes.filter((d, i) => d === title))} onChange={(event: any) => setTitle(event.target.value)} />
        </View>
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextArea placeholder="Body" className='form-control' value={body} onChange={(event: any) => setBody(event.target.value)}
          onKeyPress={(e) => {
            onEnter(e, onPush);
          }} />
        </View>
        <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
            <Button
            className='mR-10'
            title="PUSH"
            onPress={onPush}
            />
            <Button
            title="CLEAR"
            onPress={() => {
              setTitle('');
              setBody('');
            }}
            />
        </View>
      {(isNN(data)) ?
        data.map((d: INotification, i: number) => {
          return(
            <View key={i} className={'mT-5' + (i === 0 ? '' : ' bdT')}>
              <View className='mT-1 mB-3 p-2'>
                  <Text className='fw-600'>{d.createdBy + " on " + toDateString(d.createdAt)}</Text>
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text className='fw-600'>{d.title}</Text>
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.body}</Text>
              </View>
              <View className='mT-3 mB-3 pT-5 pB-5'>
                <Button title="Duplicate" onPress={() => {
                  setTitle(d.title);
                  setBody(d.body);
                }} />
              </View>
            </View>
          )
        })
        :
        <Text className='p-20'>No notification found.</Text>
      }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};