import React, {useState} from 'react';
import Modal from 'react-responsive-modal';
import moment from 'moment';
import { View, Image, Text, TextInput } from '../../common/elements';
import Checkbox from '../../common/checkbox';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home/visitor';
import { isNN } from '../../../util/index';
import { FILE_BASE_PATH, ROLE } from '../../../constants';
import Button from '../../common/button';
import { toDateTimeString } from '../../../util/convert';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  data: Array<IVisitor>,
  isActive: boolean,
  isSearch?: boolean,
  onActiveChange?: () => void,
  onEnter?: (id: string) => void,
  onExit?: (id: string) => void,
  onApprove?: (id: string) => void,
  onReject?: (id: string) => void,
  onSelect?: (data: IVisitor) => void,
  onRemove?: (id: string) => void
};

export default (props: IProps) => {
  const [isZoomImage, setIsZoomImage] = useState(false);
  const [zoomImage, setZoomImage] = useState('');
  const [searchText, setSearchText] = useState('');

  const onToggleZoomImage = (isZoom: boolean) => {
    setIsZoomImage(isZoom);
  };
  
  let tSearchText = isNN(searchText) ? searchText.toLowerCase() : searchText;
  let data = ((isNN(tSearchText) && isNN(props.data)) ? props.data.filter((d, i) => d.homeNumber.toLowerCase().indexOf(tSearchText) >= 0 || d.passCode.toLowerCase().indexOf(tSearchText) >= 0 || d.name.toLowerCase().indexOf(tSearchText) >= 0) : props.data);
    return(<>
      <View className='bdB mT-5'>
        <Text>{'No of ' + (props.isActive ? 'Active' : '') + ' Visitors: ' + props.data.length}</Text>
        {(!props.isSearch && props.appUser.role !== ROLE.GATEKEEPER) &&
        <Checkbox isActive={props.isActive} text='Only Active Visitors' onPress={props.onActiveChange} className='fl-r' />
        }
      </View>
      {isNN(props.data) &&
      <View className='mT-10 mB-5'>
        <TextInput type='text' placeholder="Enter passcode to search" className='form-control' value={searchText} onChange={(event: any) => setSearchText(event.target.value)} />
      </View>
      }
      <View className='p-10 mB-10'>
        {isNN(data) ?
        data.map((d: IVisitor, i: number) => {
          return(
            <View key={i} className='card m-1 d-ib'>
              {isNN(d.image) &&
              <View className='ta-c mB-5'>
                <Image src={FILE_BASE_PATH + d.image} className='cur-p m-0' style={{height: '25vh', borderRadius: '50%'}} onClick={() => {
                  setIsZoomImage(true);
                  setZoomImage(d.image);
                  }} />
              </View>
              }
              <View className='container'>
                {isNN(d.passCode) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text className='fw-600'>{d.passCode}</Text>
                </View>
                }
                <View className='mT-1 mB-3 p-2'>
                  <Text className='fw-600'>{d.name + ' [' + d.type + ']'}</Text>
                </View>
                {(d.age > 0 || isNN(d.gender)) && 
                <View className='mT-1 mB-3 p-2'>
                  <Text>{(d.age > 0 ? d.age : '') + ((d.age > 0 && isNN(d.gender)) ? ', ' : '') + (isNN(d.gender) ? (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other')) : '')}</Text>
                </View>
                }
                <View className='mT-1 mB-3 p-2'>
                  To <Text className='fw-600'>{d.homeNumber}</Text> on <Text className='fw-600'>{toDateTimeString(d.createdAt)}</Text>
                </View>
                {(!d.isEntered && !d.isExit) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{((d.gateNo) ? 'In gate ' + d.gateNo + ' by ': 'By ') + d.createdBy}</Text>
                </View>
                }
                {isNN(d.mobileNo) && 
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.emailId) && 
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.emailId}</Text>
                </View>
                }
                {d.personCount > 0 && 
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.personCount + ' Person' + (d.personCount > 1 ? 's' : '') + (d.vehicleType === 'WALK' ? ' by WALK' : ' in ' + d.vehicleType + (isNN(d.vehicleNo) ? ' ' + d.vehicleNo : ''))}</Text>
                </View>
                }
                {(!d.isApproved && !d.isRejected && !d.isEntered) && 
                <View className='mT-1 mB-3 p-2'>
                  <View className='c-blue-500'>WAITING FOR APPROVAL</View>
                </View>
                }
                {(d.isApproved || d.isRejected ) && 
                <View className='mT-1 mB-3 p-2'>
                  <View className={(d.isApproved ? 'c-green-600' : 'c-red-600')}>{d.isApproved ? 'APPROVED' : 'REJECTED'}</View>
                  <View>{'By ' + d.approvedBy}</View>
                  <View>{'On ' + toDateTimeString(d.approvedAt)}</View>
                </View>
                }
                {d.isEntered &&
                <View className='mT-1 mB-3 p-2'>
                  <View>{'Entry By ' + d.enteredBy + ' in gate ' + d.gateNo}</View>
                  <View>{'On ' + toDateTimeString(d.enteredAt)}</View>
                </View>
                }
                {(d.isApproved && !d.isEntered) &&
                <View className='mT-1 mB-3 p-2'>
                  <View className='c-red-600'>NOT ENTERED</View>
                </View>
                }
                {(!d.isRejected && d.isExit) &&
                <View className='mT-1 mB-3 p-2'>
                  <View>{'Exit By ' + d.exitBy + ' in gate ' +  d.exitGateNo}</View>
                  <View>{'On ' + toDateTimeString(d.exitAt)}</View>
                </View>
                }
                {(d.isEntered && !d.isExit) &&
                <View className='mT-1 mB-3 p-2'>
                  <View className='c-red-600'>STAYING IN</View>
                </View>
                }
                {(!d.isEntered && d.passCode && d.arriveAt) && 
                <View className='mT-1 mB-3 p-2'>
                  <Text className='fw-600 c-red-600'>{'Expected Arrival ' + toDateTimeString(d.arriveAt)}</Text>
                </View>
                }
                {(!props.isSearch && props.appUser.role === ROLE.GATEKEEPER) &&
                <View className='mT-1 mB-3 p-2 ta-c'>
                  {(!d.isEntered && !d.isRejected && (!d.arriveAt || new Date(d.arriveAt).getDate() === moment().toDate().getDate())) && <Button title='Enter' onPress={() => { props.onEnter && props.onEnter(d._id) }} />}
                  {((d.isEntered || d.isRejected) && !d.isExit) && <Button title={d.isRejected ? 'Remove' : 'Exit'} onPress={() => { props.onExit && props.onExit(d._id) }} />}
                </View>
                }
                {(!props.isSearch && props.appUser.role === ROLE.USER && !d.isApproved && !d.isRejected && !d.isEntered) &&
                <View className='mT-1 mB-3 p-2 ta-c'>
                  <Button title='Approve' className='mR-10' onPress={() => { props.onApprove && props.onApprove(d._id) }} />
                  <Button title='Reject' onPress={() => { props.onReject && props.onReject(d._id) }} />
                </View>
                }
                {(!props.isSearch && props.appUser.role === ROLE.USER && isNN(d.passCode) && !d.isEntered && !d.isRejected) &&
                <View className='mT-1 mB-3 p-2 ta-c'>
                  <Button title='Remove' onPress={() => { props.onRemove && props.onRemove(d._id) }} />
                </View>
                }
                {props.isSearch &&
                <View className='mT-1 mB-3 p-2 ta-c'>
                  <Button title='Select' onPress={() => { props.onSelect && props.onSelect(d) }} />
                </View>
                }
              </View>
            </View>
          )
        })
        :
        <Text className='p-20'>No visitor found.</Text>
      }
      </View>
      <Modal
          open={isZoomImage}
          onClose={() => onToggleZoomImage(false)}>
          <View className='ta-c'>
            <Image src={FILE_BASE_PATH + zoomImage} style={{ height: '50%', width: '50%' }} />
          </View>
        </Modal>
    </>
    );
};