import * as React from 'react';
import { View, Text, ScrollView } from '../common/elements';
import { IHome } from '../../models/home/home';
import { IPayment } from '../../models/home';
import { ITransaction } from '../../models/home/transaction';
import { isNN } from '../../util';
import { toDateString } from '../../util/convert';
import { MONTH } from '../../constants';

interface IProps {
  history: any,
  home: IHome,
};

export default (props: IProps) => {
    return (
      <ScrollView>
        {isNN(props.home.payments.filter((d: IPayment, i: number) => isNN(d.transactions) && isNN(d.transactions.filter((t: ITransaction, j: number) => t.isSuccess === true)))) ?
        props.home.payments.filter((d: IPayment, i: number) => isNN(d.transactions) && isNN(d.transactions.filter((t: ITransaction, j: number) => t.isSuccess === true))).slice(0, 3).map((d: IPayment, i: number) => {
          let amountPaid = 0;
          let paidDate = null;
          d.transactions.filter(t => t.isSuccess === true).map((t: ITransaction, j: number) => {
            amountPaid += t.amount;
            if(j === 0){
              paidDate = t.createdAt;
            }
            return 1;
          });
              return (
                <View key={'my-his' + i} className={'m-7' + (i === 0 ? '' : ' pL-15 bdL') }>
                  <View className='mT-4'>
                    <Text>{"Paid for: " + MONTH.filter(m => m.value === d.forMonth)[0].title + "/" + d.forYear}</Text>
                  </View>
                  <View className='mT-4 mB-3'>
                    <Text>{"Rs " + amountPaid}</Text>
                  </View>
                  <View>
                    <Text>{"Paid On: " + toDateString(paidDate)}</Text>
                  </View>
                </View>
              )
          })
          :<Text>No history found!</Text>
        }
        </ScrollView>
    );
};