import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import {  View, Text, TextInput, TextArea } from '../common/elements';
import Button from '../common/button';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IDocuments } from '../../models/store/documents';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home';
import { IDocument } from '../../models/document';
import { FILE_BASE_PATH } from '../../constants';
import { isNN, generateUUID } from '../../util/index';
import { toDateString } from '../../util/convert';
import Toast from  '../../util/toast';

interface IProps {
  homeIndex: number,
  user: IUser,
  documents: IDocuments,
  onLoad: (homeId: string, callback: (result: boolean) => void) => void,
  onSave: (homeId: string, data: IDocument, homeIndex: number, user: IUser, documents: Array<IDocument>, callback: (result: boolean) => void) => void,
  onRemove: (id: string, documents: Array<IDocument>, callback: (result: boolean) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [state, setState] = useState({
    id: '',
      homeId: isNN(props.user.homes) ? props.user.homes[0]._id : '',
      title: '',
      description: '',
      fileName: ''
  });

  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.homes[props.homeIndex]._id, (result: boolean) => {
      setIsLoading(false);
      if(!result){
        Toast.fail('Failed to load Documents.');
      }
    });
  }, []);

  const onFileSelect = (event: any) => {
    let file = event.target.files[0];
    if(file && file.name){
      let arrFileName = file.name.split('.');
      if(arrFileName && arrFileName.length > 1){
        let fileName = generateUUID();
        fileName += '.' + arrFileName[arrFileName.length - 1];
        setIsLoading(true);
        // upload file to api.
        let data = {
          file: file,
          type: file.type,
          name: fileName
        };
        props.onUpload(data, (result: boolean, message: string) => {
          setIsLoading(false);
          if(!result){
            Toast.fail(message ? message : 'Failed to upload file.');
          }else{
            setState({
              ...state,
              fileName: fileName
            });
          }
        });
      }
    }
  };

  const onSave = () => {
    if(isNN(state.title) && isNN(state.fileName)){
      setIsLoading(true);
        /*
        let data = state.data;
        data.id = state.id,
        data.userId = props.user._id,
        data.homeId = state.homeId;
        data.title = state.title;
        data.description = state.description;
        data.createdBy = props.user._id;
        data.createdOn = new Date();
        */
        
        let document = {
          _id: state.id,
          title: state.title,
          description: state.description,
          file: state.fileName,
          createdBy: props.user.name,
          createdAt: new Date()
        } as IDocument;
        props.onSave(state.homeId, document, props.homeIndex, props.user, props.documents.data, (result: boolean) => {
          setIsLoading(false);
          if(result === true){
            setIsEdit(false);
            onClear();
            Toast.success('Document saved successfully.');
          }else{
            Toast.fail('Failed to save document.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onClear = () => {
    setState({
      ...state,
      id: '',
      homeId: isNN(props.user.homes) ? props.user.homes[0]._id : '',
      title: '',
      description: '',
      fileName: ''
    });
  };
  
  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.documents.data, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                Toast.success('Document removed successfully.');
              }else{
                Toast.fail('Failed to remove document.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'My Documents', link: 'my-documents'}]} />
        <View className='bgc-white p-20'>
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextInput type='text' placeholder="Title" className='form-control' value={state.title} onChange={(event: any) => setState({...state, title: event.target.value})} />
        </View>
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextArea placeholder="Description" className='form-control' value={state.description} onChange={(event: any) => setState({...state, description: event.target.value})} />
        </View>
        {isNN(props.user.homes) &&
        <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text className='mR-10'>House:</Text>
        {props.user.homes.map((h: IHome, i: number) => {
          return(
            <RadioButton isActive={h._id === state.homeId} text={h.number} key={'home' + i} onPress={() => setState({...state, homeId: h._id})} />
          )
        })
        }
        </View>
        }
        <View className='mT-3 mB-3 pT-5 pB-5'>
          {/* <Button
              title='Select Document'
              onPress={onImageSelect}
              /> */}
              <input type='file' name='file' placeholder='Select Document' onChange={onFileSelect}/>
              {isNN(state.fileName) &&
              <Text className='mL-15'>{state.fileName}</Text>
              }
        </View>
        <View className='cur-p mT-3 mB-3 pT-5 pB-5'>
            <Button
            className='mR-10'
            title='SAVE'
            onPress={onSave}
            />
            <Button
            title='CLEAR'
            onPress={onClear}
            />
        </View>
      {(props.documents.isUpToDate && !props.documents.isError && isNN(props.documents.data)) ?
        props.documents.data.map((d: IDocument, i: number) => {
          return(
            <View key={i} className='bdB mT-5'>
              <View className='mT-1 mB-3 p-2'>
                  <Text className='fw-600'>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text className='fw-600'>{d.title}</Text>
              </View>
              {isNN(d.description) &&
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.description}</Text>
              </View>
              }
              {isNN(props.user.homes.filter(ho => ho._id === d._id)) &&
              <View className='mT-1 mB-3 p-2'>
                <Text>{'House: ' + props.user.homes.filter(ho => ho._id === d._id)[0].number}</Text>
              </View>
              }
              {isNN(d.file) &&
              <View className='mT-1 mB-3 p-2'>
                <a target='_blank' rel='noopener noreferrer' href={FILE_BASE_PATH + d.file}>{d.file}</a>
              </View>
              }
              <View className='mT-1 mB-3 p-2'>
                {/* <span className="icon-holder mR-10 cur-p" onClick={() => {
                  setIsEdit(true);
                  setState({
                    ...state,
                    id: d.id,
                    homeId: d.homeId,
                    title: d.title,
                    description: d.description,
                  });
                }}>
                    <i className="c-blue-500 fa fa-pencil"></i>
                  </span> */}
                  <span className="icon-holder cur-p" onClick={() => {
                    onRemove(d._id);
                  }}>
                    <i className="c-red-500 fa fa-trash"></i>
                  </span>
              </View>
            </View>
          )
        })
        :
        <Text className='p-20'>{props.documents.isUpToDate ? 'No document found.' : (props.documents.isError ?  props.documents.errorMessage : 'No document found.')}</Text>
      }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};