import React, {useState} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import {  View, Text, TextInput, } from '../common/elements';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { IVehicle } from '../../models/home/vehicle';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  homeIndex: number,
  onSave: (data: IVehicle, user: IUser, homeIndex: number, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, user: IUser, homeIndex: number, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [id, setId] = useState('');
  const [number, setNumber] = useState('');
  const [type, setType] = useState('');
  
  const onSave = () => {
    if(isNN(number) && isNN(type)){
      setIsLoading(true);
        let vehicle = {
          _id: id,
          number: number,
          type: type
        } as IVehicle;
        props.onSave(vehicle, props.user, props.homeIndex, (result: boolean) => {
          setIsLoading(false);
          if(result === true){
            setIsEdit(false);
            setId('');
            setNumber('');
            setType('');
            Toast.success('Vehicle saved successfully.');
          }else{
            Toast.fail('Failed to save vehicle.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.user, props.homeIndex, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                Toast.success('Vehicle removed successfully.');
              }else{
                Toast.fail('Failed to remove vehicle.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Vehicles', link: 'vehicles'}]} />
        <View className='bgc-white p-20'>
        <View>
          <Text className='fl-r cur-p c-blue-500' onClick={() => props.history.push('new-vehicle')}>Add New</Text>
        </View>
        {isNN(props.user.homes[props.homeIndex].vehicles) ?
          props.user.homes[props.homeIndex].vehicles.map((d: IVehicle, i: number) => {
            return(
              (isEdit === true && id === d._id) ?
              <View key={i} className='bdB mT-5'>
                <View className='mT-1 mB-3 p-2'>
                  <RadioButton isActive={type === 'TWO WHEELER'} text='Two Wheeler' onPress={() => setType('TWO WHEELER')} />
                  <RadioButton isActive={type === 'FOUR WHEELER'} text='Four Wheeler' onPress={() => setType('FOUR WHEELER')} />
                </View>
                <View className='mT-1 mB-3 p-2'>
                <TextInput type='text' placeholder="Number" className='form-control' value={number} onChange={(event: any) => setNumber(event.target.value)} />
                </View>
                <View className='mT-1 mB-3 p-2'>
                <span className="icon-holder mR-10 cur-p" onClick={onSave}>
                  <i className="c-green-500 fa fa-check"></i>
                </span>
                <span className="icon-holder cur-p" onClick={() => {
                    setIsEdit(false);
                    setId('');
                    setNumber('');
                    setType('');
                  }}>
                  <i className="c-red-500 fa fa-times"></i>
                </span>
                </View>
              </View>
              :
              <View key={i} className='bdB mT-5'>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.type}</Text>                  
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.number}</Text>
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <span className="icon-holder mR-10 cur-p" onClick={() => {
                    setIsEdit(true);
                    setId(d._id);
                    setNumber(d.number);
                    setType(d.type);
                  }}>
                    <i className="c-blue-500 fa fa-pencil"></i>
                  </span>
                  <span className="icon-holder cur-p" onClick={() => {
                    onRemove(d._id);
                  }}>
                    <i className="c-red-500 fa fa-trash"></i>
                  </span>
                </View>
              </View>
            )
          })
          :
          <Text className='p-20'>No vehicle found.</Text>
        }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};