import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home/visitor';
import Toast from  '../../../util/toast';
import { IHomes } from '../../../models/store/homes';
import VisitorsView from './visitors-view';
import { View } from '../../common/elements';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  homes: IHomes,
  gateNo: string,
  onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void,
  onEnter: (id: string, gateNo: string, callback: (result: boolean) => void) => void,
  onExit: (id: string, gateNo: string, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([] as Array<IVisitor>);
  const [isActive, setIsActive] = useState(true);
  
  const loadData = () => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, '', isActive, (result: Array<IVisitor>) => {
      setIsLoading(false);
      setData(result);
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  };

  useEffect(() => {
    loadData();
  }, []);
  
  const onActiveChange = () => {
    setIsActive(!isActive);
    loadData();
  };

  const onEnter = (id: string) => {
    confirmAlert({
      title: 'Confirm to Allow Visitor Entry',
      message: 'Are you sure you wish to Allow the visitor to Enter?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onEnter(id, props.gateNo, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                let tData = data;
                let foundIndex = tData.findIndex(v => v._id === id);
                tData[foundIndex].isEntered = true;
                tData[foundIndex].enteredBy = props.user.name;
                tData[foundIndex].enteredAt = new Date();
                setData(tData);
                Toast.success('Visitor entered successfully.');
              }else{
                Toast.fail('Failed to allow the entry of visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onExit = (id: string) => {
    confirmAlert({
      title: 'Confirm to Exit Visitor',
      message: 'Are you sure you wish to exit visitor?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onExit(id, props.gateNo, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                setData(data.filter(v => v._id !== id));
                Toast.success('Visitor exit successfully.');
              }else{
                Toast.fail('Failed to exit visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Visitors', link: 'all-visitors'}]} />
          <View className='bgc-white p-20'>
            <VisitorsView user={props.user} appUser={props.appUser} data={data} isActive={isActive} onActiveChange={onActiveChange} onEnter={onEnter} onExit={onExit} />
            <Spinner visible={isLoading} />
          </View>
        </View>
      </View>
    </View>
  );
};