import React, {useState} from 'react';
import { View } from '../../common/elements';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IHomes } from '../../../models/store/homes';
import { IPayment } from '../../../models/home';
import PaymentForm from './payment-form';
import PaymentHistory from './payment-history';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onLoad: (appClientId: string, homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onSave: (appClientId: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void,
  onSearch: (appClientId: string, data: any, user: IUser, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onUpdate: (id: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [activeTab, setActiveTab] = useState(1);
  // const [history, setHistory] = useState([] as Array<IPayment>);

  // useEffect(() => {
  //   props.onLoad(props.user.client._id, '', (result: Array<IPayment>) => {
  //     if(isNN(result)){
  //       setHistory(result);
  //     }
  //   }, (message: string) => {
  //     Toast.fail(message ? message : 'Failed to load Payments.');
  //   });
  // }, []);
  
    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Payment', link: 'payment'}]} />
        <View className='bgc-white p-20'>
          <View className='tab-bar'>
            <span className={'tab' + (activeTab === 1 ? ' active': '')} onClick={() => setActiveTab(1)}>
              NEW
            </span>
            <span className={'tab' + (activeTab === 2 ? ' active': '')} onClick={() => setActiveTab(2)}>
              HISTORY
            </span>
          </View>
          <View className='tab-content p-15'>
            {
              activeTab === 1 ? 
                <PaymentForm history={props.history} appUser={props.appUser} user={props.user} homes={props.homes} onSave={props.onSave} />
              :
                <PaymentHistory history={props.history} appUser={props.appUser} user={props.user} homes={props.homes} onSearch={props.onSearch} onUpdate={props.onUpdate} />
            }            
          </View>
        </View>        
      </View>
      </View>
      </View>
    );
};