import React, {useState, useEffect} from 'react';
import Modal from 'react-responsive-modal';
import { ScrollView, View, TextInput, Text, Picker, TextArea } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { isNN } from '../../../util/index';
import { IAppUser } from '../../../models/app-user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IBlocks } from '../../../models/store/blocks';
import { IBlock } from '../../../models/block';
import { IHome, IMember } from '../../../models/home';
import Toast from  '../../../util/toast';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks,
  onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => void,
  onSave: (data: IHome, user: IUser, homes: IHomes, callback: (result: boolean, message: string) => void) => void,
  onCheckNumberAvail: (id: string, number: string, callback: (result: boolean) => void) => void,
};

interface IState{
  id: string,
	blockId: string,
	number: string,
	address: string,
  bedRoom: number,
  parking: string,
  description: string,
  members: Array<string>,
};

export default (props:IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isShowUsers, setIsShowUsers] = useState(false);
  const [formData, setFormData] = useState({
    id: '',
    blockId: '',
    number: '',
    address: '',
    bedRoom: 1,
    parking: '',
    description: '',
    members: [] as Array<string>
  });

useEffect(() => {
    let homeId = props.match.params.id; // props.navigation.state.params.id;
    if(homeId.length > 0 && homeId !== '0'){
      setIsLoading(true);
      props.onLoad(homeId, (result: IHome) => {
        setIsLoading(false);
        if(result && isNN(result._id)){
          setFormData({
            ...formData,
            id: result._id,
            blockId: result._blockId,
            number: result.number,
            address: result.address,
            bedRoom: result.bedRoom,
            parking: result.parking,
            description: result.description,
            members: result.memberIds
          });
        }else{
          Toast.fail('Failed to load house.');
        }
      }, (message: string) => {
        setIsLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load house.');
      });
    }
    
    /*
    if(homeId > 0 && (props.homes.isUpToDate && !props.homes.isError && isNN(props.homes.data))){
      let homes = props.homes.data.filter((u: IHome, i: number) => u._id === homeId);
      if(isNNObject(homes) && homes.length > 0){
        let members = [] as Array<string>;
        if(props.userHomes.isUpToDate && !props.userHomes.isError && isNN(props.userHomes.data)){
          props.userHomes.data.filter((uh: IUserHome, i: number) => uh.homeId === homeId).map(uh => {
            members.push(uh.userId);
          });
        }
        setState({
          id: homes[0]._id,
          blockId: homes[0]._blockId,
          number: homes[0].number,
          address: homes[0].address,
          bedRoom: homes[0].bedRoom,
          parking: homes[0].parking,
          description: homes[0].description,
          members: members
        });
      }
    }
    */
  }, []);

  const getUsers = () => {
    let users = '';
    if(isNN(formData.members) && (props.users.isUpToDate && !props.users.isError && isNN(props.users.data))){
      props.users.data.filter(d => formData.members.indexOf(d._id) >= 0).map((u: IMember, i: number) => 
        users += (users.length > 0 ? ', ' : '') + u.name + (isNN(u.number) ? '(' + u.number + ')' : '')
      );
    }
    return users;
  };

  const onSave = () => {
    if(isNN(formData.blockId) && isNN(formData.number)){
      setIsLoading(true);
      /*
      props.onCheckNumberAvail(state.id, state.number, (result: boolean) => {
        if(result === true){
          */
         let home = {
           _id: formData.id,
           _blockId: formData.blockId,
           number: formData.number,
           address: formData.address,
           bedRoom: formData.bedRoom,
           parking: formData.parking,
           description: formData.description,
           memberIds: formData.members
         } as IHome;
          props.onSave(home, props.user, props.homes, (result: boolean, message: string) => {
            setIsLoading(false);
            if(result === true){
              setFormData({
                id: '',
                blockId: '',
                number: '',
                address: '',
                bedRoom: 1,
                parking: '',
                description: '',
                members: []
              });
              Toast.success('House saved successfully.');
            }else{
              Toast.fail(isNN(message) ? message : 'Failed to save house.');
            }
          });
          /*
        }else{
          setState({
            isLoading: false,
          });
          Toast.warn('House with same Number already available. Please change the house number.');
        }
      });
      */
    }else{
      Toast.warn('Please fill all the required fields before '+ (formData.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onToggleUserModel = (isShow: boolean) => {
    setIsShowUsers(isShow);
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Houses', link: 'homes'}, {title: 'Edit House', link: 'edit-home/0'}]} />
        <View className='bgc-white p-20'>
        <View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Picker
            className='form-control'
            value={formData.blockId}
            onChange={(event: any) => setFormData({...formData, blockId: event.target.value})}>
              <option key={'block0'} value=''>Select Block</option>
              {(props.blocks && props.blocks.isUpToDate && !props.blocks.isError && isNN(props.blocks.data)) &&
                props.blocks.data.map((d: IBlock, i: number) => {
                  return(<option key={'block' + i} value={d._id}>{d.name}</option>);
                })
              }
            </Picker>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Number" className='form-control' value={formData.number} onChange={(event: any) => setFormData({...formData, number: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextArea placeholder="Address" className='form-control' value={formData.address} onChange={(event: any) => setFormData({...formData, address: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text>No of bed rooms: </Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={formData.bedRoom === 1} text='1' onPress={() => setFormData({...formData, bedRoom: 1})} />
            <RadioButton isActive={formData.bedRoom === 2} text='2' onPress={() => setFormData({...formData, bedRoom: 2})} />
            <RadioButton isActive={formData.bedRoom === 3} text='3' onPress={() => setFormData({...formData, bedRoom: 3})} />
            <RadioButton isActive={formData.bedRoom === 4} text='4' onPress={() => setFormData({...formData, bedRoom: 4})} />
            <RadioButton isActive={formData.bedRoom === 5} text='5' onPress={() => setFormData({...formData, bedRoom: 5})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Parking" className='form-control' value={formData.parking} onChange={(event: any) => setFormData({...formData, parking: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Description" className='form-control' value={formData.description} onChange={(event: any) => setFormData({...formData, description: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mR-5'>User(s):</Text>
            <span className="icon-holder mR-10 cur-p" onClick={() => {
              setIsShowUsers(true);
             }}>
              <i className="c-blue-500 fa fa-pencil"></i>
            </span>
            <Text>{getUsers()}</Text>
          </View>
          {(props.users.isUpToDate && !props.users.isError && isNN(props.users.data)) &&
          <Modal
          open={isShowUsers}
          onClose={() => onToggleUserModel(false)}>
            <View className='mT-30 mB-25 mL-15 mR-15' style={{height: '80%'}}>
              <ScrollView className='bd bdc-blue-500' style={{height: '50%', maxHeight: '50%'}}>
              {
                props.users.data.map((d: IMember, i: number) => {
                  return(
                    <Text key={'user' + i} className={'cur-p bdB p-3 mB-1' + (formData.members.indexOf(d._id) >= 0 ? ' bgc-blue-500 c-white' : '')} onClick={() => {
                      if(formData.members.indexOf(d._id) >= 0){
                        setFormData({
                          ...formData,
                          members: formData.members.filter(e => e !== d._id)
                        });
                      }else{
                        let tMembers = formData.members;
                        tMembers.push(d._id);
                        setFormData({
                          ...formData,
                          members: tMembers
                        });
                      }
                    }}>{d.name + (d.number ? '(' + d.number + ')' : '')}</Text>
                  )
                })
              }
              </ScrollView>
              </View>
          </Modal>
          }
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Button
            title="SAVE"
            onPress={onSave}
            />
          </View>
        </View>
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};