import * as React from 'react';
import { Link } from 'react-router-dom';

interface IProps {
  data: Array<{title: string, link: string}>
};

export default (props: IProps) => {
  return (
    <div className='mB-10'>
      <Link to=''>Home</Link>
      {props.data.map((d, i) => {
        return(<>
        <span key={'bd' + i} className='mL-10 mR-10'>/</span>
        {(i === props.data.length - 1) ?
        <span key={'bc' + i}>{d.title}</span>
        :
        <Link key={'bc' + i} to={'/' + d.link}>{d.title}</Link>
        }
        </>);
      })}
    </div>
  );
}