import * as React from 'react';
import { View } from '../../common/elements';
import { ROLE } from '../../../constants';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IBlocks } from '../../../models/store/blocks';
import Banner from '../../common/banner';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks
};

export default (props: IProps) => {
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item w-100">
          <View className="row gap-20">
            <Banner title='Ticket(s)' icon='ti-headphone-alt' color='c-orange-500' onClick={() => props.history.push('tickets')} />
            <Banner title='Visitor(s)' icon='fa fa-id-card-o' color='c-pink-500' onClick={() => props.history.push('all-visitors')} />
            <Banner title='ads' icon='fa fa-bullhorn' color='c-teal-500' onClick={() => props.history.push('ads')} />
            <Banner title='Notification' icon='ti-bell' color='c-red-500' onClick={() => props.history.push('notification')} />
          </View>
          <View className="row gap-20">
          <Banner title='Door Lock(s)' icon='ti-lock' color='c-indigo-800' onClick={() => props.history.push('outings')} />
            {props.appUser.role === ROLE.ADMIN &&
            <>
              <Banner title='Block(s)' icon='fa fa-building' color='c-brown-500' bgColor='bgc-brown-50' hasCount={true} count={props.blocks.isUpToDate ? (!props.blocks.isError ? props.blocks.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('blocks')} />
              <Banner title='House(s)' icon='fa fa-home' color='c-blue-700' bgColor='bgc-blue-50' hasCount={true} count={props.homes.isUpToDate ? (!props.homes.isError ? props.homes.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('homes')} />
              <Banner title='User(s)' icon='fa fa-users' color='c-deep-purple-500' bgColor='bgc-deep-purple-50' hasCount={true} count={props.users.isUpToDate ? (!props.users.isError ? props.users.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('users')} />
            </>
            }
            {(props.appUser.role !== ROLE.ADMIN && props.user.client.config.hasPayment) &&
            <Banner title='Payment' icon='fa fa-credit-card' color='c-indigo-500' onClick={() => props.history.push('payment')} />
            }
          </View>
          {(props.appUser.role === ROLE.ADMIN && props.user.client.config.hasPayment) &&
          <View className="row gap-20">
            <Banner title='Payment' icon='fa fa-credit-card' color='c-indigo-500' onClick={() => props.history.push('payment')} />
          </View>
          }
        </View>
      </View>
    </View>
  );
};