import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import {  View, Text, TextInput, TextArea } from '../common/elements';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { IServant, ITakeaway } from '../../models/home/servant';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';
import Button from '../common/button';
import { toDateTimeString } from '../../util/convert';

interface IProps {
  history: any,
  user: IUser,
  homeIndex: number,
  onLoad: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onSave: (homeId: string, data: IServant, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, sCallback: (result: boolean) => void) => void,
  onCreateTakeaway: (id: string, data: ITakeaway, sCallback:(result: string) => void, fCallback:(message: string) => void) => void,
  onRemoveTakeaway: (id: string, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [state, setState] = useState({
    data: [] as Array<IServant>,
    id: '',
    name: '',
    age: 0,
    gender: '',
    mobileNo: '',
    emailId: '',
    viewId: '',
    takeaways: [] as Array<ITakeaway>,
    takeawayTitle: '',
    takeawayDesc: '',
    editTakeawayId: '',
    editTakeawayTitle: '',
    editTakeawayDesc: ''
  });

  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.homes[props.homeIndex]._id, (result: Array<IServant>) => {
      setIsLoading(false);
      setState({
        ...state,
        data: result
      });
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  }, []);
  
  const onSave = () => {
    if(isNN(state.name) && state.age > 0 && isNN(state.gender)){
      setIsLoading(true);
        let servant = {
          _id: state.id,
          name: state.name,
          age: state.age,
          gender: state.gender,
          mobileNo: state.mobileNo,
          emailId: state.emailId ? state.emailId.toLowerCase() : ''
        } as IServant;
        props.onSave(props.user.homes[props.homeIndex]._id, servant, (result: boolean) => {
          setIsLoading(false);
          if(result === true){
            let tData = state.data;
            var foundIndex = tData.findIndex(d => d._id === state.id);
            tData[foundIndex].name = state.name;
            tData[foundIndex].age = state.age;
            tData[foundIndex].gender = state.gender;
            tData[foundIndex].mobileNo = state.mobileNo;
            tData[foundIndex].emailId = state.emailId;
            setIsEdit(false);
            setState({
              ...state,
              data: tData,
              id: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              viewId: '',
              takeaways: [],
              takeawayTitle: '',
              takeawayDesc: '',
              editTakeawayId: '',
              editTakeawayTitle: '',
              editTakeawayDesc: ''
            });
            Toast.success('Servant saved successfully.');
          }else{
            Toast.fail('Failed to save servant.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                setState({
                  ...state,
                  data: state.data.filter((d, i) => d._id !== id),
                  viewId: '',
                  takeaways: [],
                  takeawayTitle: '',
                  takeawayDesc: '',
                  editTakeawayId: '',
                  editTakeawayTitle: '',
                  editTakeawayDesc: ''
                });
                Toast.success('Servant removed successfully.');
              }else{
                Toast.fail('Failed to remove servant.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };
  
  const onView = (id: string, takeaways: Array<ITakeaway>) => {
    if(state.viewId === id){
      setState({
        ...state,
        viewId: '',
        takeaways: [],
        takeawayTitle: '',
        takeawayDesc: '',
        editTakeawayId: '',
        editTakeawayTitle: '',
        editTakeawayDesc: ''
      });
    }else{
      setState({
        ...state,
        viewId: id,
        takeaways: takeaways,
        takeawayTitle: '',
        takeawayDesc: '',
        editTakeawayId: '',
        editTakeawayTitle: '',
        editTakeawayDesc: ''
      });
    }
  };

  const onCreateTakeaway = (id: string) => {
    if(isNN(state.takeawayTitle) && isNN(state.takeawayDesc)){
      setIsLoading(true);
      props.onCreateTakeaway(id, {
        _id: '',
        title: state.takeawayTitle,
        description: state.takeawayDesc,
        createdBy: props.user.name,
        createdAt: new Date(),
        updatedBy: props.user._id,
        updatedAt: new Date()
      } as ITakeaway, (result: string) => {
        setIsLoading(false);
        let tData = state.data;
        var foundIndex = tData.findIndex(d => d._id === id);
        tData[foundIndex].takeaways.unshift({
          _id: result,
          title: state.takeawayTitle,
          description: state.takeawayDesc,
          createdAt: new Date(),
          createdBy: props.user.name,
          updatedBy: props.user._id,
          updatedAt: new Date()
        } as ITakeaway);
        setState({
          ...state,
          data: tData,
          takeawayTitle: '',
          takeawayDesc: ''
        });
        Toast.success('Takeayway added successfully.');
      }, (message: string) => {
        setIsLoading(false);
        Toast.fail('Failed to add takeaway.');
      });
    }else{
      Toast.warn('Please fill Takeaway title & description before Add.');
    }
  };

  const onUpdateTakeaway = (id: string) => {
    if(isNN(state.editTakeawayTitle) && isNN(state.editTakeawayDesc)){
      setIsLoading(true);
      props.onCreateTakeaway(id, {
        _id: state.editTakeawayId,
        title: state.editTakeawayTitle,
        description: state.editTakeawayDesc,
        createdBy: props.user.name,
        createdAt: new Date(),
        updatedBy: props.user._id,
        updatedAt: new Date()
      } as ITakeaway, (result: string) => {
        setIsLoading(false);
        let tData = state.data;
        var foundIndex = tData.findIndex(d => d._id === id);        
        var takeawayIndex = tData[foundIndex].takeaways.findIndex(d => d._id === state.editTakeawayId);
        tData[foundIndex].takeaways[takeawayIndex].title = state.editTakeawayTitle;
        tData[foundIndex].takeaways[takeawayIndex].description = state.editTakeawayDesc;
        tData[foundIndex].takeaways[takeawayIndex].updatedBy = props.user.name;
        tData[foundIndex].takeaways[takeawayIndex].updatedAt = new Date();
        setState({
          ...state,
          data: tData,
          editTakeawayId: '',
          editTakeawayTitle: '',
          editTakeawayDesc: ''
        });
        Toast.success('Takeayway updated successfully.');
      }, (message: string) => {
        setIsLoading(false);
        Toast.fail('Failed to update takeaway.');
      });
    }else{
      Toast.warn('Please fill Takeaway title & description before Add.');
    }
  };

  const onRemoveTakeaway = (id: string, servantId: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete this takeaway?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemoveTakeaway(id, (result: boolean) => {
              setIsLoading(false);
              let tData = state.data;
              var foundIndex = tData.findIndex(d => d._id === servantId);        
              tData[foundIndex].takeaways = tData[foundIndex].takeaways.filter((d, i) => d._id !== id);
              if(result === true){
                setState({
                  ...state,
                  data: tData,
                  editTakeawayId: '',
                  editTakeawayTitle: '',
                  editTakeawayDesc: '',
                  viewId: ''
                });
                Toast.success('Takeaway removed successfully.');
              }else{
                Toast.fail('Failed to remove takeaway.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Servants', link: 'servants'}]} />
        <View className='bgc-white p-20'>
        <View>
          <Text className='fl-r cur-p c-blue-500' onClick={() => props.history.push('new-servant')}>Add New</Text>
        </View>
        {isNN(state.data) ?
          state.data.map((d: IServant, i: number) => {
            return(
              (isEdit === true && state.id === d._id) ?
              <View key={i} className='bdB mT-5'>
                <View className='mT-1 mB-3 p-2'>
                <TextInput type='text' placeholder="Name" className='form-control' value={state.name} onChange={(event: any) => setState({...state, name: event.target.value})} />
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} value={state.age > 0 ? state.age + '' : ''} onChange={(event: any) => setState({...state, age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <RadioButton isActive={state.gender === 'M'} text='Male' onPress={() => setState({...state, gender: 'M'})} />
                  <RadioButton isActive={state.gender === 'F'} text='Female' onPress={() => setState({...state, gender: 'F'})} />
                  {props.user.client.config.hasOtherGender &&
                  <RadioButton isActive={state.gender === 'O'} text='Other' onPress={() => setState({...state, gender: 'O'})} />
                  }
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <TextInput type='text' placeholder="Mobile Number" className='form-control' value={state.mobileNo} onChange={(event: any) => setState({...state, mobileNo: event.target.value})} />
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <TextInput type='text' placeholder="Email Id" className='form-control' value={state.emailId} onChange={(event: any) => setState({...state, emailId: event.target.value})} />
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <span className="icon-holder mR-10 cur-p" onClick={onSave}>
					          <i className="c-green-500 fa fa-check"></i>
				          </span>
				          <span className="icon-holder cur-p" onClick={() => {
                    setIsEdit(false);
                    setState({
                      ...state,
                      id: '',
                      name: '',
                      age: 0,
                      gender: '',
                      mobileNo: '',
                      emailId: '',
                      viewId: '',
                      takeaways: [],
                      takeawayTitle: '',
                      takeawayDesc: '',
                      editTakeawayId: '',
                      editTakeawayTitle: '',
                      editTakeawayDesc: ''
                    });
                  }}>
					          <i className="c-red-500 fa fa-times"></i>
				          </span>
                </View>
              </View>
              :
              <View key={i} className='bdB mT-5'>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.name}</Text>
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
                </View>
                {isNN(d.mobileNo) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.emailId) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.emailId}</Text>
                </View>
                }
                <View className='mT-1 mB-3 p-2'>
                  <span className="icon-holder mR-10 cur-p" onClick={() => {
                    setIsEdit(true);
                    setState({
                      ...state,
                      id: d._id,
                      name: d.name,
                      age: d.age,
                      gender: d.gender,
                      mobileNo: d.mobileNo,
                      emailId: d.emailId,
                      viewId: '',
                      takeaways: [],
                      takeawayTitle: '',
                      takeawayDesc: '',
                      editTakeawayId: '',
                      editTakeawayTitle: '',
                      editTakeawayDesc: ''
                    });
                  }}>
                    <i className="c-blue-500 fa fa-pencil"></i>
                  </span>
                  <span className="icon-holder mR-25 cur-p" onClick={() => {
                    onRemove(d._id);
                  }}>
                    <i className="c-red-500 fa fa-trash"></i>
                  </span>
                  <span onClick={() => {
                    onView(d._id, d.takeaways);
                  }}>
                    <Text className='cur-p c-blue-500'>{(state.viewId === d._id ? 'Hide' : 'View') + ' Takeaways'}</Text>
                  </span>   
                </View>
                {state.viewId === d._id &&
                <View className='mT-5 mB-5'>
                  <View>
                    <Text className='fw-600'>Takeaways</Text>
                  </View>
                  <View className='p-15'>
                    <View className='mT-5 mB-5'>
                      <TextInput type='text' placeholder='Takeaway Title' className='form-control mB-10' value={state.takeawayTitle} onChange={(event: any) => setState({...state, takeawayTitle: event.target.value})} />
                      <TextArea placeholder='Takeaway Description' className='form-control bd' value={state.takeawayDesc} onChange={(event: any) => setState({...state, takeawayDesc: event.target.value})} />
                      <View className='mT-5'>
                        <Button
                          title='ADD NEW TAKEAWAY'
                          onPress={() => onCreateTakeaway(d._id)}
                          />
                      </View>
                    </View>
                    {isNN(state.takeaways) ?
                      state.takeaways.map((e: ITakeaway, j: number) => {
                        return(
                        <View key={'tkycmt' + j} className={'pL-3 pR-3 pT-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                          <View>
                            <Text className='fw-600'>{e.createdBy + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                          </View>
                          <View>
                            {
                              (state.editTakeawayId === e._id && !e.isExit) ?
                              <TextInput type='text' placeholder='Takeaway Title' className='form-control mB-10' value={state.editTakeawayTitle} onChange={(event: any) => setState({...state, editTakeawayTitle: event.target.value})} />
                              :
                              <Text>{e.title}</Text>
                            }
                          </View>
                          <View>
                            {
                              (state.editTakeawayId === e._id && !e.isExit) ?
                              <TextArea placeholder='Takeaway Description' className='form-control bd' value={state.editTakeawayDesc} onChange={(event: any) => setState({...state, editTakeawayDesc: event.target.value})} />
                              :
                              <Text>{e.description}</Text>
                            }
                          </View>
                          {
                            e.isExit ?
                          <View>
                            <Text className='c-green-600'>{'Exit by ' + e.exitBy + ' in Gate ' + e.exitGateNo + ' on ' + toDateTimeString(e.exitAt)}</Text>
                          </View>
                          :
                          <View>
                            <Text className='c-red-600'>Still not exit.</Text>
                          </View>
                          }
                          {
                            !e.isExit &&
                            <>
                              {
                              state.editTakeawayId === e._id ?
                              <View className='mT-1 mB-3 p-2'>
                                <span className="icon-holder mR-10 cur-p" onClick={() => onUpdateTakeaway(d._id)}>
                                  <i className="c-green-500 fa fa-check"></i>
                                </span>
                                <span className="icon-holder cur-p" onClick={() => {
                                  setState({
                                    ...state,
                                    editTakeawayId: '',
                                    editTakeawayTitle: '',
                                    editTakeawayDesc: ''
                                  });
                                }}>
                                  <i className="c-red-500 fa fa-times"></i>
                                </span>
                              </View>
                              :
                              <View className='mT-1 mB-3 p-2'>
                                <span className="icon-holder mR-10 cur-p" onClick={() => {
                                  setState({
                                    ...state,
                                    editTakeawayId: e._id,
                                    editTakeawayTitle: e.title,
                                    editTakeawayDesc: e.description
                                  });
                                }}>
                                  <i className="c-blue-500 fa fa-pencil"></i>
                                  </span>
                                  <span className="icon-holder mR-25 cur-p" onClick={() => {
                                    onRemoveTakeaway(e._id, d._id);
                                  }}>
                                    <i className="c-red-500 fa fa-trash"></i>
                                  </span>
                              </View>
                              }
                            </>
                          }
                        </View>
                        )
                      })
                      :
                      <Text className='p-20'>No takeaway found.</Text>
                    }
                  </View>
                </View>
                }
              </View>
            )
          })
          :
          <Text className='p-20'>No servant found.</Text>
        }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};