import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import {  View, Text, TextInput, TextArea, Picker } from '../common/elements';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { ITickets } from '../../models/store/tickets';
import { ITicket } from '../../models/home/ticket';
import { ITicketComment } from '../../models/home/ticket-comment';
import { isNN, isNNObject } from '../../util/index';
import { toDateTimeString } from '../../util/convert';
import { ROLE } from '../../constants';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser
  user: IUser,
  tickets: ITickets,
  onLoad: (appClientId: string, homeId: string, callback: (result: boolean) => void) => void,
  onSave: (data: ITicket, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onResolve: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onStart: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onAddComment: (ticketId: string, comment: ITicketComment, appUser: IAppUser, user: IUser, tickets: ITickets, callback: (result: boolean) => void) => void,
  onRemoveComment: (id: string, ticketId: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [state, setState] = useState({
    data: [] as Array<ITicket>,
    id: '',
    category: '',
    title: '',
    description: '',
    status: 'INIT',
    viewId: '',
    comments: [] as Array<ITicketComment>,
    comment: '',
    editCommentId: '',
    editComment: ''
  });

  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, (props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) ? '' : props.user.homes[props.appUser.homeIndex]._id, (result: boolean) => {
      setIsLoading(false);
      // setState({
      //   ...state,
      //   data: getTickets()
      // });
      if(!result){
        Toast.fail('Failed to load Tickets.');
      }
    });
    /*
    if(props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) {
      setState({
        isLoading: true
      });
      props.onLoadAll(props.user.client._id, (result: boolean) => {
        setState({
          isLoading: false,
          data: getTickets()
        });
        if(!result){
          Toast.fail('Failed to load Tickets.');
        }
      });
    }else{
      setState({
        isLoading: true
      });
      props.onLoadAllByHome(props.user, props.appUser.homeIndex, (result: boolean) => {
        setState({
          isLoading: false,
          data: getTickets()
        });
        if(!result){
          Toast.fail('Failed to load Tickets.');
        }
      });
    }
    */
  }, []);
  
  /*
  getTickets = () => {
    if(props.appUser.role === ROLE.USER) {
      return isNN(props.user.homes[props.appUser.homeIndex].tickets) ? props.user.homes[props.appUser.homeIndex].tickets : [];
    }else if(props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) {
      return (props.tickets.isUpToDate && !props.tickets.isError && isNN(props.tickets.data)) ? props.tickets.data : [];
    }else{
      return [];
    }
  };
  */

  const onSave = () => {
    if(isNN(state.category) && isNN(state.title) && isNN(state.description)){
      setIsLoading(true);
        let ticket = {
          _id: state.id,
          category: state.category,
          title: state.title,
          description: state.description,
          status: state.status
        } as ITicket;
        props.onSave(ticket, props.user, props.appUser.homeIndex, props.tickets, (result: boolean) => {          
          setIsLoading(false);
          if(result === true){
            setIsEdit(false);
            setState({
              ...state,
              id: '',
              category: '',
              title: '',
              description: '',
              status: 'INIT',
              viewId: '',
              comments: [],
              comment: '',
              editCommentId: '',
              editComment: ''
            });
            Toast.success('Ticket saved successfully.');
          }else{
            Toast.fail('Failed to save ticket.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.user, props.appUser.homeIndex, props.tickets, (result: boolean) => {
              setIsLoading(false);
              setState({
                ...state,
                viewId: '',
                comments: [],
                comment: '',
                editCommentId: '',
                editComment: ''
              });
              if(result === true){
                Toast.success('Ticket removed successfully.');
              }else{
                Toast.fail('Failed to remove ticket.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onResolve = (id: string) => {
    confirmAlert({
      title: 'Confirm to resolve ticket',
      message: 'Are you sure you wish to resolve this ticket?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onResolve(id, props.user, props.appUser.homeIndex, props.tickets, (result: boolean) => {
              setIsLoading(false);
              setState({
                ...state,
                viewId: '',
                comments: [],
                comment: '',
                editCommentId: '',
                editComment: ''
              });
              if(result === true){
                Toast.success('Ticket resolved successfully.');
              }else{
                Toast.fail('Failed to resolve ticket.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onStart = (id: string) => {
    confirmAlert({
      title: 'Confirm to start ticket',
      message: 'Are you sure you wish to start this ticket?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onStart(id, props.user, props.appUser.homeIndex, props.tickets, (result: boolean) => {
              setIsLoading(false);
              setState({
                ...state,
                viewId: '',
                comments: [],
                comment: '',
                editCommentId: '',
                editComment: ''
              });
              if(result === true){
                Toast.success('Ticket started successfully.');
              }else{
                Toast.fail('Failed to start ticket.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onView = (id: string, comments: Array<ITicketComment>) => {
    if(state.viewId === id){
      setState({
        ...state,
        viewId: '',
        comments: [],
        comment: '',
        editCommentId: '',
        editComment: ''
      });
    }else{
      setState({
        ...state,
        viewId: id,
        comments: comments,
        comment: '',
        editCommentId: '',
        editComment: ''
      });
    }
  };

  const onAddComment = (id: string) => {
    if(isNN(state.comment)){
      setIsLoading(true);
      props.onAddComment(id, {
        _id: '',
        comment: state.comment,
        createdBy: props.user._id,
        createdByName: props.user.name,
        createdAt: new Date(),
        updatedBy: props.user._id,
        updatedAt: new Date()
      } as ITicketComment, props.appUser, props.user, props.tickets, (result: boolean) => {
        setIsLoading(false);
        if(result === true){
          setState({
            ...state,
            comment: ''
          });
          Toast.success('Comment added successfully.');
        }else{
          Toast.fail('Failed to add comment.');
        }
      });
  }else{
    Toast.warn('Please fill comment before Add.');
  }
  };

  const onUpdateComment = (id: string) => {
    if(isNN(state.editCommentId) && isNN(state.editComment)){
      setIsLoading(true);
      props.onAddComment(id, {
        _id: state.editCommentId,
        comment: state.editComment,
        updatedBy: props.user._id,
        updatedAt: new Date()
      } as ITicketComment, props.appUser, props.user, props.tickets, (result: boolean) => {
        setIsLoading(false);
        if(result === true){
          setState({
            ...state,
            editCommentId: '',
            editComment: ''
          });
          Toast.success('Comment updated successfully.');
        }else{
          Toast.fail('Failed to update comment.');
        }
      });
  }else{
    Toast.warn('Please fill comment before update.');
  }
  };

  const onRemoveComment = (id: string, ticketId: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete this comment?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemoveComment(id, ticketId, props.user, props.appUser.homeIndex, props.tickets, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                setState({
                  ...state,
                  editCommentId: '',
                  editComment: '',
                  viewId: ''
                });
                Toast.success('Comment removed successfully.');
              }else{
                Toast.fail('Failed to remove comment.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Tickets', link: 'tickets'}]} />
        <View className='bgc-white p-20'>
        {
          props.appUser.role === ROLE.USER &&
          <View>
            <Text className='fl-r cur-p c-blue-500' onClick={() => props.history.push('new-ticket')}>Add New</Text>
          </View>
        }
        {(isNNObject(props.tickets) && props.tickets.isUpToDate && !props.tickets.isError && isNN(props.tickets.data)) ?
        props.tickets.data.map((d: ITicket, i: number) => {
            return(
              <View key={'tkt' + i} className='bdB mT-5'>
               <View className='fw-600 mT-1 mB-3 p-2'>
                  <Text className='fw-600'>{d.createdBy + ' from ' + d._homeId + ' on ' + toDateTimeString(d.createdAt)}</Text>
                </View>
                <View className='mT-1 mB-3 p-2'>
                  {(isEdit && state.id === d._id) ?
                    <Picker
                    className='form-control'
                    value={state.category}
                    onChange={(event: any) => setState({...state, category: event.target.value})}>
                      <option key={'ticketcat-1'} value=''>Select Ticket Category</option>
                      {isNN(props.user.client.config.ticketCategories) ?
                      props.user.client.config.ticketCategories.map((d: string, i: number) => {
                        return(<option key={'ticketcat' + i} value={d}>{d}</option>);
                      })
                      :
                      <option key={'ticketcat0'} value='Maintenance'>Maintenance</option>
                      }
                    </Picker>
                    :
                    <Text className='fw-600'>{d.category}</Text>
                  }
                </View>
                <View className='mT-1 mB-3 p-2'>
                  {(isEdit && state.id === d._id) ?
                    <TextInput type='text' placeholder='Title' className='form-control' value={state.title} onChange={(event: any) => setState({...state, title: event.target.value})} />
                    :
                    <Text>{d.title}</Text>
                  }
                </View>
                <View className='mT-1 mB-3 p-2'>
                  {(isEdit && state.id === d._id) ?
                    <TextArea placeholder='Description' className='form-control' value={state.description} onChange={(event: any) => setState({...state, description: event.target.value})} />
                    :
                    <Text>{d.description}</Text>
                  }
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{d.status}</Text>
                </View>
                {(isEdit && state.id === d._id) ?
                <View className='mT-1 mB-3 p-2'>
                  <span className="icon-holder mR-10 cur-p" onClick={onSave}>
                    <i className="c-green-500 fa fa-check"></i>
                  </span>
                  <span className="icon-holder cur-p" onClick={() => {
                    setIsEdit(false);
                    setState({
                      ...state, 
                      id: '',
                      category: '',
                      title: '',
                      description: '',
                      status: 'INIT',
                      viewId: '',
                      comments: [],
                      comment: ''
                    });
                  }}>
                    <i className="c-red-500 fa fa-times"></i>
                  </span>
                </View>
                :
                <View className='mT-1 mB-3 p-2'>
                {props.appUser.role === ROLE.USER &&
                <span className="icon-holder mR-10 cur-p" onClick={() => {
                  setIsEdit(true);
                  setState({
                    ...state, 
                    id: d._id,
                    category: d.category,
                    title: d.title,
                    description: d.description,
                    status: 'INIT',
                    viewId: '',
                    comments: [],
                    comment: ''
                  });
                }}>
                  <i className="c-blue-500 fa fa-pencil"></i>
                </span>
                }
                {props.appUser.role === ROLE.USER &&
                <span className="icon-holder mR-25 cur-p" onClick={() => {
                  onRemove(d._id);
                }}>
                  <i className="c-red-500 fa fa-trash"></i>
                </span>
                }
                {props.appUser.role === ROLE.USER &&
                  <span className='m-25' onClick={() => {
                    onResolve(d._id);
                  }}>
                    <Text className='cur-p c-blue-500'>RESOLVE</Text>
                  </span>
                }
                {((props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) && d.status === 'INIT') &&
                <span className='mR-25' onClick={() => {
                  onStart(d._id);
                }}>
                  <Text className='cur-p c-blue-500'>START</Text>
                </span>
                }
                <span onClick={() => {
                  onView(d._id, d.comments);
                }}>
                  <Text className='cur-p c-blue-500'>{(state.viewId === d._id ? 'Hide' : 'View') + ' Comments'}</Text>
                </span>
                </View>
                }
                {state.viewId === d._id &&
                <View className='mT-5 mB-5'>
                  <View>
                    <Text className='fw-600'>Comments</Text>
                  </View>
                  <View className='p-15'>
                    <View className='mT-5 mB-5'>
                      <TextArea placeholder='Comment Text' className='form-control bd' value={state.comment} onChange={(event: any) => setState({...state, comment: event.target.value})} />
                      <View className='mT-5'>
                        <Button
                          title='ADD COMMENT'
                          onPress={() => onAddComment(d._id)}
                          />
                      </View>
                    </View>
                    {isNN(state.comments) ?
                      state.comments.map((e, j) => {
                        return(
                        <View key={'tktcmt' + j} className={'pL-3 pR-3 pt-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                          <View>
                            <Text className='fw-600'>{e.createdByName + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                          </View>
                          <View>
                            {
                              state.editCommentId === e._id ?
                              <TextArea placeholder='Comment Text' className='form-control bd' value={state.editComment} onChange={(event: any) => setState({...state, editComment: event.target.value})} />
                              :
                              <Text>{e.comment}</Text>
                            }
                          </View>
                          {
                            e.createdBy === props.user._id &&
                            <>
                              {
                              state.editCommentId === e._id ?
                              <View className='mT-1 mB-3 p-2'>
                                <span className="icon-holder mR-10 cur-p" onClick={() => onUpdateComment(d._id)}>
                                  <i className="c-green-500 fa fa-check"></i>
                                </span>
                                <span className="icon-holder cur-p" onClick={() => {
                                  setState({
                                    ...state, 
                                    editCommentId: '',
                                    editComment: ''
                                  });
                                }}>
                                  <i className="c-red-500 fa fa-times"></i>
                                </span>
                              </View>
                              :
                              <View className='mT-1 mB-3 p-2'>
                                <span className="icon-holder mR-10 cur-p" onClick={() => {
                                  setState({
                                    ...state, 
                                    editCommentId: e._id,
                                    editComment: e.comment
                                  });
                                }}>
                                  <i className="c-blue-500 fa fa-pencil"></i>
                                </span>
                                <span className="icon-holder mR-25 cur-p" onClick={() => {
                                  onRemoveComment(e._id, d._id);
                                }}>
                                  <i className="c-red-500 fa fa-trash"></i>
                                </span>
                              </View>
                              }
                            </>
                          }
                        </View>
                        )
                      })
                      :
                      <Text className='p-20'>No comment found.</Text>
                    }
                  </View>
                </View>
                }
              </View>
            )
          })
          :
          <Text className='p-20'>{(props.tickets.isUpToDate ? (props.tickets.isError ? props.tickets.errorMessage : "No ticket found.") : "Loading...")}</Text>
        }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};