import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import {  View, Text, TextInput, TextArea } from '../../common/elements';
import Breadcrum from '../../common/breadcrum';
import Spinner from '../../common/spinner';
import { IAds } from '../../../models/store/ads';
import { IUser } from '../../../models/user/user';
import { IAd } from '../../../models/ad';
import { isNN } from '../../../util/index';
import { toDateString } from '../../../util/convert';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  ads: IAds,
  onLoad: (appClientId: string, callback: (result: boolean) => void) => void,
  onSave: (appClientId: string, data: IAd, user: IUser, ads: Array<IAd>, callback: (result: boolean) => void) => void,
  onRemove: (id: string, ads: Array<IAd>, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [id, setId] = useState('');
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  
  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, (result: boolean) => {
      setIsLoading(false);
      if(!result){
        Toast.fail('Failed to load Ads.');
      }
    });
  }, []);
  
  const onSave = () => {
    if(isNN(title) && isNN(description)){
      setIsLoading(true);
        let ad = {
          _id: id,
          title: title,
          description: description
        } as IAd;
        props.onSave(props.user.client._id, ad, props.user, props.ads.data, (result: boolean) => {
          setIsLoading(false);
          if(result === true){
            setIsEdit(false);
            setId('');
            setTitle('');
            setDescription('');
            Toast.success('Ad saved successfully.');
          }else{
            Toast.fail('Failed to save ad.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.ads.data, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                Toast.success('Ad removed successfully.');
              }else{
                Toast.fail('Failed to remove ad.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Ads', link: 'ads'}]} />
          <View className='bgc-white p-20'>
            <View>
              <Text className='fl-r cur-p mB-3 c-blue-500' onClick={() => props.history.push('new-ad')}>Add New</Text>
            </View>
            {(props.ads.isUpToDate && !props.ads.isError && isNN(props.ads.data)) ?
              props.ads.data.map((d: IAd, i: number) => {
                return(
                  (isEdit === true && id === d._id) ?
                  <View key={i} className='bdB mT-5'>
                    <View className='mT-1 mB-3 p-2'>
                        <Text className='fw-600'>{d.createdBy + " on " + toDateString(d.createdAt)}</Text>
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <TextInput type='text' placeholder="Title" className='form-control' value={title} onChange={(event: any) => setTitle(event.target.value)} />
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <TextArea placeholder="Description" className='form-control' value={description} onChange={(event: any) => setDescription(event.target.value)} />
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <span className="icon-holder mR-10 cur-p" onClick={onSave}>
                        <i className="c-green-500 fa fa-check"></i>
                      </span>
                      <span className="icon-holder cur-p" onClick={() => {
                        setIsEdit(false);
                        setId('');
                        setTitle('');
                        setDescription('');
                      }}>
                        <i className="c-red-500 fa fa-times"></i>
                      </span>
                    </View>
                  </View>
                  :
                  <View key={i} className='bdB mT-5'>
                    <View className='mT-1 mB-3 p-2'>
                        <Text className='fw-600'>{d.createdBy + " on " + toDateString(d.createdAt)}</Text>
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <Text>{d.title}</Text>
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <Text>{d.description}</Text>
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <span className="icon-holder mR-10 cur-p" onClick={() => {
                        setIsEdit(true);
                        setId(d._id);
                        setTitle(d.title);
                        setDescription(d.description);
                      }}>
                        <i className="c-blue-500 fa fa-pencil"></i>
                      </span>
                      <span className="icon-holder cur-p" onClick={() => {
                        onRemove(d._id);
                      }}>
                        <i className="c-red-500 fa fa-trash"></i>
                      </span>
                    </View>
                  </View>
                )
              })
              :
              <Text className='p-20'>{(props.ads.isUpToDate ? (props.ads.isError ? props.ads.errorMessage : "No ad found.") : "Loading...")}</Text>
            }
          <Spinner visible={isLoading} />
    </View>
        </View>
      </View>
    </View>
  );
};