import React, {useState} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View,  TextInput, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IBlocks } from '../../../models/store/blocks';
import { IBlock } from '../../../models/block';
import { isNN, onEnter } from '../../../util';
import Toast from  '../../../util/toast';
import { IHomes } from '../../../models/store/homes';
import { Link } from 'react-router-dom';

interface IProps {
  history: any,
  appUser: IAppUser
  user: IUser,
  blocks: IBlocks,
  homes: IHomes,
  onRemove: (id: string, blocks: Array<IBlock>, callback: (result: boolean) => void) => void,
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  // const [isEdit, setIsEdit] = useState(false);
  const [searchText, setSearchText] = useState('');
  const [searchResult, setSearchResult] = useState([] as Array<IBlock>);
  const [searchResultIndex, setSearchResultIndex] = useState(10);

  const onSearch = () => {
    if(isNN(searchText) && props.blocks.isUpToDate && !props.blocks.isError && isNN(props.blocks.data)){
      setSearchResult(props.blocks.data.filter(d => (d.name + '').toLocaleLowerCase().indexOf(searchText.toLowerCase()) >= 0));
      setSearchResultIndex(10);
    }else{
        setSearchResult([]);
        setSearchResultIndex(10);
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            let homes = (isNN(props.homes.data)) ? props.homes.data.filter(h => h._blockId === id) : [];
            if(isNN(homes)){
              let msg = '';
              homes.map(h => {
                msg += (msg.length > 0 ? ', ' : '') + h.number;
              });
              if(homes.length > 1){
                msg = 'Houses ' + msg + ' are assigned under this block. Please move these houses to other block and try to delete';
              }else{
                msg = 'House ' + msg + ' is assigned under this block. Please move this house to other block and try to delete.';
              }
              Toast.fail(msg);
            }else{
              setIsLoading(true);
              props.onRemove(id, props.blocks.data, (result: boolean) => {
                setIsLoading(false);
                if(result === true){
                  onSearch();
                  Toast.success('House removed successfully.');
                }else{
                  Toast.fail('Failed to remove house.');
                }
              });
            }
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Blocks', link: 'blocks'}]} />
        <View className='bgc-white p-20'>
        <View>
          <Text className='fl-r cur-p c-blue-500 mB-3' onClick={() => props.history.push('edit-block/0')}>Add New</Text>
        </View>
        <View className='mB-3 pB-5'>
          <TextInput type='text' placeholder="Search Block" className='form-control mR-10 d-i' style={{width: 'calc(100% - 40px)'}} value={searchText} onChange={(event: any) => setSearchText(event.target.value)}
          onKeyPress={(e) => {
            onEnter(e, onSearch);
          }} />
          <span className='icon-holder cur-p' onClick={onSearch}>
            <i className='ti-search pdd-right-10 fw-900'></i>
          </span> 
        </View>
        {isNN(searchResult) ?
        searchResult.slice(0, searchResultIndex).map((d: IBlock, i: number) => {
          return(
            <View key={'block' + i} className='bdB mT-5'>
              <View className='mT-1 mB-3 p-2'>
                <Text>{'Name: ' + d.name}</Text>         
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text>{'Description: ' + (isNN(d.description) ? d.description : '')}</Text>
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text className='mR-10'>Houses: </Text>
                {
                  (isNN(props.homes.data) && isNN(props.homes.data.filter(h => h._blockId === d._id))) ?
                  props.homes.data.filter(h => h._blockId === d._id).map((h, i) => {
                  return(<Link to={'edit-home/' + h._id} className='mR-10'>{h.number}</Link>)
                  })
                  :
                  <Text>No house assigned under this block.</Text>
                }
              </View>
              <View className='mT-1 mB-3 p-2'>
                <span className="icon-holder mR-10 cur-p" onClick={() => {
                  props.history.push('edit-block/' + d._id);////TODO
                  // props.navigation.navigate('EditBlock', {id: d._id});
                }}>
                  <i className="c-blue-500 fa fa-pencil"></i>
                </span>
                <span className="icon-holder cur-p" onClick={() => {
                    onRemove(d._id);
                  }}>
                  <i className="c-red-500 fa fa-trash"></i>
                </span>
              </View>
            </View>
          )
        })
        :
        <Text className='p-20'>No block match with your search.</Text>
      }
      {(searchResult.length > searchResultIndex) &&
      <View className='bdB mT-5'>
        <View className='p-10'>
          <Text className='fw-600 c-blue-500' onClick={() =>{
            setSearchResultIndex(searchResultIndex + 10);
          }}>Show More</Text>
        </View>
      </View>
      }
      <Spinner visible={isLoading} />
  </View>
  </View>
  </View>
  </View>
  );
};