import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user/index';
import { IOutings } from '../models/store/outings';
import { loadAll, retunBack } from '../store/actions/outing';
import Page from '../components/home/outings';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    data: state.Outings
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, callback: (result: boolean) => void) => dispatch(loadAll(appClientId, homeId, callback)),
    onReturnBack: (id: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => dispatch(retunBack(id, user, outings, callback))
});

export const Outings = connect(mapStateToProps, mapDispatchToProps)(Page);