import React, {useState, useEffect} from 'react';
import { Route, Switch } from 'react-router-dom';
import { View, Text } from '../common/elements';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import TitleBar from './title-bar';
import NavBar from './nav-bar';
import Footer from './footer';
import { ROLE } from '../../constants';
import { isNNObject, isNN } from '../../util';
import Toast from  '../../util/toast';

import { Dashboard as CDashboard} from '../../containers/dashboard';
import { Members as CMembers } from '../../containers/members';
import { NewMember as CNewMember } from '../../containers/new-member';
import { Vehicles as CVehicles } from '../../containers/vehicles';
import { NewVehicle as CNewVehicle } from '../../containers/new-vehicle';
import { Servants as CServants } from '../../containers/servants';
import { AllTakeaways as CAllTakeaways } from '../../containers/all-takeaways';
import { NewServant as CNewServant } from '../../containers/new-servant';
import { Tickets as CTickets } from '../../containers/tickets';
import { NewTicket as CNewTicket } from '../../containers/new-ticket';
import { Visitors as CVisitors } from '../../containers/visitors';
// import { NewVisitor as CNewVisitor } from '../../containers/new-visitor';
import { ChangePassword as CChangePassword } from '../../containers/change-password';
import { MyProfile as CMyProfile } from '../../containers/my-profile';
import { MyRole as CMyRole } from '../../containers/my-role';
import { Ads as CAds } from '../../containers/ads';
import { NewAd as CNewAd } from '../../containers/new-ad';
import { Users as CUsers } from '../../containers/users';
import { Homes as CHomes } from '../../containers/homes';
import { EditUser as CEditUser } from '../../containers/edit-user';
import { ApproveUsers as CApproveUsers } from '../../containers/approve-users';
import { EditHome as CEditHome } from '../../containers/edit-home';
import { AllVisitors as CAllVisitors } from '../../containers/all-visitors';
import { Notification as CNotification } from '../../containers/notification';
import { Outings as COutings } from '../../containers/outings';
import { NewOuting as CNewOuting } from '../../containers/new-outing';
import { MyDocuments as CMyDocuments } from '../../containers/my-documents';
import { Blocks as CBlocks } from '../../containers/blocks';
import { EditBlock as CEditBlock } from '../../containers/edit-block';
import { Payment as CPayment } from '../../containers/payment';
import { ViewHome as CViewHome } from '../../containers/view-home';
import { Notice as CNotice } from '../../containers/notice';
import { Help as CHelp } from '../../containers/help';
import NotFound from '../../components/not-found';

import initSideBar from '../../theme/scripts/sidebar/index';
import initScrollBar from '../../theme/scripts/scrollbar/index';
import initPopover from '../../theme/scripts/popover/index';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  getProfile: (callback: (result: boolean, message: string) => void) => void,
  onHomeChange: (index: number, callback: (result: boolean) => void) => void,
  onLogout: (callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  useEffect(() => {
    if(!isNNObject(props.user) || !isNN(props.user._id)){
      setIsLoading(true);
      props.getProfile( (result: boolean, message: string) => {
        setIsLoading(false);
        if(result !== true){
          Toast.fail('Failed to load Profile: ' + message);
        }
      });
    }
    // setTimeout(() => {
    //   initSideBar();
    //   initScrollBar();
    //   initPopover();
    // }, 1000);
  }, []);

  useEffect(() => {
    initSideBar();
    initScrollBar();
    initPopover();
  });
  
  const isUser = () => {
    return props.appUser.role === ROLE.USER;
  };

  const isAdmin = () => {
    return props.appUser.role === ROLE.ADMIN;
  };
  
  const isManager = () => {
    return props.appUser.role === ROLE.MANAGER;
  };

  const isGateKeeper = () => {
    return props.appUser.role === ROLE.GATEKEEPER;
  };

  return (
    !isLoading ?
      <View>
        <NavBar appUser={props.appUser} user={props.user} />
        <View className="page-container">
          <TitleBar appUser={props.appUser} user={props.user} onHomeChange={props.onHomeChange} onLogout={props.onLogout}/>
          <main className='main-content bgc-grey-100'>
            {((isNN(props.appUser.role) && !isUser()) || isNN(props.user.homes)) ?
            <Switch>
              <Route path={'/'} exact component={CDashboard} />
              {/* <Route path={'/login'} exact component={CLogin} /> */}
              {isUser() && <Route path={'/members'} exact component={CMembers} />}
              {isUser() && <Route path={'/new-member'} exact component={CNewMember} />}
              {isUser() && <Route path={'/vehicles'} exact component={CVehicles} />}
              {isUser() && <Route path={'/new-vehicle'} exact component={CNewVehicle} />}
              {isUser() && <Route path={'/servants'} exact component={CServants} />}
              {isUser() && <Route path={'/new-servant'} exact component={CNewServant} />}
              {(isUser() || isAdmin() || isManager()) && <Route path={'/tickets'} exact component={CTickets} />}
              {isUser() && <Route path={'/new-ticket'} exact component={CNewTicket} />}
              {isUser() && <Route path={'/visitors'} exact component={CVisitors} />}
              <Route path={'/outings'} exact component={COutings} />
              {isUser() && <Route path={'/new-outing'} exact component={CNewOuting} />}
              {/* {isUser() && <Route path={'/new-visitor'} exact component={CNewVisitor} />} */}
              {(isAdmin() || isManager()) && <Route path={'/ads'} exact component={CAds} />}
              {(isAdmin() || isManager()) && <Route path={'/new-ad'} exact component={CNewAd} />}
              {(isAdmin() || isManager()) && <Route path={'/notification'} exact component={CNotification} />}
              {isAdmin() && <Route path={'/users'} exact component={CUsers} />}
              {isAdmin() && <Route path={'/edit-user/:id/:isApprove'} exact component={CEditUser} />}
              {isAdmin() && <Route path={'/approve-users'} exact component={CApproveUsers} />}
              {isAdmin() && <Route path={'/homes'} exact component={CHomes} />}
              {isAdmin() && <Route path={'/view-home/:id'} exact component={CViewHome} />}
              {isAdmin() && <Route path={'/edit-home/:id'} exact component={CEditHome} />}
              {isAdmin() && <Route path={'/blocks'} exact component={CBlocks} />}
              {isAdmin() && <Route path={'/edit-block/:id'} exact component={CEditBlock} />}
              {(isAdmin() || isManager() && props.user.client.config.hasPayment) && <Route path={'/payment'} exact component={CPayment} />}
              {(isAdmin() || isManager() || isGateKeeper()) && <Route path={'/all-visitors'} exact component={CAllVisitors} />}
              {(isAdmin() || isManager() || isGateKeeper()) && <Route path={'/all-takeways'} exact component={CAllTakeaways} />}
              <Route path={'/notice'} exact component={CNotice} />
              <Route path={'/my-profile'} exact component={CMyProfile} />
              {props.user.role.indexOf(',') > 0 && <Route path={'/my-role'} exact component={CMyRole} />}
              <Route path={'/my-documents'} exact component={CMyDocuments} />
              <Route path={'/change-password'} exact component={CChangePassword} />
              <Route path={'/info'} exact component={CHelp} />              
              <Route component={NotFound} />
            </Switch>
            :
            <View id='mainContent'>
              <Text className='m-20'>You are not authorized to view this content. Please contact administrator/manager.</Text>
            </View>
            }
          </main>
          <Footer />
        </View>
      </View>
      :
      <Spinner visible={isLoading} text={"Loading..."} />
  );
};