import React, {useState, useEffect} from 'react';
import { View } from '../common/elements';
import Breadcrum from '../common/breadcrum';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IAds } from '../../models/store/ads';
import { IPolls } from '../../models/store/polls';
import { IPoll, IVote } from '../../models/poll/index';
import { IComplaint, IRemainder } from '../../models/home/index';
import AMPoll from './manager/poll';
import UserPoll from './poll';
import CustomList from './custom-list';
import { ROLE } from '../../constants';
import { isNNObject } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  ads: IAds,
  polls: IPolls,
  remainders: Array<IRemainder>,
  complaints: Array<IComplaint>,
  onLoadAds: (appClientId: string, callback: (result: boolean) => void) => void,
  onLoadPoll: (appClientId: string, userRole: string, callback: (result: boolean) => void) => void,
  onSavePoll: (appClientId: string, data: IPoll, polls: Array<IPoll>, user: IUser, sCallback: (result: boolean) => void) => void,
  onRemovePoll: (id: string, polls: Array<IPoll>, userId: string, sCallback: (result: boolean) => void) => void,
  onVote: (pollId: string, votes: Array<IVote>, polls: IPolls, user: IUser, appUser: IAppUser, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [activeTab, setActiveTab] = useState((isNNObject(props.user) && (props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER)) ? 1 : 2);
  const [isShowPoll, setIsShowPoll] = useState(isNNObject(props.user) && (props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER || (props.appUser.role === ROLE.USER && props.appUser.homeIndex >= 0)));
  
  useEffect(() => {
    props.onLoadAds(props.user.client._id, (result: boolean) => {
      if(!result){
        Toast.fail('Failed to load Ads.');
      }
    });
    if(isShowPoll){
      props.onLoadPoll(props.user.client._id, props.appUser.role, (result: boolean) => {
        if(!result){
          Toast.fail('Failed to load Poll.');
        }
      });
    }
  }, []);
  
    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Notice', link: 'notice'}]} />
        <View className='bgc-white p-20'>
          <View className='tab-bar'>
          {isShowPoll &&
            <span className={'tab' + (activeTab === 1 ? ' active': '')} onClick={() => setActiveTab(1)}>
              POLL
            </span>
          }
            <span className={'tab' + (activeTab === 2 ? ' active': '')} onClick={() => setActiveTab(2)}>
              ADS
            </span>
            {/* <span className={'tab' + (activeTab === 3 ? ' active': '')} onClick={() => setActiveTab(3)}>
              REMINDERS
            </span>
            <span className={'tab' + (activeTab === 4 ? ' active': '')} onClick={() => setActiveTab(4)}>
            COMPLAINTS
            </span> */}
          </View>
          <View className='p-15'>
            {
              activeTab === 1 ? 
                (props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) ? 
                  <AMPoll appUser={props.appUser} user={props.user} polls={props.polls} onSave={props.onSavePoll} onRemove={props.onRemovePoll} />
                :
                  <UserPoll appUser={props.appUser} user={props.user} polls={props.polls} onVote={props.onVote} />
              :
                activeTab === 2 ?
                  <CustomList data={(props.ads.isUpToDate ? props.ads.data : [])} message={(props.ads.isUpToDate ? (props.ads.isError ? props.ads.errorMessage : "No data found.") : "Loading...")} />
                :
                  <CustomList data={activeTab === 4 ? props.remainders : props.complaints} message="No data found." />
            }            
          </View>
        </View>        
      </View>
      </View>
      </View>
    );
};