import React, {useState} from 'react';
import Modal from 'react-responsive-modal';
import { confirmAlert } from 'react-confirm-alert';
import DatePicker from 'react-date-picker';
import { View, Text, TextInput, TextArea } from '../../common/elements';
import Button from '../../common/button';
import Checkbox from '../../common/checkbox';
import Spinner from '../../common/spinner';
import { IPolls } from '../../../models/store/polls';
import { IPoll } from '../../../models/poll/index';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { toDateString } from '../../../util/convert';
import { isNN, isNNObject } from '../../../util/index';
import { percentage } from '../../../util/calc';
import Toast from  '../../../util/toast';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  polls: IPolls,
  onSave: (appClientId: string, data: IPoll, polls: Array<IPoll>, user: IUser, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, polls: Array<IPoll>, userID: string, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [state, setState] = useState({
    isEdit: false,
    isLoading: false,
    id: '',
    topic: '',
    description: '',
    isAllowMultiple: false,
    isOneVotePerFlat: false,
    isAnonymous: false,
    fromDate: new Date(),
    toDate: new Date(),
    pollOption: [] as Array<string>,
    showVotePoll: { _id: '' } as IPoll,
    datePickerType: '',
    datePickerDate: new Date()
  });
  
  const onSave = () => {
    if(isNN(state.topic) && isNN(state.description) && isNNObject(state.fromDate) && isNNObject(state.toDate) && isNN(state.pollOption.filter(po => isNN(po)))){
        setState({
          ...state,
          isLoading: true
        });
        let poll = {
          _id: state.id,
          topic: state.topic,
          description: state.description,
          isAllowMultiple: state.isAllowMultiple,
          isOneVotePerFlat: state.isOneVotePerFlat,
          isAnonymous: state.isAnonymous,
          fromDate: state.fromDate,
          toDate: state.toDate,
          options: state.pollOption.filter(po => isNN(po))
        } as IPoll;
        props.onSave(props.user.client._id, poll, props.polls.data, props.user, (result: boolean) => {
          setState({
            ...state,
            isLoading: false,
          });
          if(result === true){
            setState({
              ...state,
              isEdit: false,
              id: '',
              topic: '',
              description: '',
              isAllowMultiple: false,
              isOneVotePerFlat: false,
              isAnonymous: false,
              fromDate: new Date(),
              toDate: new Date(),
              pollOption: [],
              showVotePoll: { _id: '' } as IPoll,
              datePickerType: '',
              datePickerDate: new Date()
            });
            Toast.success('Poll saved successfully.');
          }else{
            Toast.fail('Failed to save pol.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setState({
              ...state,
              isLoading: true
            });
            props.onRemove(id, props.polls.data, props.user._id, (result: boolean) => {
              setState({
                ...state,
                isLoading: false,
              });
              if(result === true){
                Toast.success('Poll removed successfully.');
              }else{
                Toast.fail('Failed to remove poll.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const loadVotes = () => {
    if(isNNObject(state.showVotePoll) && state.showVotePoll._id.length > 0){
      let votes = [] as any;
      state.showVotePoll.options.map((po: string, poi: number) => 
        votes.push({
          option: po,
          votes: state.showVotePoll.votes.filter(v => v.option === poi).length
        })
      );
      return(
        <View className='p-15'>
          <View className='mT-1 mB-3 p-2'>
            <Text className='fw-600'>{state.showVotePoll.topic}</Text>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text>{state.showVotePoll.description}</Text>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text className='fw-600'>{'Votes: ' + state.showVotePoll.votes.length}</Text>
          </View>
        {
        votes.sort(function(a: any, b: any){return b.votes - a.votes}).map((vo: any, voi: number) => {
          return(
            <View key={'pvo' + voi} className='mT-1 mB-3 p-2'>
              <Text>
                {vo.votes + ' (' + percentage(vo.votes, state.showVotePoll.votes.length) +  '%) - ' + vo.option}
              </Text>
            </View>
          )
        })
        }
        </View>
      )
    }else{
      return "";
    }
  };

    return(
      <View>
        {state.isEdit === false &&
        <View className='bdB mT-5 pB-5'>
          <Button
          title="ADD NEW"
          onPress={() => {
            setState({
              ...state,
              isEdit: true,
              id: '',
              topic: '',
              description: '',
              isAllowMultiple: false,
              isOneVotePerFlat: false,
              isAnonymous: false, 
              fromDate: new Date(),
              toDate: new Date(),
              pollOption: [],
              showVotePoll: { _id: '' } as IPoll,
              datePickerType: '',
              datePickerDate: new Date()
             });
          }}
          />
        </View>
        }
        {
         (state.isEdit === true && state.id.length === 0)  ?
         <View key={"p0"} className='bdB mT-5'>
          <View className='mT-1 mB-3 p-2'>
            <TextInput type='text' placeholder="Topic" className='form-control' value={state.topic} onChange={(event: any) => setState({...state, topic: event.target.value})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <TextArea placeholder="Description" className='form-control' value={state.description} onChange={(event: any) => setState({...state, description: event.target.value})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <DatePicker
            onChange={(date : any) => {
              setState({
                ...state,
                fromDate: date
              });
            }}
            value={state.fromDate}
            />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <DatePicker
            onChange={(date : any) => {
              setState({
                ...state,
                toDate: date
              });
            }}
            value={state.toDate}
            />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Checkbox isActive={state.isAllowMultiple} text='Allow Multiple Votes' onPress={() => setState({...state, isAllowMultiple: !state.isAllowMultiple})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Checkbox isActive={state.isOneVotePerFlat} text='One Vote per Flat' onPress={() => setState({...state, isOneVotePerFlat: !state.isOneVotePerFlat})} />        
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Checkbox isActive={state.isAnonymous} text='Anonymous Voting' onPress={() => setState({...state, isAnonymous: !state.isAnonymous})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text>Options: </Text>
          </View>
          {
            isNN(state.pollOption) ?
            state.pollOption.map((e: string, j: number) => {
              return(
                <View key={'pop' + j} className='mT-1 mB-3 p-2'>
                  <TextInput type='text' key={'popt' + j} placeholder={"Option" + (j + 1)} className='form-control' value={e} onChange={(event: any) => {
                    let newOptions = state.pollOption;
                    // var foundIndex = newOptions.findIndex(f => f.id === e.id);
                    newOptions[j] = event.target.value;
                    setState({...state, pollOption: newOptions});
                  }} />
                  <span className="icon-holder cur-p" onClick={() => {
                    let newOptions = state.pollOption;
                    delete newOptions[j];
                    setState({...state, pollOption: newOptions});
                  }}>
                    <i className="c-red-500 fa fa-times"></i>
                  </span>
                </View>
              )
            })
            :
            <View className='mT-1 mB-3 p-2'>
              <Text>No option found.</Text>
            </View>
          }
            <View className='mT-1 mB-3 p-2'>
              <span
              onClick={() => {
                if(!isNN(state.pollOption.filter(h => !isNN(h))))
                {
                  let newOptions = state.pollOption;
                  newOptions.push('');
                  setState({...state, pollOption: newOptions});
                }
              }}
              >
                <Text>Add More +</Text>
              </span>
            </View>
          <View className='mT-1 mB-3 p-2'>
            <span className="icon-holder mR-10 cur-p" onClick={onSave}>
              <i className="c-green-500 fa fa-check"></i>
            </span>
            <span className="icon-holder cur-p" onClick={() => {
              setState({
                ...state,
                isEdit: false,
                id: '',
                topic: '',
                description: '',
                isAllowMultiple: false,
                isOneVotePerFlat: false,
                isAnonymous: false,
                fromDate: new Date(),
                toDate: new Date(),
                pollOption: []
              });
            }}>
              <i className="c-red-500 fa fa-times"></i>
            </span>
          </View>
        </View>
        :
        []
        }
      {
      (isNNObject(props.polls) && props.polls.isUpToDate && !props.polls.isError && isNN(props.polls.data)) ?      
      props.polls.data.map((d: IPoll, i: number) => {
      return(
        (state.isEdit === true && state.id === d._id) ?
        <View key={"p" + d._id} className='bdB mT-5'>
          <View className='mT-1 mB-3 p-2'>
            <TextInput type='text' placeholder="Topic" className='form-control' value={state.topic} onChange={(event: any) => setState({...state, topic: event.target.value})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <TextArea placeholder="Description" className='form-control' value={state.description} onChange={(event: any) => setState({...state, description: event.target.value})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <DatePicker
            onChange={(date : any) => {
              setState({
                ...state,
                fromDate: date
              });
            }}
            value={state.fromDate}
            />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <DatePicker
            onChange={(date : any) => {
              setState({
                ...state,
                toDate: date
              });
            }}
            value={state.toDate}
            />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Checkbox isActive={state.isAllowMultiple} text='Allow Multiple Votes' onPress={() => setState({...state, isAllowMultiple: !state.isAllowMultiple})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Checkbox isActive={state.isOneVotePerFlat} text='One Vote per Flat' onPress={() => setState({...state, isOneVotePerFlat: !state.isOneVotePerFlat})} />        
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Checkbox isActive={state.isAnonymous} text='Anonymous Voting' onPress={() => setState({...state, isAnonymous: !state.isAnonymous})} />
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text>Options: </Text>
          </View>
          {
            isNN(state.pollOption) ?
            state.pollOption.map((e: string, j: number) => {
              return(
                <View key={'pop' + j} className='mT-1 mB-3 p-2'>
                  <TextInput type='text' key={'popt' + j} placeholder={"Option" + (j + 1)} className='form-control' value={e} onChange={(event: any) => {
                    let newOptions = state.pollOption;
                    // var foundIndex = newOptions.findIndex(f => f.id === e.id);
                    newOptions[j] = event.target.value;
                    setState({...state, pollOption: newOptions});
                  }} />
                  <span className="icon-holder cur-p" onClick={() => {
                    let newOptions = state.pollOption;
                    delete newOptions[j];
                    setState({...state, pollOption: newOptions});
                  }}>
                    <i className="c-red-500 fa fa-times"></i>
                  </span>
                </View>
              )
            })
            :
            <View className='mT-1 mB-3 p-2'>
              <Text>No option found.</Text>
            </View>
          }
            <View className='mT-1 mB-3 p-2'>
              <span
              onClick={() => {
                if(!isNN(state.pollOption.filter(h => !isNN(h))))
                {
                  let newOptions = state.pollOption;
                  newOptions.push('');
                  setState({...state, pollOption: newOptions});
                }            
              }}
              >
                <Text>Add More +</Text>
              </span>
            </View>
          <View className='mT-1 mB-3 p-2'>
            <span className="icon-holder mR-10 cur-p" onClick={onSave}>
              <i className="c-green-500 fa fa-check"></i>
            </span>
            <span className="icon-holder cur-p" onClick={() => {
              setState({
                ...state,
                isEdit: false,
                id: '',
                topic: '',
                description: '',
                isAllowMultiple: false,
                isOneVotePerFlat: false,
                isAnonymous: false,
                fromDate: new Date(),
                toDate: new Date(),
                pollOption: []
              });
            }}>
              <i className="c-red-500 fa fa-times"></i>
            </span>
          </View>
        </View>
        :
        <View key={"p" + d._id} className='bdB mT-5'>
          <View className='mT-1 mB-3 p-2'>
            <Text className='fw-600'>{d.topic}</Text>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text>{d.description}</Text>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text>{toDateString(d.fromDate) + " to " + toDateString(d.toDate)}</Text>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <View className={'bd pT-3 pB-3 pL-5 pR-5 ' + (d.isAllowMultiple === true ? 'bgc-blue-600 bdc-blue-600' : 'mL-5')}>
              <Text className={(d.isAllowMultiple === true ? 'c-white' : '')}>Allow Multiple Votes</Text>
            </View>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <View className={'bd pT-3 pB-3 pL-5 pR-5 ' + (d.isOneVotePerFlat === true ? 'bgc-blue-600 bdc-blue-600' : 'mL-5')}
             >
              <Text className={(d.isOneVotePerFlat === true ? 'c-white' : '')}>One Vote per Flat</Text>
            </View>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <View className={'bd pT-3 pB-3 pL-5 pR-5 ' + (d.isAnonymous === true ? 'bgc-blue-600 bdc-blue-600' : 'mL-5')}>
              <Text className={(d.isAnonymous === true ? 'c-white' : '')}>Anonymous Voting</Text>
            </View>
          </View>
          <View className='mT-1 mB-3 p-2'>
            <Text>Options: </Text>
          </View>
          {
            isNN(d.options) ?
            d.options.map((e: string, j: number) => {
              return(
                <View key={"po" + j} className='mT-1 mB-3 p-2'>
                  <Text key={"pot" + j}>{e}</Text>
                </View>
              )
            })
            :
            <View className='mT-1 mB-3 p-2'>
              <Text className='p-10'>No option found.</Text> 
            </View>           
          }
          <View className='mT-1 mB-3 p-2'>
          {(d.fromDate > new Date()) &&
          <span className="icon-holder mR-10 cur-p" onClick={() => {
            setState({
              ...state,
              isEdit: true,
              id: d._id,
              topic: d.topic,
              description: d.description,
              isAllowMultiple: d.isAllowMultiple,
              isOneVotePerFlat: d.isOneVotePerFlat,
              isAnonymous: d.isAnonymous,
              fromDate: d.fromDate,
              toDate: d.toDate,
              pollOption: (isNN(d.options) ? d.options : [])
            });
          }}>
            <i className="c-blue-500 fa fa-pencil"></i>
          </span>
          }
          <span className="icon-holder mR-25 cur-p" onClick={() => {
              onRemove(d._id);
            }}>
            <i className="c-red-500 fa fa-trash"></i>
          </span>
            <Text className='cur-p c-blue-500' onClick={() => {
              setState({...state, showVotePoll: d})
            }}>View Votes</Text>
          </View>
        </View>
      )
      })
      :
      <Text className='p-20'>{(props.polls.isUpToDate ? (props.polls.isError ? props.polls.errorMessage : "No poll found.") : "Loading...")}</Text>
    }
    <Modal
      open={(isNNObject(state.showVotePoll) && state.showVotePoll._id.length > 0)}
      onClose={() => setState({...state, showVotePoll : { _id: '' } as IPoll})}>
      <View className='mT-30 mB-25 mL-15 mR-15' style={{height: '80%'}}>
        <View>
        {
          loadVotes()
        }
        </View>
      </View>
    </Modal>
    <Spinner visible={state.isLoading} />
    </View>  
    );
};