import React, {useState, useEffect} from 'react';
import { View, Picker } from '../common/elements';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  appUser: IAppUser,
  onUpdate: (role: string, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [role, setRole] = useState('');

  useEffect(() => {
    setRole(props.appUser.role);
  }, []);
  
  const onUpdate = () => {
    if(isNN(role)){
      setIsLoading(true);
      props.onUpdate(role, (result: boolean, message: string) => {
        setIsLoading(false);
        if(result === true){
          Toast.success('Role updated successfully.');
          props.history.push('/')
        }else{
          Toast.fail(message);
        }
      });
    }else{
      Toast.warn('Please select role before Update.');
    }
  };

      return(
        <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'My Role', link: 'my-role'}]} />
          <View className='bgc-white p-20'>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <Picker
                className='form-control'
                value={role}
                onChange={(event: any) => setRole(event.target.value)}>
                  {
                  props.user.role.split(',').map((d: string, i: number) => {
                    return(<option key={'role' + i} value={d}>{d}</option>);
                  })
                  }
              </Picker>
            </View>
            <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
              <Button
              title="UPDATE"
              onPress={onUpdate}
              />
            </View>
          </View>
          <Spinner visible={isLoading} />
        </View>
        </View>
        </View>
      );
};