import React, {useState} from 'react';
import {confirmAlert} from 'react-confirm-alert';
import {View, Text, TextInput} from '../common/elements';
import Spinner from '../common/spinner';
import Breadcrum from '../common/breadcrum';
import RadioButton from '../common/radio-button';
import {IUsers} from '../../models/store/users';
import {IUser} from '../../models/user/user';
import {IMember} from '../../models/home/member';
import {isNN} from '../../util/index';
import Toast from '../../util/toast';
import { APPTYPE } from '../../constants';

interface IProps {
  history: any,
  user: IUser,
  users: IUsers,
  homeIndex: number,
  onSave: (data: IMember, user: IUser, homeIndex: number, sCallback: (result: boolean, message: string) => void) => void,
  onRemove: (id: string, homeIndex: number, user: IUser, users: IUsers, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [state, setState] = useState({
    id: '',
    number: '',
    name: '',
    age: 0,
    gender: '',
    mobileNo: '',
    emailId: '',
    type: 'TENANT',
    role: 'USER'
  });

  const onSave = () => {
    if (isNN(state.name) && state.age > 0 && isNN(state.gender) && isNN(state.type)) {
      setIsLoading(true);
      const member = {
        _id: state.id,
        number: state.number,
        name: state.name,
        age: state.age,
        gender: state.gender,
        mobileNo: state.mobileNo,
        emailId: state.emailId ? state.emailId.toLowerCase() : '',
        type: state.type,
        role: state.role,
      } as IMember;
      props.onSave(member, props.user, props.homeIndex, (result: boolean, message: string) => {
        setIsLoading(false);
        if (result === true) {
          setIsEdit(false);
          setState({
            id: '',
            number: '',
            name: '',
            age: 0,
            gender: '',
            mobileNo: '',
            emailId: '',
            type: 'TENANT',
            role: 'USER',
          });
          Toast.success('Member saved successfully.');
        } else {
          Toast.fail(isNN(message) ? message : 'Failed to save member.');
        }
      });
    } else {
      // Toast.warn('Please fill all the required fields before '+ (state.id > 0 ? 'Update' : 'Save') + '.');
      Toast.warn('Please fill all the required fields before '+ (state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.homeIndex, props.user, props.users, (result: boolean) => {
              setIsLoading(false);
              if (result === true) {
                Toast.success('Member removed successfully.');
              } else {
                Toast.fail('Failed to remove member.');
              }
            });
          },
        },
        {
          label: 'No',
          onClick: () => {},
        },
      ],
    });
  };

    return (
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
            <Breadcrum data={[{title: 'Members', link: 'members'}]} />
            <View className='bgc-white p-20'>
              <View>
                <Text className='fl-r cur-p c-blue-500' onClick={() => props.history.push('new-member')}>Add New</Text>
              </View>
              {isNN(props.user.homes[props.homeIndex].members) ?
        props.user.homes[props.homeIndex].members.map((d: IMember, i: number) => {
          return (
            (isEdit === true && state.id === d._id) ?
            <View key={i} className='bdB mT-5'>
              {(props.user.client.config.appType == APPTYPE.QUARTERS && isNN(d.number)) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Number: ' + d.number}</Text>                  
                </View>
              }
              <View className='mT-1 mB-3 p-2'>
                <TextInput type='text' placeholder="Name" className='form-control' value={state.name} onChange={(event: any) => setState({...state, name: event.target.value})} />
              </View>
              <View className='mT-1 mB-3 p-2'>
                <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} value={state.age > 0 ? state.age + '' : ''} onChange={(event: any) => setState({...state, age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
              </View>
              <View className='mT-1 mB-3 p-2'>
                <RadioButton isActive={state.gender === 'M'} text='Male' onPress={() => setState({...state, gender: 'M'})} />
                <RadioButton isActive={state.gender === 'F'} text='Female' onPress={() => setState({...state, gender: 'F'})} />
                {props.user.client.config.hasOtherGender &&
                <RadioButton isActive={state.gender === 'O'} text='Other' onPress={() => setState({...state, gender: 'O'})} />
                }
              </View>
              <View className='mT-1 mB-3 p-2'>
                <TextInput type='text' placeholder="Mobile Number" className='form-control' value={state.mobileNo} onChange={(event: any) => setState({...state, mobileNo: event.target.value})} />
              </View>
              <View className='mT-1 mB-3 p-2'>
                <TextInput type='text' placeholder="Email ID" className='form-control' value={state.emailId} onChange={(event: any) => setState({...state, emailId: event.target.value})} />
              </View>
              {/* <View className='mT-1 mB-3 p-2'>
                <RadioButton isActive={state.type === 'OWNER'} text='Owner' onPress={() => setState({...state, type: 'OWNER'})} />
                <RadioButton isActive={state.type === 'TENANT'} text='Tenant' onPress={() => setState({...state, type: 'TENANT'})} />
              </View> */}
              <View className='mT-1 mB-3 p-2'>
                <span className="icon-holder mR-10 cur-p" onClick={onSave}>
                  <i className="c-green-500 fa fa-check"></i>
                </span>
				      <span className="icon-holder cur-p" onClick={() => {
                setIsEdit(false);
                  setState({
                    ...state,
                    id: '',
                    number: '',
                    name: '',
                    age: 0,
                    gender: '',
                    mobileNo: '',
                    emailId: '',
                    type: 'TENANT',
                    role: 'USER',
                  });
                }}>
					      <i className="c-red-500 fa fa-times"></i>
				      </span>
              </View>
            </View> :
            <View key={i} className='bdB mT-5'>
              {(props.user.client.config.appType == APPTYPE.QUARTERS && isNN(d.number)) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Number: ' + d.number}</Text>                  
                </View>
              }
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.name}</Text>
              </View>
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
              </View>
              {isNN(d.mobileNo) &&
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.mobileNo}</Text>
              </View>
              }
              {isNN(d.emailId) &&
              <View className='mT-1 mB-3 p-2'>
                <Text>{d.emailId}</Text>
              </View>
              }
              {/* <View className='mT-1 mB-3 p-2'>
                <Text>{d.type}</Text>
              </View> */}
              <View className='mT-1 mB-3 p-2'>
                <span className="icon-holder mR-10 cur-p" onClick={() => {
                  setIsEdit(true);
                  setState({
                    ...state,
                    id: d._id,
                    number: d.number,
                    name: d.name,
                    age: d.age,
                    gender: d.gender,
                    mobileNo: d.mobileNo,
                    emailId: d.emailId,
                    type: d.type,
                    role: d.role,
                  });
                }}>
                  <i className="c-blue-500 fa fa-pencil"></i>
                </span>
                <span className="icon-holder cur-p" onClick={() => {
                  onRemove(d._id);
                }}>
                  <i className="c-red-500 fa fa-trash"></i>
                </span>
              </View>
            </View>
          );
        }) :
        <Text className='p-20'>No member found.</Text>
              }
              <Spinner visible={isLoading} />
            </View>
          </View>
        </View>
      </View>
    );
};
