import React, {useState} from 'react';
import { View, Text } from '../common/elements';
import DatePicker from 'react-date-picker';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IOutings } from '../../models/store/outings';
import { IOuting } from '../../models/outing';
import { isNNObject } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  outings: IOutings,
  onSave: (data: IOuting, homeId: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => void,
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [state, setState] = useState({
    fromDate: new Date(),
    toDate: new Date(),
    place: '',
    address: '',
    datePickerType: '',
    datePickerDate: new Date()
  });
  
  const onSave = () => {
    // if(isNNObject(state.fromDate) && isNNObject(state.toDate) && isNN(state.place) && isNN(state.address)){
    if(isNNObject(state.fromDate) && isNNObject(state.toDate)){
        setIsLoading(true);
        let outing = {
          ////homeId: props.user.home[props.appUser.homeIndex].id,
          _homeId: props.user.homes[props.appUser.homeIndex].number,
          fromDate: state.fromDate,
          toDate : state.toDate,
          place: state.place,
          address: state.address,
          isReturned: false,
          createdBy: props.user.name,
          createdAt: new Date()
        } as IOuting;
        props.onSave(outing, props.user.homes[props.appUser.homeIndex]._id, props.user, props.outings, (result: boolean) => {
          setIsLoading(false);
          if(result === true){
            setState({
              fromDate: new Date(),
              toDate: new Date(),
              place: '',
              address: '',
              datePickerType: '',
              datePickerDate: new Date()
            });
            Toast.success('Outing submitted successfully.');
          }else{
            Toast.fail('Failed to submit Outing.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before submit Outing.');
    }
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Notice', link: 'notice'}, {title: 'New Outing', link: 'new-outing' }]} />
        <View className='bgc-white p-20' style={{minHeight: 450}}>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='w-100'>From Date: </Text>
            <DatePicker
            onChange={(date : any) => {
              setState({
                ...state,
                fromDate: date
              });
            }}
            value={state.fromDate}
            />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='w-100'>To Date: </Text>
            <DatePicker
            onChange={(date : any) => {
              setState({
                ...state,
                toDate: date
              });
            }}
            value={state.toDate}
            />
          </View>
          {/* <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Place" className='form-control' value={state.place} onChange={(event: any) => setState({...state, place: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextArea placeholder="Address" className='form-control' value={state.address} onChange={(event: any) => setState({...state, address: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, onSave);
            }} />
          </View> */}
          <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
            <Button
            title="SUBMIT"
            onPress={onSave}
            />
          </View>
        </View>
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
    );
};