import React, {useState} from 'react';
import { View, Text, TextInput, Image } from '../common/elements';
import Button from '../common/button';
import Spinner from '../common/spinner';
import Images from '../images';
import { isNN, onEnter } from '../../util';
import Toast from  '../../util/toast';
import { IMember } from '../../models/home';
import SignUp from './sign-up';

interface IProps {
    onLogin: (userName: string, password: string, callback: (result: boolean, message: string) => void) => void,
    onForgetPassword: (mobileNumber: string, email: string, callback: (result: boolean, message: string) => void) => void,
    onSignUp: (data: IMember, clientCode: string, sCallback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
    const [isLoading, setIsLoading] = useState(false);
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [message, setMessage] = useState('');
    const [isForgetPass, setIsForgetPass] = useState(false);
    const [isSignUp, setIsSignUp] = useState(false);
    const [mobileNumber, setMobileNumber] = useState('');
    const [email, setEmail] = useState('');

    const onLogin = () =>{
        if(isNN(userName) && isNN(password)){
            setMessage('');
            setIsLoading(true);
            props.onLogin(userName, password, (result: boolean, rMessage: string) => {       
                setIsLoading(false);
                if(result === true){
                    setMessage('');
                }else{
                    setMessage(rMessage);
                }
            });
        }else{
            // Toast.warn('Enter Mobile Number and Password to continue.');
            Toast.warn('Enter Mobile Number and Password to continue.');
        }
    };
    
    const onForgetPassword = () =>{
        setIsForgetPass(true);
        setIsSignUp(false);
        setMobileNumber('');
        setEmail('');
        setMessage('');
    };

    const onForgetPassSubmit = () => {
        if(isNN(mobileNumber) && isNN(email)){
            setMessage('');
            setIsLoading(true);
            props.onForgetPassword(mobileNumber, email.toLowerCase(), (result: boolean, rMessage: string) => {
                setIsLoading(false);
                if(result === true){
                    Toast.success('Password reset success. Login with New password sent to your Email. Change password after login.');
                    setMessage('');
                    setIsForgetPass(false);
                    setIsSignUp(false);
                    setUserName('');
                    setPassword('');
                }else{
                    setMessage(rMessage);
                }
            });
        }else{
            Toast.warn('Enter Mobile Number and Email to continue.');
        }
    };

    const onCreateAccount = () =>{
        setIsForgetPass(false);
        setIsSignUp(true);
        setMobileNumber('');
        setEmail('');
        setMessage('');
    };

    const onSignUp = (data: IMember, clientCode: string, callback: (result: boolean) => void) => {
        setMessage('');
        setIsLoading(true);
        props.onSignUp(data, clientCode, (result: boolean, message: string) => {
            setIsLoading(false);
            if(result === true){
              setIsForgetPass(false);
              setIsSignUp(false);
              setMessage('');
              Toast.success('Sign Up success. Please wait till Manager approves then you can access your account.');
              callback(true);
            }else{
              Toast.fail(isNN(message) ? message : 'Failed to Sign Up.');
              callback(false);
            }
          });
    };

    return (
            <View className='container bgc-white' style={{maxWidth: '270px'}}>
                <View className='ta-c ai-c jc-c mB-30 mT-5'>
                    <Image src={Images.logo} style={{width: 150, height: 150}}/>
                </View>
                {isForgetPass &&
                <View className='ai-c jc-c'>
                    <View className='col-12 mB-25'>
                        <Image className='pos-a' src={Images.mobile} style={styles.txtIcon} />
                        <TextInput type='text'  className='form-control pL-25' placeholder='Registered Mobile Number' value={mobileNumber}
                        onChange={(event: any) => setMobileNumber(event.target.value)}
                        onKeyPress={(e) => {
                            onEnter(e, onForgetPassSubmit);
                        }} />
                    </View>
                    <View className='col-12 mB-25'>
                        <Image className='pos-a' src={Images.email} style={styles.txtIcon} />
                        <TextInput type='text'  className='form-control pL-25' placeholder='Registered Email' value={email}
                        onChange={(event: any) => setEmail(event.target.value)}
                        onKeyPress={(e) => {
                            onEnter(e, onForgetPassSubmit);
                        }} />
                    </View>
                    <View className='ta-c'>
                    <Button
                    title={isLoading === false ? "Reset my password" : "Loading..."}
                    onPress={onForgetPassSubmit}
                    />
                    <Text className={'col-12 ta-c mT-5' + (isLoading ? '' : ' c-red-600')}>{message}</Text>
                    </View>
                    <View className='mT-5 ta-c'>
                        <Text className='cur-p mB-5 ta-c c-blue-500' onClick={() => { 
                            setIsForgetPass(false);
                            setIsSignUp(false);
                            setMobileNumber('');
                            setEmail('');
                            setMessage('');
                         }}>Go back to Login.</Text>
                    </View>
                </View>
                }
                {isSignUp && <>
                    <SignUp onSignUp={onSignUp} />
                    <View className='mT-15 ta-c'>
                        <Text className='cur-p mB-5 ta-c c-blue-500' onClick={() => { 
                            setIsForgetPass(false);
                            setIsSignUp(false);
                            setMobileNumber('');
                            setEmail('');
                            setMessage('');
                         }}>Go back to Login.</Text>
                    </View>
                    </>
                }
                {(!isForgetPass && !isSignUp) && 
                <View className='ai-c jc-c'>
                    <View className='col-12 mB-25'>
                        <Image className='pos-a' src={Images.username} style={styles.txtIcon} />
                        <TextInput type='text' className='form-control pL-25' placeholder='Mobile Number' value={userName} 
                        onChange={(event: any) => setUserName(event.target.value)}
                        onKeyPress={(e) => {
                            onEnter(e, onLogin);
                        }} />
                    </View>
                    <View className='col-12 mB-15'>
                        <Image className='pos-a' src={Images.password} style={styles.txtIcon} />
                        <TextInput type='password'  className='form-control pL-25' placeholder='Password' value={password}
                        onChange={(event: any) => setPassword(event.target.value)}
                        onKeyPress={(e) => {
                            onEnter(e, onLogin);
                        }} />
                    </View>
                    <View className='ta-c'>
                    <Text className='col-12 cur-p mB-15 ta-r c-blue-500' onClick={onForgetPassword}>Forgot Password?</Text>
                    <Button
                    title={isLoading === false ? "LOGIN" : "Loading..."}
                    onPress={onLogin}
                    />
                    <Text className={'col-12 ta-c mT-5' + (isLoading ? '' : ' c-red-600')}>{message}</Text>
                    </View>
                    <View className='col-12 mT-5 ai-c jc-c'>
                        <Text className='col-12 mB-5 ta-c'>Dont have an account?</Text>
                        <View className='col ai-c jc-c cur-p bd p-0' style={{borderRadius: '15px'}}>
                            <Text className='ta-c m-0 p-15 cur-p' onClick={onCreateAccount}>CREATE / JOIN YOUR FLAT</Text>
                        </View>
                    </View>
                </View>
                }
                <Spinner visible={isLoading} text={""} />
            </View>
    );
};

const styles = {
    txtIcon: {
        marginLeft: 6,
        // marginRight: -15,
        height: 15,
        width: 15,
        resizeMode : 'stretch',
        alignItems: 'center',
        top: 11
    }
};