import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Button from '../../common/button';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IServant, ITakeaway } from '../../../models/home/servant';
import Toast from  '../../../util/toast';
import { isNN } from '../../../util';
import { toDateTimeString } from '../../../util/convert';
import { ROLE } from '../../../constants';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  gateNo: string,
  onLoad: (appClientId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onExit: (id: string, gateNo: string, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([] as Array<IServant>);

  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, (result: Array<IServant>) => {
      setIsLoading(false);
      setData(result);
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  }, []);

  const onExit = (id: string, servantId: string) => {
    confirmAlert({
      title: 'Confirm to Exit Servant with Takeaway',
      message: 'Are you sure you wish to exit the servant with this takeaway things?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onExit(id, props.gateNo, (result: boolean) => {
              if(result === true){
                let tData = data;
                var foundIndex = tData.findIndex(d => d._id === servantId);
                tData[foundIndex].takeaways = tData[foundIndex].takeaways.filter(d => d._id !== id);
                setData(tData);
                setIsLoading(false);
                Toast.success('Servant exit with takeaway successfully.');
              }else{
                setIsLoading(false);
                Toast.fail('Failed to exit servant with takeaway.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Takeaways', link: 'all-takeaways'}]} />
          <View className='bgc-white p-20'>
            {(isNN(data) && isNN(data.filter(d => d.takeaways && d.takeaways.length > 0))) ?
              data.filter(d => d.takeaways && d.takeaways.length > 0).map((d: IServant, i: number) => {
              return(
                <View key={i} className='mT-5 bdB'>
                  <View className='mB-3'>
                    <Text className='fw-600'>{d.createdBy + ' on ' + toDateTimeString(d.createdAt)}</Text>
                  </View>
                  <View className='mB-3'>
                    <Text>{d.name}</Text>
                  </View>
                  <View className='mB-3'>
                    <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
                  </View>
                  {isNN(d.mobileNo) &&
                  <View className='mB-3'>
                    <Text>{d.mobileNo}</Text>
                  </View>
                  }
                  {isNN(d.emailId) &&
                  <View className='mB-3'>
                    <Text>{d.emailId}</Text>
                  </View>
                  }
                  <View className='mT-5 mB-5'>
                    <View>
                      <Text className='fw-600'>Takeaways</Text>
                    </View>
                    <View className='pL-15 pR-15 pB-15 pT-3'>
                      {isNN(d.takeaways) ?
                        d.takeaways.map((e: ITakeaway, j: number) => {
                          return(
                          <View key={'tkycmt' + j} className={'pT-5 pB-5 pR-3 pL-3' + (j === 0 ? '' : ' bdB')}>
                            <View>
                              <Text className='fw-600'>{e.createdBy + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                            </View>
                            <View>
                              <Text>{e.title}</Text>
                            </View>
                            <View>
                              <Text>{e.description}</Text>
                            </View>
                            {props.appUser.role === ROLE.GATEKEEPER &&
                            <View className='mT-10'>
                              {!e.isExit && <Button title='Exit' onPress={() => {onExit(e._id, d._id);}} />}
                            </View>
                            }
                          </View>
                          )
                        })
                        :
                        <Text className='p-20'>No takeaway found.</Text>
                      }
                    </View>
                  </View>
                </View>
                );
            })
            :
            <Text>No data found.</Text>
            }
            <Spinner visible={isLoading} />
          </View>
        </View>
      </View>
    </View>
  );
};