import React, {useState} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View,  TextInput, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IHome } from '../../../models/home';
import { IUserHomes } from '../../../models/store/user-homes';
import { isNN, onEnter } from '../../../util';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser
  user: IUser,
  users: IUsers,
  homes: IHomes,
  userHomes: IUserHomes,
  onRemove: (id: string, user: IUser, homes: IHomes, callback: (result: boolean) => void) => void,
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  // const [isEdit, setIsEdit] = useState(false);
  const [searchText, setSearchText] = useState('');
  const [searchResult, setSearchResult] = useState([] as Array<IHome>);
  const [searchResultIndex, setSearchResultIndex] = useState(10);
  
  // const getUsers = (homeId: string) => {
  //   let users = '';
  //   if((props.users.isUpToDate && !props.users.isError && isNN(props.users.data)) && (props.userHomes.isUpToDate && !props.userHomes.isError && isNN(props.userHomes.data))){
  //     let userIds = [] as Array<string>;
  //     props.userHomes.data.filter(uh => uh.homeId === homeId).map((uh: IUserHome, i: number) => userIds.push(uh.userId));
  //     props.users.data.filter(d => userIds.indexOf(d._id) >= 0).map((u: IMember, i: number) => users += (users.length > 0 ? ', ' : '') + u.name);
  //   }
  //   return users;
  // };

  const onSearch = () => {
    if(isNN(searchText) && props.homes.isUpToDate && !props.homes.isError && isNN(props.homes.data)){
      setSearchResult(props.homes.data.filter(d => (d.number + '').toLocaleLowerCase().indexOf(searchText.toLowerCase()) >= 0));
      setSearchResultIndex(10);
    }else{
      setSearchResult([]);
      setSearchResultIndex(10);
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.user, props.homes, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                onSearch();
                Toast.success('House removed successfully.');
              }else{
                Toast.fail('Failed to remove house.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

      return(
        <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Houses', link: 'homes'}]} />
          <View className='bgc-white p-20'>
          <View>
            <Text className='fl-r cur-p c-blue-500 mB-3' onClick={() => props.history.push('edit-home/0')}>Add New</Text>
          </View>
          <View className='mB-3 pB-5'>
            <TextInput type='text' placeholder="Search House" className='form-control mR-10 d-i' style={{width: 'calc(100% - 40px)'}} value={searchText} onChange={(event: any) => setSearchText(event.target.value)}
            onKeyPress={(e) => {
              onEnter(e, onSearch);
            }} />
            <span className='icon-holder cur-p' onClick={onSearch}>
              <i className='ti-search pdd-right-10 fw-900'></i>
            </span>
          </View>

          {isNN(searchResult) ?
          searchResult.slice(0, searchResultIndex).map((d: IHome, i: number) => {
            return(
              <View key={'home' + i} className='bdB mT-5'>
                {/* <View className='mT-1 mB-3 p-2'>
                  <Text>{'Name: ' + (isNN(d.name) ? d.name : '')}</Text>                  
                </View> */}
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Number: ' + d.number}</Text>         
                </View>
                {/* <View className='mT-1 mB-3 p-2'>
                  <Text>{'Address: ' + (isNN(d.address) ? d.address : '')}</Text>         
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'User(s): ' + getUsers(d._id)}</Text>         
                </View> */}
                <View className='mT-1 mB-3 p-2'>
                  <span className="icon-holder mR-10 cur-p" onClick={() => {
                    props.history.push('view-home/' + d._id);
                    // props.navigation.navigate('ViewHome', {id: d._id});
                  }}>
                    <i className="c-orange-500 fa fa-eye"></i>
                  </span>
                  <span className="icon-holder mR-10 cur-p" onClick={() => {
                    props.history.push('edit-home/' + d._id);
                    // props.navigation.navigate('EditHome', {id: d._id});
                    // setState({
                    //   isEdit: true,
                    //   id: d.id
                    // });
                  }}>
                    <i className="c-blue-500 fa fa-pencil"></i>
                  </span>
                  <span className="icon-holder cur-p" onClick={() => {
                      onRemove(d._id);
                    }}>
                    <i className="c-red-500 fa fa-trash"></i>
                  </span>
                </View>
              </View>
            )
          })
          :
          <Text className='p-20'>No house match with your search.</Text>
        }
        {(searchResult.length > searchResultIndex) &&
        <View className='bdB mT-5'>
          <View className='p-10'>
            <Text className='fw-600 c-blue-500' onClick={() => {
            setSearchResultIndex(searchResultIndex + 10)
            }}>Show More</Text>
          </View>
        </View>
        }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
      );
};