import React, {useState, useEffect} from 'react';
import { View, Text } from '../../../common/elements';
import { IUser } from '../../../../models/user';
import { IMember } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { toDateString } from '../../../../util/convert';
import { APPTYPE } from '../../../../constants';
import { isNN } from '../../../../util/index';

interface IProps {
  user: IUser,
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [data, setData] = useState([] as Array<IMember>);

  useEffect(() => {
    if(props.homeId.length > 0){
      props.onToggleLoading(true);
      props.onLoad(props.homeId, (result: Array<IMember>) => {
        props.onToggleLoading(false);
        setData(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Members.');
      });
    }
  }, []);

  return(
    <View>
    <View className='ta-c mT-1 mB-3 p-2'>
      <Text className='c-blue-700 fw-600 mB-10'>Members</Text>
    </View>
      {isNN(data) ?
        data.map((d: IMember, i: number) => {
        return(
          <View key={i} className='bdB mT-5'>
            <View className='mT-1 mB-3 p-2'>
              <Text className='fw-600'>{(isNN(d.createdBy) ? d.createdBy : '') + ' on ' + toDateString(d.createdAt)}</Text>
            </View>
            {(props.user.client.config.appType == APPTYPE.QUARTERS && isNN(d.number)) &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.name}</Text>
            </View>
            }
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.name}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
            </View>
            {isNN(d.mobileNo) &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.mobileNo}</Text>
            </View>
            }
            {isNN(d.emailId) &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.emailId}</Text>
            </View>
            }
            {props.user.client.config.appType == APPTYPE.APARTMENT &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.type}</Text>
            </View>
            }
          </View>
          );
      })
      :
      <Text>No data found.</Text>
    }
    </View>
  );
};