import React, {useState} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, TextInput, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IMember } from '../../../models/home';
import { IUserHomes } from '../../../models/store/user-homes';
import { isNN, onEnter } from '../../../util';
import Toast from  '../../../util/toast';
import { APPTYPE } from '../../../constants';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  userHomes: IUserHomes,
  onRemove: (id: string, homeIndex: number, user: IUser, users: IUsers, callback: (result: boolean) => void) => void,
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  // const [isEdit, setIsEdit] = useState(false);
  const [searchText, setSearchText] = useState('');
  const [searchResult, setSearchResult] = useState([] as Array<IMember>);
  const [searchResultIndex, setSearchResultIndex] = useState(10);

  // const getHomes = (userId: string) => {
  //   let homes = '';
  //   if((props.homes.isUpToDate && !props.homes.isError && isNN(props.homes.data)) && (props.userHomes.isUpToDate && !props.userHomes.isError && isNN(props.userHomes.data))){
  //     let homeIds = [] as Array<string>;
  //     props.userHomes.data.filter(uh => uh.userId === userId).map((uh: IUserHome, i: number) => homeIds.push(uh.homeId));
  //     props.homes.data.filter(d => homeIds.indexOf(d._id) >= 0).map((h: IHome, i: number) => homes += (homes.length > 0 ? ', ' : '') + h.number);
  //   }
  //   return homes;
  // };

  const onSearch = () => {
    if(isNN(searchText) && props.users.isUpToDate && !props.users.isError && isNN(props.users.data)){
      let tempSearchText = searchText.toLowerCase();
      let homes = props.homes.data.filter(h => h.number.toLowerCase() === tempSearchText);
      setSearchResult(props.users.data.filter(d => (d.name + '').toLowerCase().indexOf(tempSearchText) >= 0 || (d.mobileNo + '').toLowerCase().indexOf(tempSearchText) >= 0 || (d.emailId + '').toLowerCase().indexOf(tempSearchText) >= 0 || (d.number + '').toLowerCase().indexOf(tempSearchText) >= 0 || (isNN(homes.filter(h => h.memberIds.indexOf(d._id) >= 0)))));
      setSearchResultIndex(10);
    }else{
      setSearchResult([]);
      setSearchResultIndex(10);
    }
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, props.appUser.homeIndex, props.user, props.users, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                onSearch();
                Toast.success('User removed successfully.');
              }else{
                Toast.fail('Failed to remove user.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

      return(
        <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Users', link: 'users'}]} />
          <View className='bgc-white p-20'>
          <View>
            <Text className='fl-r cur-p c-blue-500' onClick={() => props.history.push('edit-user/0/false')}>Add New</Text>
            <Text className='fl-r cur-p c-blue-500 mR-15' onClick={() => props.history.push('approve-users')}>View Users Waiting for Approval</Text>
          </View>
          <View className='mB-3 pB-5'>
            <TextInput type='text' placeholder="Search User" className='form-control mR-10 d-i' style={{width: 'calc(100% - 40px)'}} value={searchText} onChange={(event: any) => setSearchText(event.target.value)}
            onKeyPress={(e) => {
              onEnter(e, onSearch);
            }} />
            <span className='icon-holder cur-p' onClick={onSearch}>
              <i className='ti-search pdd-right-10 fw-900'></i>
            </span>
          </View>

          {isNN(searchResult) ?
          searchResult.slice(0, searchResultIndex).map((d: IMember, i: number) => {
            return(
              <View key={'usr' + i} className='bdB mT-5'>
                {(props.user.client.config.appType == APPTYPE.QUARTERS && isNN(d.number)) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Number: ' + d.number}</Text>                  
                </View>
                }
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Name: ' + (isNN(d.name) ? d.name : '')}</Text>                  
                </View>
                {isNN(d.mobileNo) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Mobile No: ' + d.mobileNo}</Text>         
                </View>
                }
                {isNN(d.emailId) &&
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Email: ' + d.emailId}</Text>         
                </View>
                }
                {/* <View className='mT-1 mB-3 p-2'>
                  <Text>{'Owner Name: ' + (isNN(d.ownername) ? d.ownername : '')}</Text>         
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Owner Mobile No: ' + (d.ownermobile > 0 ? d.ownermobile : '')}</Text>         
                </View> */}
                {/* <View className='mT-1 mB-3 p-2'>
                  <Text>{'Role: ' + (isNN(d.type) ? d.type.replace(/,/g, ', ') : '')}</Text>         
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'House(s): ' + getHomes(d._id)}</Text>         
                </View> */}
                {(d._id !== props.user._id) &&
                  <View className='mT-1 mB-3 p-2'>
                    <span className="icon-holder mR-10 cur-p" onClick={() => {
                      props.history.push('edit-user/' + d._id + '/false');////TODO
                      // props.navigation.navigate('EditUser', {id: d._id});
                      // setState({
                      //   isEdit: true,
                      //   id: d.id
                      // });
                    }}>
                    <i className="c-blue-500 fa fa-pencil"></i>
                  </span>
                  <span className="icon-holder cur-p" onClick={() => {
                      onRemove(d._id);
                    }}>
                    <i className="c-red-500 fa fa-trash"></i>
                  </span>
                  </View>
                }
              </View>
              
            )
          })
          :
          <Text className='p-20'>No user match with your search.</Text>
        }
        {(searchResult.length > searchResultIndex) &&
        <View className='bdB mT-5'>
          <View className='p-10'>
            <Text className='c-blue-500 fw-600' onClick={() => {
              setSearchResultIndex(searchResultIndex + 10)
            }}>Show More</Text>
          </View>
        </View>
        }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
      );
};