import React, {useState} from 'react';
import { View, ScrollView, TextInput, Text, Picker, TextArea } from '../../common/elements';
import Button from '../../common/button';
import Checkbox from '../../common/checkbox';
import Spinner from '../../common/spinner';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IHomes } from '../../../models/store/homes';
import { IHome } from '../../../models/home';
import { isNN } from '../../../util/index';
import Toast from  '../../../util/toast';
import { MONTH } from '../../../constants';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onSave: (appClientId: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [amount, setAmount] = useState(0);
  const [description, setDescription] = useState('');
  const [month, setMonth] = useState(new Date().getMonth());
  const [year, setYear] = useState(new Date().getFullYear());
  const [searchText, setSearchText] = useState('');
  const [showHomes, setShowHomes] = useState(false);
  const [isAllHome, setIsAllHome] = useState(false);
  const [homes, setHomes] = useState([] as Array<IHome>);

  const onSave = () => {
    if(amount > 0 && (isAllHome || homes.length > 0)){
      let homeIds = [] as any;
      homes.map((h,i) => homeIds.push(h._id));
      let data = {
        amount: amount,
        description: description,
        month: month,
        year: year,
        isAllHome: isAllHome,
        homes: homeIds
      };
      setIsLoading(true);
      props.onSave(props.user.client._id, data, props.user, (result: boolean, message: string) => {
        setIsLoading(false);
        if(result === true){
          // props.navigation.navigation.goBack(null);
          onClear();
          Toast.success('Payment saved successfully.');
        }else{
          Toast.fail( message ? message : 'Failed to save payment.');
        }
      });
    }else{
      Toast.warn('Please fill all required fields before Save.');
    }
  };

  const onClear = () => {
    setAmount(0);
    setDescription('');
    setMonth(new Date().getMonth());
    setYear(new Date().getFullYear());
    setSearchText('');
    setShowHomes(false);
    setIsAllHome(false);
    setHomes([]);
  };

    return(
        <View className='pL-15 pR-15'>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Amount" className='form-control' value={amount > 0 ? amount + '' : ''} onChange={(event: any) => setAmount(+(event.target.value.replace(/[^0-9]/g, '')))} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mT-15'>{'Payment for ' + year + ' '}</Text>
            <Picker
              className='form-control'
              value={month}
              onChange={(event: any) => setMonth(event.target.value)}>
              {
                MONTH.filter((m, i) => m.value <= new Date().getMonth()).map((m: any, i: number) => {
                  return(<option key={'month' + i} value={m.value}>{m.title}</option>);
                })
              }
            </Picker>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextArea placeholder="Description" className='form-control' value={description} onChange={(event: any) => setDescription(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mR-15'>{'Apply to House(s): ' + (isAllHome ? '' : homes.length)}</Text>
            <Checkbox isActive={isAllHome} text='All Homes' onPress={() => setIsAllHome(!isAllHome)} />
          </View>
          {!isAllHome &&
          <ScrollView className='mT-3 mB-3 pT-5 pB-5 ov-s'>
                {
                  homes.map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='cur-p mR-6 c-blue-500'
                      key={'ho' + i}
                      onClick={() => {
                        setHomes(homes.filter((h, i) => h._id !== d._id));
                      }}>{d.number}</Text>
                    )
                  })
                }
          </ScrollView>
          }
          {!isAllHome &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Enter House Number" className='form-control p-6 bd bdrs-5'
            value={searchText} 
            onChange={(event: any) => {
              setShowHomes(true);
              setSearchText(event.target.value)
            }} />
          </View>
          }
            {(!isAllHome && showHomes && isNN(searchText) && props.homes && props.homes.data.length > 0 ) &&
              <ScrollView className='mT-3 mB-3 pT-5 pB-5 ov-s'>
                {
                  props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(searchText.toLowerCase()) >= 0 && (homes.filter(h => h._id === d._id).length < 1)).map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='cur-p mR-6'
                      key={'ho' + i}
                      onClick={() => {
                        let tHomes = homes;
                        tHomes.push(d);
                        setHomes(tHomes);
                      }}>{d.number}</Text>
                    )
                  })
                }
              </ScrollView>
            }
          <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
              <Button
              className='mR-10'
              title="SAVE"
              onPress={onSave}
              />
              <Button
              title="CLEAR"
              onPress={onClear}
              />
          </View>
          <Spinner visible={isLoading} />
        </View>
    );
};