import React, {useState, useEffect} from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { IPayment } from '../../../../models/home';
import { ITransaction } from '../../../../models/home/transaction';
import Toast from '../../../../util/toast';
import { toDateString } from '../../../../util/convert';
import { MONTH } from '../../../../constants';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [data, setData] = useState([] as Array<IPayment>);
  const [viewId, setViewId] = useState('');
  const [transactions, setTransactions] = useState([] as Array<ITransaction>);
  
  useEffect(() => {
    if(props.homeId.length > 0){
      props.onToggleLoading(true);
      props.onLoad(props.homeId, (result: Array<IPayment>) => {
        props.onToggleLoading(false);
        setData(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Payments.');
      });
    }
  }, []);

  const onView = (id: string, transactions: Array<ITransaction>) => {
    if(viewId === id){
      setViewId('');
      setTransactions([]);
    }else{
      setViewId(id);
      setTransactions(transactions);
    }
  };

  const getAmountPaid = (transactions: Array<ITransaction>) => {
    let amount = 0;
    if(isNN(transactions)){
      transactions.filter((d: ITransaction, i: number) => d.isSuccess === true).map((d:ITransaction, i: number) => amount += d.amount);
    }
    return amount;
  };

  return(
    <View>
      <View className='ta-c mT-1 mB-3 p-2'>
        <Text className='c-blue-700 fw-600 mB-10'>Payments</Text>
      </View>
      {isNN(data) ?
        data.map((d: IPayment, i: number) => {
        return(
          <View key={i} className='bdB mT-5'>
                <View className='mT-1 mB-3 p-2'>
                    <Text className='fw-600'>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Rs. ' + d.amount + ' for ' + MONTH.filter(m => m.value === d.forMonth)[0].title  + "/" + d.forYear + (isNN(d.mode) ? ' ' + d.mode : '') + (isNN(d.type) ? ' ' + d.type : '')}</Text>
                </View>
                <View className='mT-1 mB-3 p-2'>
                  <Text>{'Amount Paid Rs. ' + getAmountPaid(d.transactions)}</Text>
                </View>
                {isNN(d.description) &&
                  <View className='mT-1 mB-3 p-2'>
                    <Text>{d.description}</Text>
                  </View>
                }
                <View className='mT-1 mB-3 p-2'>
                  <span onClick={() => {
                    onView(d._id, d.transactions);
                  }}>
                    <Text className='cur-p c-blue-500'>{(viewId === d._id ? 'Hide' : 'View') + ' Transactions'}</Text>
                  </span>
                </View>
                {viewId === d._id &&
                <View className='mT-5 mB-5'>
              <View className='bdB'>
                <Text className='fw-600'>Transactions</Text>
              </View>
              <View className='mT-3'>
                {isNN(transactions) ?
                  transactions.map((e: ITransaction, j: number) => {
                    return(
                    <View key={'tktcmt' + j} className={'pL-3 pR-3 pT-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                      <View className='fw-600'>
                        <Text>{e.createdBy + ' on ' + toDateString(e.createdAt)}</Text>
                      </View>
                      <View>
                        <Text>{'Ref Id: ' + e.txnRef}</Text>
                      </View>
                      <View>
                        <Text>{'Txn Id: ' + e.txnId}</Text>
                      </View>
                      <View>
                        <Text>{'Amount: ' + e.amount}</Text>
                      </View>
                      <View>
                        <Text>{'Status: ' + (e.isSuccess ? 'SUCCESS' : 'FAILED')}</Text>
                      </View>
                      {
                        isNN(e.reason) &&
                        <View>
                          <Text>{'Reason: ' + e.reason}</Text>
                        </View>
                      }
                    </View>
                    )
                  })
                  :
                  <Text className='p-20'>No transaction found.</Text>
                }
              </View>
            </View>
            }
              </View>
          );
      })
      :
      <Text>No data found.</Text>
    }
    </View>
  );
};