import * as React from 'react';
import { Text, View } from '../common/elements';
import { isNN } from '../../util/index';

export default (props: any) => {
  const {data, message} = props;
  return(
    isNN(data) ?
    data.map((d: any, i: number) => {
      return(
        <View className='bdB pB-3'>
          <View>
            <Text key={i} className='fw-600'>
              {d.title}
            </Text>
          </View>
          <View>
            <Text key={i}>
              {d.description}
            </Text>
          </View>
        </View>
      )
    })
    :
    isNN(message) ? <Text>{message}</Text> : null
  );
};