import * as React from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { IBlocks } from '../../../../models/store/blocks';
import { IBlock } from '../../../../models/block';
import { IHome } from '../../../../models/home';

interface IProps {
  blocks: IBlocks,
  data: IHome
};

export default (props: IProps) => {
  let { blocks, data } = props;
  let blockName = '';
  if(data && isNN(data._id) && blocks && blocks.isUpToDate && !blocks.isError && blocks.data){
    let block = blocks.data.filter((d: IBlock, i: number) => d._id === data._blockId);
    if(block && block.length > 0){
      blockName = block[0].name;
    }
  }
  return(
    (data && isNN(data._id)) ?
    <View>
      <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text>{'Block: ' + blockName}</Text>
      </View>
      <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text>{'Number: ' + data.number}</Text>
      </View>
      <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text>{'Address: ' + data.address}</Text>
      </View>
      <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text>{'No of bed room(s): ' + data.bedRoom}</Text>
      </View>
      <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text>{'Parking: ' + data.parking}</Text>
      </View>
      <View className='mT-3 mB-3 pT-5 pB-5'>
        <Text>{'Description: ' + data.description}</Text>
      </View>
    </View>
    :
    <Text>No data found.</Text>
  );
};