import React, {useState, useEffect} from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { IDocument } from '../../../../models/document';
import Toast from '../../../../util/toast';
import { toDateString } from '../../../../util/convert';
import { FILE_BASE_PATH } from '../../../../constants';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IDocument>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [data, setData] = useState([] as Array<IDocument>);
  
  useEffect(() => {
    if(props.homeId.length > 0){
      props.onToggleLoading(true);
      props.onLoad(props.homeId, (result: Array<IDocument>) => {
        props.onToggleLoading(false);
        setData(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Vehicles.');
      });
    }
  }, []);

  return(
    <View>
      <View className='ta-c mT-1 mB-3 p-2'>
        <Text className='c-blue-700 fw-600 mB-10'>Documents</Text>
      </View>
      {isNN(data) ?
        data.map((d: IDocument, i: number) => {
        return(
          <View key={i} className='bdB mT-5'>
            <View className='mT-1 mB-3 p-2'>
              <Text className='fw-600'>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
              <Text className='fw-600'>{d.title}</Text>
            </View>
            {isNN(d.description) &&
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.description}</Text>
            </View>
            }
            {isNN(d.file) &&
            <View className='mT-1 mB-3 p-2'>
            <a target='_blank' rel='noopener noreferrer' href={FILE_BASE_PATH + d.file}>{d.file}</a>
            </View>
            }
          </View>
          );
      })
      :
      <Text>No data found.</Text>
    }
    </View>
  );
};