import React, {useState, useEffect} from 'react';
import { View, TextInput } from '../../common/elements';
import Button from '../../common/button';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { isNN, onEnter } from '../../../util/index';
import { IAppUser } from '../../../models/app-user';
import { IBlocks } from '../../../models/store/blocks';
import { IBlock } from '../../../models/block';
import { IHomes } from '../../../models/store/homes';
import { IHome } from '../../../models/home';
import Toast from  '../../../util/toast';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  blocks: IBlocks,
  homes: IHomes,
  onSave: (appClientId: string, data: IBlock, user: IUser, blocks: Array<IBlock>, homes: Array<IHome>, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [id, setId] = useState('');
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [homeCount, setHomeCount] = useState(0);

  useEffect(() => {
    let blockId = props.match.params.id; // props.navigation.state.params.id;
    if(blockId.length > 0 && blockId !== '0' && props.blocks && isNN(props.blocks.data)){
      let block = props.blocks.data.filter((d, i) => {
        return d._id === blockId
      });
      if(isNN(block)){
        setId(block[0]._id);
        setName(block[0].name);
        setDescription(block[0].description)
      }
    }
  }, []);
  
  const onSave = () => {
    if(isNN(name) && (isNN(id) || homeCount > 0)){
      if(!isNN(props.blocks.data) || !isNN(props.blocks.data.filter(b => b.name === name))){
        setIsLoading(true);
        let block = {
          _id: id,
          name: name,
          description: description
        } as IBlock;
        block = Object.assign(block, {
          homeCount: homeCount
        });
        props.onSave(props.user.client._id, block, props.user, (props.blocks && props.blocks.data) ? props.blocks.data : [], (props.homes && props.homes.data) ? props.homes.data : [], (result: boolean, message: string) => {
          setIsLoading(false);
          if(result === true){
              setId('');
              setName('');
              setDescription('');
              setHomeCount(0);
            Toast.success(isNN(id) ? 'Block saved successfully.' : 'Block saved and house(s) created successfully.');
          }else{
            Toast.fail(isNN(message) ? message : 'Failed to save block.');
          }
        });
      }else{
        Toast.fail('Block with same name already exists. Please give different name.');
      }
    }else{
      Toast.warn('Please fill all the required fields before '+ (id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
      <Breadcrum data={[{title: 'Blocks', link: 'blocks'}, {title: 'Edit Block', link: 'edit-block/0'}]} />
      <View className='bgc-white p-20'>
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextInput type='text' placeholder="Name" className='form-control' value={name} onChange={(event: any) => setName(event.target.value)} />
        </View>
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextInput type='text' placeholder="Description" className='form-control' value={description} onChange={(event: any) => setDescription(event.target.value)}
          onKeyPress={(e) => {
            onEnter(e, onSave);
          }} />
        </View>
        {!isNN(id) &&
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <TextInput type='number' placeholder="Number of houses in this block" className='form-control' value={homeCount > 0 ? homeCount + '' : ''} onChange={(event: any) => setHomeCount(+(event.target.value.replace(/[^0-9]/g, '')))} />
        </View>
        }
        <View className='mT-3 mB-3 pT-5 pB-5'>
          <Button
          title="SAVE"
          onPress={onSave}
          />
        </View>
      </View>
      <Spinner visible={isLoading} />
    </View>
    </View>
    </View>
  );
};