import React, {useState} from 'react';
import { View, TextInput } from '../common/elements';
import Button from '../common/button';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { IServant } from '../../models/home';
import { isNN, onEnter } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  homeIndex: number,
  onSave: (homeId: string, data: IServant, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [id, setId] = useState('');
  const [name, setName] = useState('');
  const [age, setAge] = useState(0);
  const [gender, setGender] = useState('');
  const [mobileNo, setMobileNo] = useState('');
  const [emailId, setEmailId] = useState('');

  const onSave = () => {
    if(isNN(name) && age > 0 && isNN(gender)){
      setIsLoading(true);
        let servant = {
          _id: id,
          name: name,
          age: age,
          gender: gender,
          mobileNo: mobileNo,
          emailId: emailId ? emailId.toLowerCase() : ''
        } as IServant;
        props.onSave(props.user.homes[props.homeIndex]._id, servant, (result: boolean) => {
          setIsLoading(false);
          if(result === true){
            setId('');
            setName('');
            setAge(0);
            setGender('');
            setMobileNo('');
            setEmailId('');
            // props..navigation.goBack(null)
            Toast.success('Servant saved successfully.');
          }else{
            Toast.fail('Failed to save servant.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before Save.');
    }
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Servants', link: 'servants'}, {title: 'New Servant', link: 'new-servant' }]} />  
        <View className='bgc-white p-20'>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Name" className='form-control' value={name} onChange={(event: any) => setName(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Age" className='form-control' maxLength={3} value={age > 0 ? age + '' : ''} onChange={(event: any) => setAge(+(event.target.value.replace(/[^0-9]/g, '')))} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={gender === 'M'} text='Male' onPress={() => setGender('M')} />
            <RadioButton isActive={gender === 'F'} text='Female' onPress={() => setGender('F')} />
            {props.user.client.config.hasOtherGender &&
            <RadioButton isActive={gender === 'O'} text='Other' onPress={() => setGender('O')} />
            }
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Mobile Number" className='form-control' value={mobileNo} onChange={(event: any) => setMobileNo(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Email Id" className='form-control' value={emailId} onChange={(event: any) => setEmailId(event.target.value)}
            onKeyPress={(e) => {
              onEnter(e, onSave);
            }} />
          </View>
          <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
            <Button
            title="SAVE"
            onPress={onSave}
            />
          </View>
        </View>
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
    );
};