import React, {useState} from 'react';
import {ScrollView, View, Text} from '../common/elements';
import Button from '../common/button';
import Spinner from '../common/spinner';
import {IUser} from '../../models/user/user';
import {IHome} from '../../models/home/home';
import {IPayment} from '../../models/home';
import {ITransaction} from '../../models/home/transaction';
import {isNN, generateUUID} from '../../util/index';
import Toast from '../../util/toast';
import {MONTH} from '../../constants';

interface IProps {
  history: any,
  user: IUser,
  home: IHome,
  onPaymentUpload: (file: any, homeId: string, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [payment, setPayment] = useState({
    paymentId: '',
    amount: 0,
    transactionRef: '',
  });

  const getTotalDue = () => {
    let totalDue = 0;
    props.home.payments.map((d: IPayment, i: number) => {
      let amountPaid = 0;
      d.transactions.filter((t, j) => t.isSuccess === true).map((data, index) => amountPaid += data.amount);
      totalDue += d.amount - amountPaid;
      return 1;
    });
    return totalDue;
  };

  const payNow = (paymentId: string, amount: number) => {
    if (paymentId && paymentId.length > 0 && amount && amount > 0) {
      if (isNN(props.user.client.upi.id) && isNN(props.user.client.upi.name)) {
        const transactionRef = generateUUID();
        setPayment({
            paymentId: paymentId,
            amount: amount,
            transactionRef: transactionRef
          });
        Toast.warn('Payment is in progress. Please contact administrator.');
        // RNUpiPayment.initializePayment({
        //   vpa: props.user.client.upi.id, // or can be john@ybl or mobileNo@upi
        //   payeeName: props.user.client.upi.name,
        //   amount: amount + '',
        //   transactionRef: transactionRef,
        //   transactionNote: ''
        // }, successCallback, failureCallback);
      } else {
        Toast.warn('Please update UPI ID and UPI Name to app administrator to proceed further.');
      }
    } else {
      Toast.warn('Amount should be more then 0 to proceed payment.');
    }
  };

  const successCallback = (result: any) => {
    console.log('Payment Success...', result);
    if (result && result.Status) {
      if (result.Status === 'SUCCESS' && result.responseCode && result.responseCode === '00') {
        setIsLoading(true);
        const transaction = {
          txnRef: result.txnRef,
          txnId: result.txnId,
          amount: payment.amount,
          status: 'PAID',
          isSuccess: true,
          reason: '',
        } as ITransaction;
        props.onPaymentUpdate(payment.paymentId, transaction, (updateResult: boolean) => {
          setIsLoading(false);
          clearTxn();
          if (updateResult === true) {
            Toast.success('Payment success.');
          } else {
            Toast.fail('Payment failed.');
          }
        });
      } else {
        txnFailure(result);
      }
    } else {
      txnFailure(result);
    }
  };

  const failureCallback = (result: any) => {
    txnFailure(result);
  };

  const txnFailure = (result: any) => {
    console.log('Payment Failure...', result);
    let msg: string = '';
    if (result && result.Status) {
      if (result.Status === 'FAILURE' && result.responseCode && result.responseCode === 'ZM') {
        msg = 'Payment failed because of wrong pin.';
      } else if (result.Status === 'FAILURE' && result.message && result.message.length > 0) {
        msg = 'Payment failed because ' + result.message;
      } else {
        msg = 'Payment failed.';
      }
    } else {
      msg = 'Payment failed.';
    }
    Toast.fail(msg);
    setIsLoading(true);
    const transaction = {
      txnRef: payment.transactionRef,
      txnId: '',
      amount: payment.amount,
      status: 'FAIL',
      isSuccess: false,
      reason: msg,
    } as ITransaction;
    props.onPaymentUpdate(payment.paymentId, transaction, (updateResult: boolean) => {
      setIsLoading(false);
      clearTxn();
      // if(updateResult === true){
      //   Toast.success('Payment success.');
      // }else{
      //   Toast.fail('Payment failed.');
      // }
    });
  };

  const clearTxn = () => {
    setPayment({
        paymentId: '',
        amount: 0,
        transactionRef: ''
      });
  };

    return (
      <View>
        <View className='m-5'>
          <Text className='mT-2 fw-600'>{'Total Due Amount: Rs ' + getTotalDue()}</Text>
        </View>
        <ScrollView>
          {isNN(props.home.payments) ?
        props.home.payments.filter((p: IPayment, i: number) => {
          let amountPaid = 0;
          if (isNN(p.transactions)) {
            p.transactions.filter((t) => t.isSuccess === true).map((p, j) => amountPaid += p.amount);
          }
          return (amountPaid < p.amount);
        }).map((d: IPayment, i: number) => {
          let amountPaid = 0;
          if (isNN(d.transactions)) {
            d.transactions.filter((p) => p.isSuccess === true).map((data, index) => amountPaid += data.amount);
          }
          if (amountPaid < d.amount) {
            return (
              <View key={'my-due' + i} className={'m-7' + (i === 0 ? '' : ' pL-15 bdL' )}>
                <View className='mT-4'>
                  <Text>{'Due for: ' + MONTH.filter((m) => m.value === d.forMonth)[0].title + '/' + d.forYear}</Text>
                </View>
                <View className='mT-4 mB-3'>
                  <Text>{'Rs ' + (d.amount - amountPaid)}</Text>
                </View>
                <View>
                  <Text>{'Due Date: 15/' + MONTH.filter((m) => m.value === d.forMonth)[0].title + '/' + d.forYear}</Text>
                </View>
                <View className='mT-4'>
                  <Button
                    title='PAY NOW'
                    onPress={() => payNow(d._id, (d.amount - amountPaid))}
                  />
                </View>
              </View>
            );
          } else {
            return null;
          }
        }):
          <Text className='p-20'>No due found.</Text>
          }
        </ScrollView>
        <Spinner visible={isLoading} />
      </View>
    );
};