import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Modal from 'react-responsive-modal';
import { View, Text } from '../common/elements';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IVisitor } from '../../models/home/visitor';
import { IHomes } from '../../models/store/homes';
import Toast from  '../../util/toast';
import { ROLE } from '../../constants';
import VisitorsView from './gatekeeper/visitors-view'
import NewVisitor from './gatekeeper/new-visitor';
import { isNN } from '../../util';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  homes: IHomes,
  gateNo: string,
  onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  void,
  onApprove: (id: string, callback: (result: boolean) => void) => void,
  onReject: (id: string, callback: (result: boolean) => void) => void,
  onRemove: (id: string, callback: (result: boolean) => void) => void,
  onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearch: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([] as Array<IVisitor>);
  const [isActive, setIsActive] = useState(true);
  const [isRequestNew, setIsRequestNew] = useState(false);
  const [passCode, setPassCode] = useState('');
  
  const loadData = () => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, props.user.homes[props.appUser.homeIndex]._id, isActive, (result: Array<IVisitor>) => {
      setIsLoading(false);
      setData(result);
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  };
  
  useEffect(() => {
    loadData();
  }, []);
  
  const onActiveChange = () => {
    setIsActive(!isActive);
    loadData();
  };
  
  const onApprove = (id: string) => {
    confirmAlert({
      title: 'Confirm to Approve Visitor Entry',
      message: 'Are you sure you wish to Approve the visitor to Enter?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onApprove(id, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                let tData = data;
                let foundIndex = tData.findIndex(v => v._id === id);
                tData[foundIndex].isApproved = true;
                tData[foundIndex].approvedBy = props.user.name;
                tData[foundIndex].approvedAt = new Date();
                setData(tData);
                Toast.success('Visitor approved successfully.');
              }else{
                Toast.fail('Failed to approve the entry of visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onReject = (id: string) => {
    confirmAlert({
      title: 'Confirm to Reject Visitor Entry',
      message: 'Are you sure you wish to reject visitor?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onReject(id, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                let tData = data;
                let foundIndex = tData.findIndex(v => v._id === id);
                tData[foundIndex].isRejected = true;
                tData[foundIndex].approvedBy = props.user.name;
                tData[foundIndex].approvedAt = new Date();
                setData(tData);
                Toast.success('Visitor rejected successfully.');
              }else{
                Toast.fail('Failed to reject visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to Remove Visitor',
      message: 'Are you sure you wish to remove visitor?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onRemove(id, (result: boolean) => {
              setIsLoading(false);
              if(result === true){
                setData(data.filter(v => v._id !== id));
                Toast.success('Visitor removed successfully.');
              }else{
                Toast.fail('Failed to remove visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onSave = (homeId: string, vData: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => {
    props.onSave(homeId, vData, user, appUser, (result: IVisitor) => {
      sCallback(result);
      // data.unshift(result);
      setData([result, ...data]);
      setPassCode(result.passCode);
      setIsRequestNew(false);
    }, (message: string) => {
      fCallback(message);
    });
  };

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Visitors', link: 'visitors'}]} />
        <View className='bgc-white p-20'>
        {props.appUser.role === ROLE.USER &&
        <View className='mT-3 mB-3 pT-5 pB-5 ta-r'>
          <Text className='cur-p c-blue-500 fw-600 ta-r' onClick={() => setIsRequestNew(!isRequestNew)}>{isRequestNew ? '< Back to Visitors' : 'Request for a New Visitor'}</Text>
        </View>
        }
        {!isRequestNew && <VisitorsView data={data} isActive={isActive} user={props.user} appUser={props.appUser} onActiveChange={onActiveChange} onApprove={onApprove} onReject={onReject} onRemove={onRemove} />}
        {isRequestNew && <NewVisitor appUser={props.appUser} user={props.user} homes={props.homes} gateNo={props.gateNo} onToggleLoading={(isLoading) => setIsLoading(isLoading)} onSave={onSave} onUpload={props.onUpload} onSearch={props.onSearch} />}
        {isNN(passCode) &&
        <Modal
        open={isNN(passCode)}
        onClose={() => setPassCode('')}>
          <View className='mT-30 mB-25 mL-15 mR-15' style={{height: '80%'}}>
            <View className='mT-3 mB-3 pT-5 pB-5'>{'Visitor Request created successfully. Please share the passcode(' + passCode + ') to yor visitor and ask him/her to show it to the security in the gate while entering.'}</View>
            <View className='mT-3 mB-3 pT-5 pB-5 ta-c fw-600 c-blue-600' style={{fontSize: 30}}>{passCode}</View>
          </View>
        </Modal>
        }
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
      </View>
    );
};