import React, {useState, useEffect} from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { ITicket } from '../../../../models/home';
import { ITicketComment } from '../../../../models/home/ticket-comment';
import Toast from '../../../../util/toast';
import { toDateTimeString } from '../../../../util/convert';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [data, setData] = useState([] as Array<ITicket>);
  const [viewId, setViewId] = useState('');
  const [comments, setComments] = useState([] as Array<ITicketComment>);

  useEffect(() => {
    if(props.homeId.length > 0){
      props.onToggleLoading(true);
      props.onLoad(props.homeId, (result: Array<ITicket>) => {
        props.onToggleLoading(false);
        setData(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Vehicles.');
      });
    }
  }, []);

  const onView = (id: string, comments: Array<ITicketComment>) => {
    if(viewId === id){
      setViewId('');
      setComments([]);
    }else{
      setViewId(id);
      setComments(comments);
    }
  };

  return(
    <View>
      <View className='ta-c mT-1 mB-3 p-2'>
        <Text className='c-blue-700 fw-600 mB-10'>Tickets</Text>
      </View>
      {isNN(data) ?
        data.map((d: ITicket, i: number) => {
        return(
          <View key={'tkt' + i} className='bdB mT-5'>
           <View className='mT-1 mB-3 p-2'>
              <Text className='fw-600'>{d.createdBy + ' from ' + d._homeId + ' on ' + toDateTimeString(d.createdAt)}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
                <Text>{d.title}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
                <Text>{d.description}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
              <Text>{d.status}</Text>
            </View>
            <View className='mT-1 mB-3 p-2'>
              <span onClick={() => {
                onView(d._id, d.comments);
              }}>
                <Text className='cur-p c-blue-500'>{(viewId === d._id ? 'Hide' : 'View') + ' Comments'}</Text>
              </span>
            </View>
            {viewId === d._id &&
            <View className='mT-5 mB-5'>
              <View>
                <Text className='fw-600'>Comments</Text>
              </View>
              <View className='p-15'>
                {isNN(comments) ?
                  comments.map((e: ITicketComment, j: number) => {
                    return(
                    <View key={'tkycmt' + j} className={'pL-3 pR-3 pT-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                      <View>
                        <Text className='fw-600'>{e.createdByName + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                      </View>
                      <View>
                        <Text>{e.comment}</Text>
                      </View>
                    </View>
                    )
                  })
                  :
                  <Text className='p-20'>No comment found.</Text>
                }
              </View>
            </View>
            }
          </View>
          );
      })
      :
      <Text>No data found.</Text>
    }
    </View>
  );
};