import React, {useState, useEffect} from 'react';
import Modal from 'react-responsive-modal';
import DatePicker from 'react-date-picker';
import TimePicker from 'react-time-picker';
import moment from 'moment';
import { View, ScrollView, TextInput, Text, Picker } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import { IHomes } from '../../../models/store/homes';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { isNN, generateUUID, onEnter, isNNObject } from '../../../util/index';
import { ROLE } from '../../../constants';
import Toast from  '../../../util/toast';
import { IHome, IVisitor } from '../../../models/home';
import VisitorsView from './visitors-view';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  gateNo: string,
  onToggleLoading: (isLoading: boolean) => void,
  onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearch: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [homeId, setHomeId] = useState('');
  const [formData, setFormData] = useState({
    id: '',
    type: 'VISITOR',
    name: '',
    age: 0,
    gender: '',
    mobileNo: '',
    arriveAt: new Date(),
    arriveAtTime: '',
    personCount: 1,
    vehicleType: 'BIKE',
    vehicleNo: '',
    fileName: '',
  });
  const [showHomes, setShowHomes] = useState(false);
  const [homeSearchText, setHomeSearchText] = useState('');
  const [searchResult, setSearchResult] = useState([] as Array<IVisitor>);
  const [isShowSearch, setIsShowSearch] = useState(false);
   
  useEffect(() => {
    if(props.appUser.role === ROLE.USER){
      setHomeId(props.user.homes[props.appUser.homeIndex]._id)
    } 
  }, []);

  const onImageSelect = (event: any) => {
    let file = event.target.files[0];
    if(file && file.name){
      let arrFileName = file.name.split('.');
      if(arrFileName && arrFileName.length > 1){
        let fileName: string = generateUUID();
        fileName += '.' + arrFileName[arrFileName.length - 1];
        setFormData({
          ...formData,
          fileName: fileName
        });
        let data = {
          file: file,
          type: file.type,
          name: fileName
        };
        props.onUpload(data, (result: boolean, message: string) => {
          if(!result){
            Toast.fail(message ? message : 'Failed to upload file.');
          }
        });
      }
    }
  };

  const onSave = () => {
    if((props.appUser.role === ROLE.USER || isNN(props.gateNo)) && (props.appUser.role !== ROLE.USER || isNNObject(formData.arriveAt)) && showHomes === false && homeId.length > 0 && isNN(formData.name) && formData.personCount > 0 && isNN(formData.fileName)){
      let arriveAt = formData.arriveAt;
      if(isNNObject(arriveAt)){
        let arriveAtTime: Array<string> = [];
        if(isNNObject(formData.arriveAt) && isNN(formData.arriveAtTime)){
          arriveAtTime = formData.arriveAtTime.split(':');
        }
        if(isNN(arriveAtTime) && arriveAtTime.length > 1){
          arriveAt.setHours(parseInt(arriveAtTime[0]), parseInt(arriveAtTime[1]), 0,0);
        }else{
          arriveAt.setHours(0, 0, 0, 0);
        }
      }
      let visitor = {
        _id: '',
        type: formData.type,
        name: formData.name,
        age: formData.age,
        gender: formData.gender,
        mobileNo: formData.mobileNo,
        emailId: '',
        arriveAt: arriveAt,
        personCount: formData.personCount,
        vehicleType: formData.vehicleType,
        vehicleNo: formData.vehicleNo,
        image: formData.fileName,
        passCode: '',
        gateNo: props.gateNo
      } as IVisitor;
      visitor = Object.assign(visitor, {
        isUser: (props.appUser.role === ROLE.USER)
      });
      props.onToggleLoading(true);
      props.onSave(homeId, visitor, props.user, props.appUser, (result: IVisitor) => {
          // props.navigation.navigation.goBack(null);
          onClear();
          props.onToggleLoading(false);
          if(props.appUser.role === ROLE.USER){
            Toast.success('Visitor Request created successfully. Please share the passcode(' + result.passCode + ') to yor visitor and ask him/her to show it to the security in the gate while entering.');
          }else{
            Toast.success('Visitor created successfully. Notification sent to the house members for approval. If this visitor got approved from house member then allow him/her inside.')
          }
      }, (message: string) => {
          props.onToggleLoading(false);
          Toast.fail('Failed to save visitor ' + visitor.name + '.');
      });
    }else{
      Toast.warn('Please fill Gate Number, House, Name, People count and Image before '+ (formData.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onClear = () => {
    setHomeId((props.appUser.role === ROLE.USER) ? homeId : '');
    setFormData({
      ...formData,
      id: '',
      type: 'VISITOR',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      arriveAt: new Date(),
      arriveAtTime: '',
      personCount: 1,
      vehicleType: 'BIKE',
      vehicleNo: '',
      fileName: ''
    });
    setShowHomes(false);
    setHomeSearchText('');
    setSearchResult([]);
    setIsShowSearch(false);
  };
  
  const onSearch = () => {
    if(formData.mobileNo.length > 2){
      props.onToggleLoading(true);
      props.onSearch(formData.mobileNo, (result: Array<IVisitor>) => {
        props.onToggleLoading(false);
        setIsShowSearch(true);
        setSearchResult(result);
      }, (message: string) => {
        props.onToggleLoading(false);
        Toast.fail(message);
        setIsShowSearch(false);
        setSearchResult([]);
      });
    }
  };

    return(
        <View>
          {props.appUser.role === ROLE.GATEKEEPER &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder='Enter House Number' className='form-control'
            value={homeSearchText} 
            onChange={(event: any) => {
              setShowHomes(true);
              setHomeSearchText(event.target.value)
              }} />
          </View>
          }
          {
            (props.appUser.role === ROLE.GATEKEEPER && showHomes && isNN(homeSearchText) && props.homes && props.homes.data.length > 0 ) &&
            <ScrollView className='mT-3 mB-3 pT-5 pB-5 ov-s'>
              {
                props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(homeSearchText.toLowerCase()) >= 0).map((d: IHome, i: number) => {
                  return(
                    <Text 
                    className='cur-p m-3 p-5 bgc-blue-50'
                    key={'ho' + i}
                    onClick={() => {
                      setHomeSearchText(d.number);
                      setHomeId(d._id);
                      setShowHomes(false);
                    }}>{d.number}</Text>
                  )
                })
              }
            </ScrollView>
          }
          {props.appUser.role === ROLE.GATEKEEPER &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={formData.type === 'VISITOR'} text='Visitor' onPress={() => setFormData({...formData, type: 'VISITOR'})} />
            <RadioButton isActive={formData.type === 'DELIVERY'} text='Delivery' onPress={() => setFormData({...formData, type: 'DELIVERY'})} />
            <RadioButton isActive={formData.type === 'SERVICE'} text='Service' onPress={() => setFormData({...formData, type: 'SERVICE'})} />
          </View>
          }
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder='Mobile Number' className='form-control mR-10 d-i' style={{width: 'calc(100% - 40px)'}} value={formData.mobileNo} onChange={(event: any) => setFormData({...formData, mobileNo: event.target.value})} 
            onKeyPress={(e) => {
              onEnter(e, onSearch);
            }} />
            <span className='icon-holder cur-p' onClick={onSearch}>
              <i className='ti-search pdd-right-10 fw-900'></i>
            </span>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Name" className='form-control' value={formData.name} onChange={(event: any) => setFormData({...formData, name: event.target.value})} />
          </View>
          {props.appUser.role === ROLE.USER &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text>Arrival At &nbsp;</Text>
            <DatePicker minDate={moment().toDate()} maxDate={moment().add(2,'d').toDate()}
            onChange={(date : any) => {
              setFormData({
                ...formData,
                arriveAt: date
              });
            }}
            value={formData.arriveAt}
            />
            <Text>&nbsp;</Text>
            <TimePicker
            value={formData.arriveAtTime}
            // minTime={(isNN(this.state.arriveAt) && this.state.arriveAt.getDate() === moment().toDate().getDate()) ? moment().toDate() : null}
            onChange={(time: any) => {
              setFormData({
                ...formData,
                arriveAtTime: time
              });
            }}
            />
          </View>
          }
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='number' placeholder='Age' className='form-control w-a d-ib mR-10' maxLength={3} value={formData.age > 0 ? formData.age + '' : ''} onChange={(event: any) => setFormData({...formData, age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
            <View className='d-ib w-a'>
              <RadioButton isActive={formData.gender === 'M'} text='Male' onPress={() => setFormData({...formData, gender: 'M'})} />
              <RadioButton isActive={formData.gender === 'F'} text='Female' onPress={() => setFormData({...formData, gender: 'F'})} />
              {props.user.client.config.hasOtherGender &&
              <RadioButton isActive={formData.gender === 'O'} text='Other' onPress={() => setFormData({...formData, gender: 'O'})} />
              }
            </View>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='number' placeholder='People Count' className='form-control w-a d-ib mR-10' value={formData.personCount > 0 ? formData.personCount + '' : ''} onChange={(event: any) => setFormData({...formData, personCount: +(event.target.value.replace(/[^0-9]/g, ''))})} />
            <Picker className='form-control w-a d-ib mR-10' value={formData.vehicleType}
            onChange={(event: any) => setFormData({...formData, vehicleType: event.target.value})}>
              <option value='WALK'>WALK</option>
              <option value='CYCLE'>CYCLE</option>
              <option value='BIKE'>BIKE</option>
              <option value='CAR'>CAR</option>
              <option value='VAN'>VAN</option>
              <option value='JEEP'>JEEP</option>
              <option value='TEMPO'>TEMPO</option>
              <option value='BUS'>BUS</option>
              <option value='TRUCK'>TRUCK</option>
            </Picker>
            <TextInput type='text' placeholder='Vehicle No' className='form-control w-a d-ib' value={formData.vehicleNo} onChange={(event: any) => setFormData({...formData, vehicleNo: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
              <input type='file' name='image' placeholder='SELECT IMAGE' onChange={onImageSelect}/>
              {isNN(formData.fileName) &&
              <Text className='mL-15'>{formData.fileName}</Text>
              }
          </View>
          <View className='ta-c mT-5 mB-3 pT-5 pB-5'>
              <Button
              title="SAVE"
              onPress={onSave}
              className='mR-10'
              />
              <Button
              title="CLEAR"
              onPress={onClear}
              />
          </View>
          {isShowSearch &&
          <Modal
          open={isShowSearch}
          onClose={() => setIsShowSearch(false)}>
            <View className='mT-30 mB-25 mL-15 mR-15' style={{height: '80%'}}>
              <VisitorsView user={props.user} appUser={props.appUser} data={searchResult} isActive={false} isSearch={true} onSelect={(data: IVisitor) => {
                setIsShowSearch(false);
                setSearchResult([]);
                setFormData({
                  ...formData,
                  name: data.name,
                  age: data.age,
                  gender: data.gender,
                  mobileNo: data.mobileNo,
                  vehicleType: data.vehicleType,
                  vehicleNo: data.vehicleNo,
                  fileName: data.image
                });
                }} />
            </View>
          </Modal>
          }
        </View>
    );
};