import * as React from 'react';

interface IProps {
  title: string,
  onPress: () => void,
  type?: string,
  className?: string,
  style? : {}
};

export default (props: IProps) => {
  const {title, onPress, type, className, style} = props;
  return (
    <input type='button' className={'btn ' + (type ? type : 'btn-primary') + (className ? ' ' + className : '')} value={title} onClick={() => onPress()} style={{...style}} />
  );
}