import React, {useState} from 'react';
import { View, TextInput } from '../common/elements';
import Button from '../common/button';
import RadioButton from '../common/radio-button';
import { IMember } from '../../models/home';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  onSignUp: (data: IMember, clientCode: string, sCallback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [id, setId] = useState('');
  const [clientCode, setClientCode] = useState('');
  const [number, setNumber] = useState('');
  const [name, setName] = useState('');
  const [age, setAge] = useState(0);
  const [gender, setGender] = useState('');
  const [mobileNo, setMobileNo] = useState('');
  const [emailId, setEmailId] = useState('');
  const [type, setType] = useState('TENANT');
  const [role, setRole] = useState('USER');
  
  const onSave = () => {
    if(isNN(clientCode) && isNN(number) && isNN(name) && age > 0 && isNN(gender) && isNN(mobileNo) && isNN(emailId)){
        let member = {
          _id: id,
          number: number,
          name: name,
          age: age,
          gender: gender,
          mobileNo: mobileNo,
          emailId: emailId ? emailId.toLowerCase() : '',
          type: type,
          role: role
        } as IMember;
        props.onSignUp(member, clientCode, (result: boolean) => {
          if(result === true){
            setId('');
            setClientCode('');
            setNumber('');
            setName('');
            setAge(0);
            setGender('');
            setMobileNo('');
            setEmailId('');
            setType('TENANT');
            setRole('USER');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before Submit.');
    }
  };

    return(
        <View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' className='form-control' placeholder="App Code" value={clientCode} onChange={(event: any) => setClientCode(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' className='form-control' placeholder="User Id / Number" value={number} onChange={(event: any) => setNumber(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' className='form-control' placeholder="Name" value={name} onChange={(event: any) => setName(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='number' className='form-control' placeholder="Age" value={age > 0 ? age + '' : ''} onChange={(event: any) => setAge(+(event.target.value.replace(/[^0-9]/g, '')))} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={gender === 'M'} text='Male' onPress={() => setGender('M')} />
            <RadioButton isActive={gender === 'F'} text='Female' onPress={() => setGender('F')} />
            <RadioButton isActive={gender === 'O'} text='Other' onPress={() => setGender('O')} />
          </View>            
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' className='form-control' placeholder="Mobile Number" value={mobileNo} onChange={(event: any) => setMobileNo(event.target.value)} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' className='form-control' placeholder="Email ID" value={emailId} onChange={(event: any) => setEmailId(event.target.value)} />
          </View>
          {/* <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={type === 'OWNER'} text='Owner' onPress={() => setType('OWNER')} />
            <RadioButton isActive={type === 'TENANT'} text='Tenant' onPress={() => setType('TENANT')} />
          </View> */}
          <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
            <Button
            title="SUBMIT"
            onPress={onSave}
            />
          </View>
        </View>
    );
};