import * as React from 'react';
import { View, Text } from '../common/elements';
import Breadcrum from '../common/breadcrum';
import { IUser } from '../../models/user';

interface IProps {
  user: IUser
};

export default (props: IProps) => {
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Info', link: 'info'}]} />
          <View className='bgc-white p-20'>
          
          {/* <Image source={Images.workInProgress} className='mT-30' />
              <Text>Work in progress.</Text> */}
          <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
            <Text className='c-blue-500 fw-600 ta-c'>{props.user.client.name}</Text>
          </View>
          <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
              <Text className='c-blue-500 fw-600 ta-c'>{props.user.client.description}</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text>
              {"Apartment Residents & Apartment Owners need a common platform through which they can connect with association, get to know & discuss common issues of the neighborhood"}
            </Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text>House users</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>Maintenance Admin Payment</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>Receive real-time notifications</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>Visitor management</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>Lodge any complaint related to your apartment and track</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text>Admin Users</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>Create polls and gather the opinion of all Apartment residents</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>All details of your flat, staff who work in your flat, visitors coming to your flat</Text>
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mL-15'>Accounts  and much more...</Text>
          </View>
        </View>
      </View>
      </View>
    </View>
  );
};