import React, {useState} from 'react';
import Modal from 'react-responsive-modal';
import { View, Text, TextInput, ScrollView, TextArea } from '../../common/elements';
import DatePicker from 'react-date-picker';
import Button from '../../common/button';
import Spinner from '../../common/spinner';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IHomes } from '../../../models/store/homes';
import { IHome, IPayment } from '../../../models/home';
import { MONTH } from '../../../constants';
import { toDateString } from '../../../util/convert';
import { isNN } from '../../../util';
import Toast from '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onSearch: (appClientId: string, data: any, user: IUser, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onUpdate: (id: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [state, setState] = useState({
    isLoading: false,
    isShowSearch: true,
    fromDate: new Date(),
    toDate: new Date(),
    datePickerType: '',
    datePickerDate: new Date(),
    homes: [] as Array<IHome>,
    searchText: '',
    showHomes: false,
    data: [] as Array<IPayment>,
    isShowUpdateModel: false,
    uPaymentId: '',
    uAmount: 0,
    uDescription: ''
  });

  const onSearch = () => {
    if(state.fromDate && state.toDate){
      let homeIds = [] as any;
      state.homes.map((h,i) => homeIds.push(h._id));
      let data = {
        fromDate: state.fromDate,
        toDate: state.toDate,
        homes: homeIds
      };
      setState({
        ...state,
        isLoading: true
      });
      props.onSearch(props.user.client._id, data, props.user, (result: Array<IPayment>) => {
        setState({
          ...state,
          isLoading: false,
          isShowSearch: false,
          data: result
        });
      }, (message: string) => {
        setState({
          ...state,
          isLoading: false,
        });
        Toast.fail(message ? message : 'Failed to load payment.');
      });
    }else{
      Toast.warn('Please fill all required fields before Search.');
    }
  };

  const onUpdate = () => {
    if(isNN(state.uPaymentId) && state.uAmount > 0){
      let data = {
        amount: state.uAmount,
        description: state.uDescription,
      };
      setState({
        ...state,
        isLoading: true
      });
      props.onUpdate(state.uPaymentId, data, props.user, (result: boolean, message: string) => {
        setState({
          ...state,
          isLoading: false,
        });
        if(result === true){
          // props.navigation.navigation.goBack(null);
          let pData = state.data;
          var foundIndex = pData.findIndex(d => d._id === state.uPaymentId);
          pData[foundIndex].amount = state.uAmount;
          pData[foundIndex].description = state.uDescription;
          setState({
            ...state,
            isShowUpdateModel: false,
            uPaymentId: '',
            uAmount: 0,
            uDescription: '',
            data: pData
          });
          Toast.success('Payment updated successfully.');
        }else{
          Toast.fail( message ? message : 'Failed to update payment.');
        }
      });
    }else{
      Toast.warn('Please fill all required fields before update.');
    }
  };

    return(
        <View className='pL-15 pR-15'>
          {state.isShowSearch ?
          <View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <DatePicker
              onChange={(date : any) => {
                setState({
                  ...state,
                  fromDate: date
                });
              }}
              value={state.fromDate}
              />
            </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <DatePicker
              onChange={(date : any) => {
                setState({
                  ...state,
                  toDate: date
                });
              }}
              value={state.toDate}
              />
          </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mR-15'>{'House(s): ' + (state.homes.length > 0 ? state.homes.length : 'All')}</Text>
          </View>
            <ScrollView className='mT-3 mB-3 pT-5 pB-5 ov-s'>
                {
                  state.homes.map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='cur-p mR-6 c-blue-500'
                      key={'ho' + i}
                      onClick={() => {
                        setState({
                          ...state,
                          homes: state.homes.filter((h, i) => h._id !== d._id)
                         });
                      }}>{d.number}</Text>
                    )
                  })
                }
          </ScrollView>
            <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Enter House Number" className='form-control p-6 bd bdrs-5'
            value={state.searchText} 
            onChange={(event: any) => setState({...state, showHomes: true, searchText: event.target.value})} />
          </View>
            {(state.showHomes && isNN(state.searchText) && props.homes && props.homes.data.length > 0 ) &&
            <ScrollView className='mT-3 mB-3 pT-5 pB-5 ov-s'>
                {
                  props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(state.searchText.toLowerCase()) >= 0 && (state.homes.filter(h => h._id === d._id).length < 1)).map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='cur-p mR-6'
                      key={'ho' + i}
                      onClick={() => {
                        let homes = state.homes;
                        homes.push(d);
                        setState({
                          ...state,
                          homes: homes
                         });
                      }}>{d.number}</Text>
                    )
                  })
                }
          </ScrollView>
            }
            <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
                <Button
                title="SEARCH"
                onPress={onSearch}
                />
            </View>
          </View>
          :
          <View>
             <Text 
             className='fl-r cur-p c-blue-500 fw-600 ta-c'
             onClick={() => {
               setState({
                ...state,
                 isShowSearch: true
               });
             }}>Show Search</Text>
          </View>
          }
          <View>
            {(isNN(state.data)) ?
              state.data.map((d: IPayment, i: number) => {
                return(
                  <View key={i} className='bdB mT-5'>
                    <View className='mT-1 mB-3 p-2'>
                        <Text className='fw-600'>{d._homeId + ' by ' + d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
                    </View>
                    <View className='mT-1 mB-3 p-2'>
                      <Text className='fw-600'>{'Rs.' + d.amount + ' for ' + MONTH.filter(m => m.value === d.forMonth)[0].title  + "/" + d.forYear + (isNN(d.mode) ? ' ' + d.mode : '') + (isNN(d.type) ? ' ' + d.type : '')}</Text>
                    </View>
                    {isNN(d.description) &&
                      <View className='mT-1 mB-3 p-2'>
                        <Text>{d.description}</Text>
                      </View>
                    }
                    <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
                      <Button title="Update" onPress={() => {
                        setState({
                          ...state,
                          isShowUpdateModel: true,
                          uPaymentId: d._id,
                          uAmount: d.amount,
                          uDescription: d.description
                        });
                      }} />
                    </View>
                  </View>
                )
              })
              :
              <Text className='p-20'>No payment found.</Text>
            }
          </View>
          <Modal
          open={state.isShowUpdateModel}
          onClose={() => {
            setState({
              ...state,
              isShowUpdateModel: false,
              uPaymentId: '',
              uAmount: 0,
              uDescription: ''
            });
          }}>
          <View className='mT-30 mB-25 mL-15 mR-15'>
            <View>
              <View className='mT-3 mB-3 pT-5 pB-5'>
                <TextInput type='text' placeholder="Amount" className='form-control' value={state.uAmount > 0 ? state.uAmount + '' : ''} onChange={(event: any) => setState({...state, uAmount: +(event.target.value.replace(/[^0-9]/g, ''))})} />
              </View>
              <View className='mT-3 mB-3 pT-5 pB-5'>
                <TextArea placeholder="Description" className='form-control' value={state.uDescription} onChange={(event: any) => setState({...state, uDescription: event.target.value})} />
              </View>
              <View className='cur-p mT-3 mB-3 pT-5 pB-5'>
                  <Button
                  className='mR-10'
                  title="SAVE"
                  onPress={onUpdate}
                  />
                  <Button
                  title="CLEAR"
                  onPress={() => {
                    setState({
                      ...state,
                      uAmount: 0,
                      uDescription: ''
                    });
                  }}
                  />
              </View>
            </View>
          </View>
        </Modal>
          <Spinner visible={state.isLoading} />
        </View>
    );
};